import React from "react";
import {Link} from "react-router-dom";
import Slider from "react-slick";
import MenuBar from "../Menubar/Menubar";
import ReactImageZoom from "react-image-zoom";
import {requestValue, requestList, downloadFile} from "../helpers/request_helpers";
import {ProductPreview} from "../Product/ProductPreview";
import {LoadingComponent} from "../StaticContent/staticContents";
import {ListPreview} from "../ListsPreview/ListPreview";
import {DefaultImage} from "../StaticContent/staticContents";
import {LOCAL_CONSTANTS} from "../helpers/Constants";
import {
    getProductDetail, getProductManufacturer, getProductOffer, getProductPrice,
    getProductQValue, setTopMargin
} from "../helpers/Utilities";

// const imageList = ["https://target.scene7.com/is/image/Target/14905348",
//     "https://target.scene7.com/is/image/Target/50250258",
//     "https://www.stockupexpress.com/media/catalog/product/cache/5/image/9df78eab33525d08d6e5fb8d27136e95/0/1/013409515853.png",
//     "https://target.scene7.com/is/image/Target/50250258"];

export class ProductDetail extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            menuVisible: false,
            currentImage: "",
            isZoomed: false,
            qty: 1,
            product: {
                offer: {}
            },
            imageList: [],
            productImageLoading: false,

            containedList: [],
            containedListLoading: false,
            containedListPagination: {start: 0, end: 4},
            containedListError: "",

            similarProducts: [],
            similarProductsLoading: false,
            similarProductsPagination: {start: 0, end: 6},
            similarProductsError: "",
            loadMoreSimilarVisible: true,
            topMargin: undefined
        };
        this.toggleMenu = this.toggleMenu.bind(this);
        this.enableZoom = this.enableZoom.bind(this);
        this.disableZoom = this.disableZoom.bind(this);
        this.toggleZoom = this.toggleZoom.bind(this);
        this.changeCurrentImage = this.changeCurrentImage.bind(this);
        this.getMainImage = this.getMainImage.bind(this);
        this.getProductQValue = getProductQValue.bind(this);
        this.getProductPrice = getProductPrice.bind(this);
        this.getProductDetail = getProductDetail.bind(this);
        this.getProductManufacturer = getProductManufacturer.bind(this);
        this.getProductOffer = getProductOffer.bind(this);
        this.increaseQty = this.increaseQty.bind(this);
        this.decreaseQty = this.decreaseQty.bind(this);
        this.setQty = this.setQty.bind(this);
        this.clearQty = this.clearQty.bind(this);
        this.getSimilarProducts = this.getSimilarProducts.bind(this);
        this.setSimilarProducts = this.setSimilarProducts.bind(this);
        this.getContainedList = this.getContainedList.bind(this);
        this.setContainedList = this.setContainedList.bind(this);
        this.getProductImages = this.getProductImages.bind(this);
        this.setTopMargin = setTopMargin.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        const {showShoppingList, identifier} = this.props;
        this.setTopMargin();

        if (window.innerWidth > 991) {
            showShoppingList();
        }
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const {imageList} = this.state;
        imageList.length ? this.changeCurrentImage(imageList[0]) : null;
        // console.log("this props :: ", this.props);
        if (identifier) {
            this.setState({
                product: {
                    identifier
                }
            }, () => {

                this.getProductDetail()
                    .then(() => {
                        this.getProductPrice()
                            .then(() => {
                                this.getProductQValue()
                                    .then(() => {
                                        this.getProductOffer()
                                            .then(() => {
                                                this.getMainImage()
                                                    .then(() => {
                                                        this.getProductManufacturer()
                                                            .then(() => {
                                                                this.getProductImages()
                                                                    .then(() => {
                                                                        this.setSimilarProducts()
                                                                        this.setContainedList();
                                                                    });
                                                            });
                                                    });
                                            })
                                    })

                            })

                    })
                    .catch(error => console.log(error))
            })
        }

    }

    componentWillReceiveProps(nextProps) {
        document.body.scrollTop = 0;
        document.documentElement.scrollTop = 0;
        const {identifier, config, query} = nextProps;
        const {product} = this.state;
        if (identifier !== product.identifier) {
            this.setState({
                product: {
                    identifier
                },
                imageList: [],
                currentImage: ""
            }, () => {
                this.getProductDetail()
                    .then(() => {
                        this.getProductPrice()
                            .then(() => {
                                this.getProductQValue()
                                    .then(() => {
                                        this.getProductOffer()
                                            .then(() => {
                                                this.getMainImage()
                                                    .then(() => {
                                                        this.getProductManufacturer()
                                                            .then(() => {
                                                                this.getProductImages()
                                                                    .then(() => {
                                                                        this.setSimilarProducts()
                                                                        this.setContainedList();
                                                                    });
                                                            });
                                                    });
                                            })
                                    })

                            })

                    })
                    .catch(error => console.log(error))
            })
        }
    }

    getProductImages() {
        const {config, query, product} = this.state;
        const {listProductImages} = query;
        const {data} = listProductImages;
        const {identifier, productName} = product;
        this.setState({
            productImageLoading: true
        });
        return new Promise((resolve, reject) => {
            requestList(Object.assign({}, listProductImages, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(imageList => {
                    let list = [];
                    if (imageList.length) {
                        imageList.map(async item => {
                            const {identifier, contentUrl} = item;
                            const {config} = this.state;
                            await downloadFile(config, contentUrl)
                                .then(url => {
                                    list = [...list, {...item, contentUrl: url}]
                                })
                                .catch(error => {
                                    console.log("Image ", error);
                                })
                                .then(() => {
                                    this.setState({
                                        imageList: list,
                                        productImageLoading: false
                                    }, () => {
                                        resolve("Done");
                                    })
                                })
                        })
                    } else {
                        this.setState({
                            productImageLoading: false,
                            currentImage: DefaultImage,
                            imageList: [{contentUrl: DefaultImage}]
                        });
                        reject("error ");
                    }
                })
                .catch(error => {
                    console.log(error);
                    this.setState({
                        productImageLoading: false,
                        currentImage: DefaultImage,
                        imageList: [{contentUrl: DefaultImage}]
                    });
                    reject(error);
                })
                .then(() => {
                    this.setState({
                        productImageLoading: false
                    })
                })
        })
    }

    getMainImage() {
        const {config, query, product} = this.state;
        const {getMainImageProduct} = query;
        const {productID, productName, identifier} = product;
        const {data} = getMainImageProduct;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getMainImageProduct, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(mainImage => {
                    // console.log(productName, mainImage);
                    this.setState({
                        product: {
                            ...product,
                            mainImage
                        }
                    }, async () => {
                        const {product} = this.state;
                        const {mainImage} = product;
                        const {contentUrl} = mainImage;
                        {/** Download*/
                        }
                        if (contentUrl) {
                            await downloadFile(config, contentUrl)
                                .then(url => {
                                    this.setState({
                                        product: {
                                            ...product,
                                            mainImage: {
                                                ...mainImage,
                                                contentUrl: url
                                            }
                                        },
                                        currentImage: url
                                    }, () => resolve("Success"))
                                })
                                .catch(error => console.log(productName + " Image Download => " + error))
                        } else {
                            this.setState({
                                currentImage: DefaultImage
                            });
                            resolve("Done");

                        }
                    })
                })
                .catch(error => {
                    console.log(productName + " Image Get => " + error);
                    this.setState({
                        currentImage: DefaultImage
                    });
                    reject(error);
                })
        });
    }

    setContainedList(countSize) {
        const {containedListPagination} = this.state;
        const {start, end} = containedListPagination;
        if (countSize) {
            this.setState({
                containedListPagination: {
                    start: start + countSize,
                    end: end + countSize
                }
            }, () => {
                this.getContainedList();
            })
        } else {
            this.getContainedList();
        }
    }

    getContainedList() {
        const {
            config, query, product, containedList, containedListPagination,
            message
        } = this.state;
        const {productID, productName} = product;
        const {listPurchasedTogether} = query;
        const {data, pageLimit} = listPurchasedTogether;
        const {start, end} = containedListPagination;
        const {noMoreListError} = message;

        this.setState({
            containedListLoading: true
        });
        return new Promise((resolve, reject) => {
            requestList(Object.assign({}, listPurchasedTogether, {
                    data: {
                        ...data,
                        productID
                    },
                    pageLimit: {
                        ...pageLimit,
                        start,
                        end
                    }
                }
            ), config)
                .then(data => {
                    this.setState({
                        containedList: [...containedList, ...data.filter(product =>
                            containedList.findIndex(item => item["productID"] === product["productID"]) === -1)],
                        containedListLoading: false
                    });
                    resolve("Similar Products success")
                })
                .catch(error => {
                    console.log("Similar products of " + productName + " " + error);
                    this.setState({
                        containedListLoading: false,
                        containedListError: noMoreListError
                    });
                    reject(error);
                })
            // .then(() => this.handleScroll())
        });
    }

    handleScroll() {
        const idList = ["similarProducts"];
        for (let key in idList) {
            const domObject = document.getElementById(idList[key]);
            if (domObject) {
                // IE9, Chrome, Safari, Opera
                domObject.addEventListener("mousewheel", (e) => {
                        e = window.event || e;
                        var delta = Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)));
                        domObject.scrollLeft -= (delta * 100); // Multiplied by 80
                        e.preventDefault();
                    },
                    false);
                // Firefox
                domObject.addEventListener("DOMMouseScroll", (e) => {
                    e = window.event || e;
                    var delta = Math.max(-1, Math.min(1, (e.wheelDelta || -e.detail)));
                    domObject.scrollLeft -= (delta * 100); // Multiplied by 80
                    e.preventDefault();
                }, false);
            }
        }
    }

    setSimilarProducts(countSize) {
        const {similarProductsPagination} = this.state;
        const {start, end} = similarProductsPagination;
        if (countSize) {
            this.setState({
                similarProductsPagination: {
                    start: start + countSize,
                    end: end + countSize
                }
            }, () => {
                this.getSimilarProducts();
            })
        } else {
            this.getSimilarProducts();
        }
    }

    getSimilarProducts() {
        const {
            config, query, product, similarProducts, similarProductsPagination,
            message
        } = this.state;
        const {productID, productName} = product;
        const {listSimilarProducts} = query;
        const {data, pageLimit} = listSimilarProducts;
        const {start, end} = similarProductsPagination;
        const {noMoreListError} = message;

        this.setState({
            similarProductsLoading: true
        });
        return new Promise((resolve, reject) => {
            requestList(Object.assign({}, listSimilarProducts, {
                    data: {
                        ...data,
                        productID
                    },
                    pageLimit: {
                        ...pageLimit,
                        start,
                        end
                    }
                }
            ), config)
                .then(data => {
                    this.setState({
                        similarProducts: [...similarProducts, ...data.filter(product =>
                            similarProducts.findIndex(item => item["productID"] === product["productID"]) === -1)],
                        similarProductsLoading: false
                    });
                    resolve("Similar Products success")
                })
                .catch(error => {
                    console.log("Similar products of " + productName + " " + error);
                    this.setState({
                        similarProductsLoading: false,
                        similarProductsError: noMoreListError
                    });
                    reject(error);
                })
                .then(() => this.handleScroll())
        });
    }

    toggleMenu() {
        const {menuVisible} = this.state;
        this.setState({
            menuVisible: !menuVisible
        })
    }

    toggleZoom() {
        const {isZoomed} = this.state;
        this.setState({
            isZoomed: !isZoomed
        }, () => console.log(this.state.isZoomed))
    }

    enableZoom() {
        this.setState({
            isZoomed: true
        }, () => console.log(this.state.isZoomed))
    }

    disableZoom() {
        this.setState({
            isZoomed: false
        }, () => console.log(this.state.isZoomed))
    }

    setActive(event) {
        const tablinks = document.getElementsByClassName("image-zoom__gallery-item");
        for (let i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" active", "");
        }
        event.currentTarget.className += " active";
    }

    changeCurrentImage(src) {
        this.setState({
            currentImage: src
        });
    }

    /** Set input quantity to State*/
    setQty(qty) {
        this.setState({qty});
    }

    /** Here need to handle for cases with limited availability*/
    increaseQty() {
        const {qty} = this.state;
        this.setState({
            qty: +qty + +1
        });

    }

    /** Minimum quantity is 1 */
    decreaseQty() {
        const {qty} = this.state;
        if (qty > 1) {
            this.setState({
                qty: qty - 1
            })
        }
    }

    clearQty() {
        this.setState({qty: 1});
    }

    render() {
        const {handleUndo, showShoppingList, handleSearchConfirm,} = this.props;
        const {
            config, menuVisible, currentImage, isZoomed, product, qty,
            similarProducts, similarProductsLoading, similarProductsError,
            containedList, containedListLoading, containedListError,
            imageList, productImageLoading, loadMoreSimilarVisible,
            topMargin
        } = this.state;
        const {
            productPrice, productName, unitValue, manufacturedBy,
            material, directionOfUse, productDetails, description,
            availability, productID, offer
        } = product || {};
        const {priceCurrency, maxPrice, price} = productPrice || {};
        const {value, unitText} = unitValue || {};
        const {legalName} = manufacturedBy || {};
        const {offerType} = offer || {};
        // const parser = DOMParser;
        return (
            <div className="all-page">
                <div style={{flex: "auto", marginTop: `${topMargin}px`}}/>

                <div className="layout page page--has-sidebar">
                    <div className="page-wrapper page--product-detail">
                        <div className="container">
                            <div className="product">
                                <div className="row">
                                    <div className="col-lg-12">
                                        <div className="row">
                                            <div className="col-md-6 order-2">
                                                {/** Product name*/}
                                                <div className="page__header product__header">
                                                    <h1 className="page__title">{productName} - {value}{unitText}</h1>
                                                    <div className="page__subtitle">
                                                        {/** Search by manufacturer name*/}
                                                        <span><Link
                                                            to="/search:Sweet Baby Ray's">{legalName}</Link></span>
                                                        {/** Availability*/}
                                                        <span>{availability}</span>
                                                    </div>
                                                </div>

                                                {/** Price information*/}
                                                <div className="product__price price price--discounted">
                                                    <span
                                                        className="price-current">{priceCurrency} {price} &nbsp;</span>
                                                    {
                                                        maxPrice
                                                            ? <span className="price-previous">
                                                                {priceCurrency} {maxPrice}
                                                                </span>
                                                            : null
                                                    }
                                                </div>

                                                {/** Offer information*/}
                                                <div className="product__offer-info">
                                                    {offerType}
                                                </div>

                                                {/** Short Description*/}
                                                <p className="product__short-description"
                                                   id="description">
                                                    {description}
                                                    {/*{JSON.stringify(parser.parseFromString(description, "text/html"))}*/}
                                                </p>

                                                {/** Product quantity options*/}
                                                {/** compare between different offers this product is present in and show all quantity option*/}
                                                {/*<div className="product__quantity-options">*/}
                                                {/*<div className="js-product__quantity-slider">*/}
                                                {/*<Slider speed={300}*/}
                                                {/*arrows={true}*/}
                                                {/*slidesToShow={3}*/}
                                                {/*adaptiveHeight={true}>*/}
                                                {/*<QuantityOption/>*/}
                                                {/*<QuantityOption/>*/}
                                                {/*<QuantityOption/>*/}
                                                {/*<QuantityOption/>*/}
                                                {/*</Slider>*/}
                                                {/*</div>*/}
                                                {/*</div>*/}

                                                {/** Quantity add and add to shopping list*/}
                                                <div className="product__qty-add">

                                                    <div className="product__quantity-select">

                                                        <div className="input-group flex-nowrap product-qty__select  ">
                                                            <div className="input-group-prepend">
                                                                <button
                                                                    className="product-qty__btn product-qty__btn--left product-qty__btn--minus"
                                                                    type="button"
                                                                    onClick={this.decreaseQty}
                                                                >-
                                                                </button>
                                                            </div>
                                                            <input type="text"
                                                                   className="form-control product-qty__input"
                                                                   value={qty}
                                                                   onChange={(event) => this.setQty(event.target.value)}/>
                                                            <div className="input-group-append">
                                                                <button
                                                                    className="product-qty__btn product-qty__btn--right product-qty__btn--plus"
                                                                    type="button"
                                                                    onClick={this.increaseQty}
                                                                >+
                                                                </button>
                                                            </div>
                                                        </div>

                                                    </div>

                                                    <div className="product__add-btn">
                                                        <div className="btn-group btn-primary btn-block" role="group">
                                                            <button type="button" className="btn btn-primary btn-block"
                                                                    onClick={() => {
                                                                        handleUndo(false);
                                                                        const temp = Object.assign({}, {...product}, {quantity: qty});
                                                                        showShoppingList(temp);
                                                                        // this.clearQty();
                                                                        setTimeout(() => {
                                                                            this.clearQty()
                                                                        }, 200);
                                                                    }}
                                                                    disabled={!productID}
                                                            >
                                                                Add
                                                                to Shopping list
                                                            </button>
                                                            {/** This dropdown menu is commented because it includes Add to certain Shopping list
                                                             option which is not included in this version*/}
                                                            {/*<div className="btn-group btn-group-lg position-static"*/}
                                                            {/*role="group">*/}
                                                            {/*<button id="btnGroupDrop1" type="button"*/}
                                                            {/*onClick={this.toggleMenu}*/}
                                                            {/*className="btn btn-primary dropdown-toggle"*/}
                                                            {/*data-toggle="dropdown" aria-haspopup="true"*/}
                                                            {/*aria-expanded="false">*/}
                                                            {/*</button>*/}

                                                            {/*{menuVisible ?*/}
                                                            {/*<div className="dropdown-menu dropdown-menu-right"*/}
                                                            {/*aria-labelledby="" xPlacement="bottom-end">*/}
                                                            {/*<h6 className="dropdown-header">Select List</h6>*/}
                                                            {/*<a className="dropdown-item"*/}
                                                            {/*href="#">Birthday</a>*/}
                                                            {/*<a className="dropdown-item" href="#">BBQ*/}
                                                            {/*Party</a>*/}
                                                            {/*<div className="dropdown-divider"/>*/}
                                                            {/*<div className="form-group px-3 py-1 mb-0">*/}
                                                            {/*<input type="text"*/}
                                                            {/*className="form-control dropdown-item px-0"*/}
                                                            {/*placeholder="Create New List"/>*/}
                                                            {/*</div>*/}
                                                            {/*</div> : null}*/}

                                                            {/*</div>*/}
                                                        </div>
                                                    </div>
                                                </div>

                                                {/** Favourite and share*/}
                                                {/*<div className="mt-4 mb-4">*/}
                                                {/*<Link to="/" className="btn btn-sm btn-link"><span*/}
                                                {/*className="icon-favorite"/>Favourite</Link>*/}
                                                {/*<Link to="/" className="btn btn-sm btn-link"><span*/}
                                                {/*className="icon-share"/>Share</Link>*/}
                                                {/*</div>*/}
                                            </div>

                                            {/** Product Image section*/}
                                            {
                                                !productImageLoading
                                                    ? (imageList.length
                                                        ? <ProductImage changeCurrentImage={this.changeCurrentImage}
                                                                        setActive={this.setActive}
                                                                        imageList={imageList}
                                                                        currentImage={currentImage}
                                                                        config={config}/>
                                                        : null
                                                    )
                                                    : <LoadingComponent/>
                                            }


                                        </div>
                                    </div>

                                </div>

                                <div className="row">
                                    {
                                        productDetails
                                            ? <div className="col-md-6">
                                                <div>
                                                    <h3 className="title"><span>Product Details</span></h3>
                                                    <div className="description" id="productDetails">
                                                    </div>
                                                </div>
                                            </div>
                                            : <p className="description" id="productDetails"/>

                                    }

                                    <div className="col-md-6">
                                        {
                                            material
                                                ? <div>
                                                    <h3 className="title"><span>Ingredients</span></h3>
                                                    <p className="description" id="material">
                                                        {material}
                                                    </p>
                                                </div>
                                                : <p className="description" id="material"/>

                                        }
                                        {
                                            directionOfUse
                                                ? <div>
                                                    <div className="title"><span>Directions to Use</span></div>
                                                    <p className="description" id="directionOfUse">
                                                        {directionOfUse}
                                                    </p>
                                                </div>
                                                : <p className="description" id="directionOfUse"/>

                                        }
                                    </div>
                                </div>


                            </div>


                        </div>
                        {/** List Containing this item*/}
                        {/** Show this section only if there is any list*/}
                        {
                            !containedListLoading
                                ? (containedList.length
                                ? <div className="related-content related-content--lists">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col">
                                                <h2 className="section__header"><span>Lists containing this item</span>
                                                    {/*<small><a href="#">View All</a></small>*/}
                                                </h2>
                                            </div>
                                        </div>
                                        <div
                                            className="row"
                                            id="containedList">
                                            {
                                                containedList.map(list => {
                                                    return <ListPreview list={list}/>
                                                })
                                            }
                                        </div>
                                        {/*{containedList.length*/}

                                        {/*? <div*/}
                                        {/*style={{*/}
                                        {/*display: "grid",*/}
                                        {/*// gridTemplateColumns: "1fr 1fr 1fr",*/}
                                        {/*gridTemplateRows: "1fr 1fr 1fr 1fr ",*/}
                                        {/*justifyItems: "center",*/}
                                        {/*margin: "10% 0"*/}
                                        {/*}}*/}
                                        {/*>*/}
                                        {/*<div/>*/}
                                        {/*{*/}
                                        {/*!containedList.length*/}
                                        {/*? <button*/}
                                        {/*className="btn btn-primary  btn-block"*/}
                                        {/*title="Click to load more "*/}
                                        {/*onClick={(event) => {*/}
                                        {/*event.preventDefault();*/}
                                        {/*this.setContainedList(4);*/}
                                        {/*}}*/}
                                        {/*style={{*/}
                                        {/*boxShadow: 'rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(0, 0, 0, 0.1) 0px 6px 20px 0px;'*/}
                                        {/*}}>*/}
                                        {/*Load More*/}
                                        {/*</button>*/}
                                        {/*: <p>{containedListError}</p>*/}
                                        {/*}*/}
                                        {/*</div>*/}
                                        {/*: null}*/}
                                    </div>
                                </div>
                                : null)
                                : <LoadingComponent/>
                        }


                        {
                            !similarProductsLoading
                                ? (    similarProducts.length
                                ?
                                <div className="related-content related-content--products">

                                    <div className="container">
                                        <div className="row">
                                            <div className="col">
                                                <h2 className="section__header"><span>Similar Products</span>
                                                    <small><Link
                                                        to={`search/SimilarProducts/Similar Products/${productID}`}>View
                                                        All</Link></small>
                                                </h2>
                                            </div>
                                        </div>
                                        <div className="horizontal-scroll " id="similarProducts">
                                            {similarProducts.map(product => {
                                                return <ProductPreview
                                                    showShoppingList={showShoppingList}
                                                    product={product}
                                                    handleUndo={handleUndo}
                                                    // classname="col-xl-4 col-md-6"
                                                    classname="col-xl-2 col-lg-4 col-md-6"
                                                />;
                                            })}
                                            {similarProducts.length

                                                ? <div
                                                    style={{
                                                        display: "grid",
                                                        // gridTemplateColumns: "1fr 1fr 1fr",
                                                        gridTemplateRows: "1fr 1fr 1fr 1fr ",
                                                        justifyItems: "center",
                                                        margin: "10% 0"
                                                    }}
                                                >
                                                    {

                                                    }
                                                    <div/>
                                                    {
                                                        !similarProductsError.length
                                                            ? <button
                                                                className="btn btn-primary  btn-block"
                                                                title="Click to load more similar products"
                                                                onClick={(event) => {
                                                                    event.preventDefault();
                                                                    this.setSimilarProducts(4);
                                                                }}
                                                                style={{
                                                                    boxShadow: 'rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(0, 0, 0, 0.1) 0px 6px 20px 0px;',
                                                                    // lineHeight : "1",
                                                                    // borderRadius : "50%"
                                                                }}>
                                                                Next &nbsp;
                                                            </button>
                                                            : <p>{similarProductsError}</p>
                                                    }
                                                </div>
                                                : null}
                                        </div>

                                    </div>

                                </div>
                                : null)
                                : <LoadingComponent/>
                        }


                    </div>
                </div>
                {/*<ImageZoom imageSrc={currentImage}/>*/}
                {/*{isZoomed ? <ImageZoom /> : null}*/}
            </div>
        );
    }
}

const QuantityOption = (props) => {
    return <div className="product__quantity-option"
                style={{
                    width: '100%',
                    display: 'inline-block'
                }}>
        Buy 5 or more
        <span className="price">Rs. 192</span> per item
    </div>
};

const ProductImage = (props) => {
    const {changeCurrentImage, currentImage, setActive, imageList, config} = props;
    const props1 = {
        width: 400,
        // height: 400,
        height: 'auto',
        zoomWidth: 6000,
        scale: 1,
        offset: {vertical: 10, horizontal: 10},
        // img: "https://cdn.pixabay.com/photo/2013/04/06/11/50/image-editing-101040_960_720.jpg"
        img: currentImage || imageList[0]["contentUrl"]
    };
    return <div className="col-md-6 order-1">
        <div className="product-image-slider">

            <div className="image-zoom">
                <div className="image-zoom__wrapper">

                    <div style={{
                        height: '100%',
                        width: '100%'
                    }} className="zoomWrapper" id="zoomWrapper">
                        {/*<img*/}
                        {/*id="js-image-zoom__image"*/}
                        {/*className="image-zoom__image "*/}
                        {/*src={currentImage}*/}
                        {/*style={{position: 'absolute'}}/>*/}
                        <ReactImageZoom {...props1}/>

                    </div>


                    <div id="image_zoom_gallery" className="image-zoom__gallery">

                        {imageList.map((image, index) => {
                            const {contentUrl, identifier, representativeOfPage} = image;
                            if (index === 0) {
                                return <span className="image-zoom__gallery-item active"
                                             style={{"cursor": "pointer"}}
                                             onClick={(event) => {
                                                 setActive(event);
                                                 changeCurrentImage(contentUrl);
                                             }}>
                                    <img src={contentUrl} width="100"/>
                                </span>
                            }
                            else {
                                return <span className="image-zoom__gallery-item"
                                             style={{"cursor": "pointer"}}
                                             onClick={(event) => {
                                                 setActive(event);
                                                 changeCurrentImage(contentUrl);
                                             }}>
                                    <img src={contentUrl} width="100"/>
                                </span>
                            }
                        })}


                    </div>
                </div>
            </div>
        </div>
    </div>;
};

const ImageZoom = (props) => {
    const {imageSrc} = props;
    return <div id="zoomContainer"
                style={{
                    transform: 'translateZ(0px)',
                    position: 'absolute', left: '111px',
                    top: '177.438px', height: '383.5px',
                    width: '383.5px'
                }}>
        <div className="zoomLens"
             style={{
                 backgroundPosition: '0px 0px',
                 float: 'right',
                 overflow: 'hidden',
                 zIndex: '999',
                 transform: 'translateZ(0px)',
                 opacity: '0.4',
                 zoom: '1',
                 width: '109.571px',
                 height: '109.571px',
                 backgroundColor: 'white',
                 cursor: 'pointer',
                 border: '1px solid rgb(0, 0, 0)',
                 backgroundRepeat: 'no-repeat',
                 position: 'absolute',
                 left: '141px',
                 top: '224px'
             }}>&nbsp;</div>
        <div className="zoomWindowContainer" style={{width: '400px'}}>
            <div
                style={{
                    overflow: 'hidden',
                    backgroundPosition: '-515.864px -820.917px',
                    textAlign: 'center',
                    backgroundColor: 'rgb(255, 255, 255)',
                    width: '400px',
                    height: '400px',
                    float: 'left',
                    backgroundSize: '1400px 1400px',
                    zIndex: '100',
                    border: '4px solid rgb(136, 136, 136)',
                    backgroundRepeat: 'no-repeat',
                    position: 'absolute',
                    backgroundImage: `url("${imageSrc}")`,
                    top: '0px',
                    left: '383.5px'
                }}
                className="zoomWindow">&nbsp;</div>
        </div>
    </div>
};


