import React from "react";
import Slider from "react-slick";
import {offerList} from "../sampleData/sampleData";
import {Link} from "react-router-dom";
import {composeOffer, LoadingComponent} from "../StaticContent/staticContents";
import {downloadFile, requestList, requestValue} from "../helpers/request_helpers";
import {getFreeCollection, getOfCollection} from "../helpers/Utilities";
import {LOCAL_CONSTANTS} from "../helpers/Constants";

export default class extends React.Component {
    /**
     * Featured Banner with offers or celebration informations
     */

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            featuredBannerList: [],
            featuredBannerLoading: false
        };
        this.setFeaturedBanner = this.setFeaturedBanner.bind(this);
        this.getFreeCollection = getFreeCollection.bind(this);
        this.getOfCollection = getOfCollection.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        this.setFeaturedBanner();
    }

    setFeaturedBanner() {
        const {config, query} = this.state;
        const {listBannerOffers} = query;
        this.setState({
            featuredBannerLoading: true
        });
        requestList(listBannerOffers, config)
            .then(data => {
                this.setState({
                    featuredBannerList: data,
                    featuredBannerLoading: false
                }, () => {
                    // console.log("HAVE TO CHECK THIS FEATURED BANNER REQUEST LATER ON! ==>> ", this.state.featuredBannerList);
                    const {featuredBannerList, config, query} = this.state;
                    let tempList = [...featuredBannerList];
                    featuredBannerList.map(async (banner, index) => {
                        const {identifier} = banner;
                        await this.getOfCollection(identifier)
                            .then(async ofCollection => {
                                await this.getFreeCollection(identifier)
                                    .then(freeCollection => {
                                        const {getMainImageOffer} = query;
                                        const {data} = getMainImageOffer;
                                        requestValue(Object.assign({}, getMainImageOffer, {
                                            data: {
                                                ...data,
                                                identifier
                                            }
                                        }), config)
                                            .then(async response => {
                                                const {contentUrl} = response;
                                                await downloadFile(config, contentUrl)
                                                    .then(url => {
                                                        tempList.splice(index, 1, {
                                                            ...tempList[index],
                                                            contentUrl: url,
                                                            ofCollection,
                                                            freeCollection
                                                        });
                                                        this.setState({
                                                            featuredBannerList: tempList
                                                        })
                                                    })
                                                    .catch(error => console.log(error))
                                            })
                                    })

                            })
                            .catch(error => console.log(error))
                    })
                })
            })
            .catch(error => {
                console.log(error);
                this.setState({
                    featuredBannerLoading: false
                });
            });
    }

    render() {
        const {showShoppingList} = this.props;
        const {featuredBannerList, config, query, featuredBannerLoading} = this.state;
        return (
            <div className="carousel" id="homepage_promo_carousel" style={{marginTop: '-10px'}}>
                {
                    !featuredBannerLoading
                        ? <Slider dots={true}
                                  infinite={true}
                                  speed={300}
                                  arrows={false}
                                  slidesToShow={1}
                                  slidesToScroll={1}
                                  autoplay={true}
                                  autoplaySpeed={6000}
                                  adaptiveHeight={true}>

                            {featuredBannerList.length
                                ? featuredBannerList.map(offer => {
                                        const {name, offerType, contentUrl, identifier} = offer;
                                        return <div className="carousel__item-wrapper">
                                            <div className="carousel__item carousel__item--gradient " style={{
                                                backgroundRepeat: 'no-repeat',
                                                backgroundSize: 'cover',
                                                backgroundPosition: 'center',
                                                backgroundImage: `url(${contentUrl})`
                                            }}>
                                                <div>
                                                    <h2 className="carousel__item-title">{composeOffer(offer)}</h2>
                                                    <p className="carousel__item-subtitle">{`${name}`}</p>
                                                    <span
                                                        style={{boxShadow: "rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(0, 0, 0, 0.1) 0px 6px 20px 0px"}}>
                                                <Link to={`/search/KiranaOffer/${name}/${identifier}`}
                                                      className="btn btn-primary btn-lg">
                                                    <i className="icon-star"/>&nbsp; View this offer
                                                </Link>
                                             </span>
                                                </div>
                                            </div>
                                        </div>
                                    }
                                )
                                : null
                            }
                        </Slider>
                        : <LoadingComponent/>
                }
            </div>
        );
    }
};



