import React from "react";
import basmati_rice from "../style/images/gyan-basmati-rice.png";
import {Link} from "react-router-dom";
import {requestValue, downloadFile} from "../helpers/request_helpers";
import {composeOffer, DefaultImage} from "../StaticContent/staticContents";
import {expandOverflow, originalClass} from "../helpers/Utilities";
import {CLASSNAMES, LOCAL_CONSTANTS} from "../helpers/Constants";

const CONTEXT = "http//semantro.com/";
const URL = "contentUrl";
const UNIT_TEXT = "unitText";
const VALUE = "value";
const PRICE = "price";
const MAX_PRICE = "maxPrice";
const PRICE_CURRENCY = "priceCurrency";


export class ProductPreview extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            root: {},
            menuVisible: false,
            qty: 1,
            fullNameVisible: false,
            product: {
                mainImage: {
                    contentUrl: ""
                },
                offer: {}
            }
        };
        this.showMenu = this.showMenu.bind(this);
        this.hideMenu = this.hideMenu.bind(this);
        this.toggleMenu = this.toggleMenu.bind(this);
        this.increaseQty = this.increaseQty.bind(this);
        this.decreaseQty = this.decreaseQty.bind(this);
        this.setQty = this.setQty.bind(this);
        this.clearQty = this.clearQty.bind(this);
        this.getMainImage = this.getMainImage.bind(this);
        this.getQValue = this.getQValue.bind(this);
        this.getProductPrice = this.getProductPrice.bind(this);
        this.toggleFullName = this.toggleFullName.bind(this);
        this.getProductOffer = this.getProductOffer.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        const root = localStorage.getItem(LOCAL_CONSTANTS.ROOT);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message),
                root: JSON.parse(root)
            })
        }
    }

    componentDidMount() {
        const {product, classname} = this.props;
        if (product) {
            this.setState({
                product
            }, () => {
                this.getQValue()
                    .then(() => {
                        this.getProductPrice()
                            .then(() => {
                                this.getMainImage()
                                    .then(() => {
                                        this.getProductOffer();
                                    });
                            })
                    })
                    .catch(error => console.log(error))
            })
        }
    }

    getProductOffer() {
        const {config, query, product} = this.state;
        const {getProductOffer} = query;
        const {data} = getProductOffer;
        const {productID, productName} = product;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getProductOffer, {
                data: {
                    ...data,
                    productID
                }
            }), config)
                .then(offer => {
                    this.setState({
                        product: {
                            ...product,
                            offer
                        }
                    }, () => {
                        resolve("success");
                    })
                })
                .catch(error => {
                    console.log(productName + " Offer " + error);
                    reject(error);
                })
        })


    }

    toggleFullName() {
        const {fullNameVisible} = this.state;
        this.setState({
            fullNameVisible: !fullNameVisible
        })
    }

    getMainImage() {
        const {config, query, product} = this.state;
        const {getMainImageProduct} = query;
        const {productID, productName, identifier} = product;
        const {data} = getMainImageProduct;
        return new Promise((resolve, reject) => {

            requestValue(Object.assign({}, getMainImageProduct, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(mainImage => {
                    // console.log(productName, mainImage);
                    this.setState({
                        product: {
                            ...product,
                            mainImage
                        }
                    }, async () => {
                        const {product} = this.state;
                        const {mainImage} = product;
                        const {contentUrl} = mainImage;
                        {/** Download*/
                        }
                        if (contentUrl) {
                            await downloadFile(config, contentUrl)
                                .then(url => {
                                    this.setState({
                                        product: {
                                            ...product,
                                            mainImage: {
                                                ...mainImage,
                                                contentUrl: url
                                            }
                                        }
                                    }, () => resolve("Success"))
                                })
                                .catch(error => console.log(productName + " Image Download => " + error))
                        } else {
                            resolve("Done")
                        }
                    })
                })
                .catch(error => {
                    console.log(productName + " Image Get => " + error);
                    reject(error);
                })
        })
    }

    getQValue() {
        const {product, config, query} = this.state;
        const {productID} = product;
        const {getProductQValue} = query || {};
        const {data} = getProductQValue;

        return new Promise((resolve, reject) => {
            const {product} = this.state;

            requestValue(Object.assign({}, getProductQValue, {
                data: {
                    ...data,
                    productID
                }
            }), config)
                .then(data => {
                    this.setState({
                        product: {
                            ...product,
                            unitValue: data
                        }
                    }, () => {
                        resolve("Unit value success");
                    })
                })
                .catch(error => {
                    console.log("Error in UnitValue");
                    reject(error);
                })
        })
    }

    getProductPrice() {
        const {product, config, query} = this.state;
        const {productID} = product;
        const {getProductPrice} = query || {};
        const {data} = getProductPrice;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getProductPrice, {
                data: {
                    ...data,
                    productID
                }
            }), config)
                .then(productPrice => {
                    this.setState({
                        product: {
                            ...product,
                            productPrice
                        }
                    }, () => {
                        resolve("Product price success");
                    })
                })
                .catch(error => {
                    console.log("Product PRICE ", error);
                    reject(error);
                });
        })
    }

    showMenu() {
        this.setState({
            menuVisible: true
        });
    }

    hideMenu() {
        this.setState({
            menuVisible: false
        });
    }

    toggleMenu() {
        const {menuVisible} = this.state;
        this.setState({menuVisible: !menuVisible})
    }

    /** Set input quantity to State*/
    setQty(qty) {
        this.setState({qty});
    }

    /** Here need to handle for cases with limited availability*/
    increaseQty() {
        const {qty} = this.state;
        this.setState({
            qty: +qty + +1
        });

    }

    /** Minimum quantity is 1 */
    decreaseQty() {
        const {qty} = this.state;
        if (qty > 1) {
            this.setState({
                qty: qty - 1
            })
        }
    }

    clearQty() {
        this.setState({qty: 1});
    }

    render() {
        const {menuVisible, qty, product, fullNameVisible, root} = this.state;
        const {showShoppingList, queryString, handleUndo, classname, origin} = this.props;
        const {
            productName, productID, identifier, unitValue, productPrice, mainImage,
            offer
        } = product;
        const {contentUrl} = mainImage || {};
        const {unitText, value} = unitValue || {};
        const {price, maxPrice, priceCurrency} = productPrice || {};
        const {offerType, offerDiscount} = offer || {};
        const {rootPath} = root;
        return (
            <div className={classname + " " + CLASSNAMES.PRODUCT_CARD} itemScope
                 itemType="http://semantro.com/KiranaProduct"
                 style={{
                     flexShrink: 0,
                     flexGrow: 0,
                     width: "50%"
                 }}>
                <div className="product-card product-card--onsale"
                     style={{
                         // boxShadow: '0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1)',
                         marginBottom: "1.5rem",
                         // minWidth : "183px",
                         width: "100%",
                     }}>
                    <a href={`${rootPath}#/detail:${identifier}`} target="_blank" style={{textDecoration: "none"}}>
                        <div className="product-card__top-actions">
                            {
                                offerType && offerType === "Percentage"
                                    ? <div className="product-card__onsale-ribbon">
                                        {/*SALE {offerDiscount} % OFF*/}
                                        SALE {composeOffer(offer)}
                                    </div>
                                    : (maxPrice
                                    ? <div className="product-card__onsale-ribbon">
                                        {/*SALE {offerDiscount} % OFF*/}
                                        SALE
                                    </div>
                                    : null)
                            }

                            {/*<a href="#"*/}
                            {/*className="product-card__action-icon product-card__action-favorite icon-favorite"/>*/}
                        </div>

                        {/** Product image*/}
                        <div className="product-card__image-holder"
                             title="Click to view detail">
                            <div itemType={`${CONTEXT}MediaObject`} className="product-card__image">
                                <img
                                    // style={{maxWidth: "100%", height: "auto"}}
                                    itemProp={URL}
                                    src={contentUrl ? contentUrl : DefaultImage}/>
                            </div>
                        </div>
                    </a>
                    {/** Product info*/}
                    <div className="product-card__info">
                        <h2 className={`product-card__title ${originalClass} `} id={origin + identifier}
                            onClick={() => expandOverflow(origin + identifier)}>
                            {productName}
                        </h2>
                        <div className="d-flex flex-column justify-content-between ">
                            <div className="d-flex flex-column product-card__price-container">

                                <div className="product-card__price">
                                    <span className="product-card__actual-price"><span
                                        className="product-card__currency">{priceCurrency}</span><span
                                        className="product-card__price-amount">{price} &nbsp;</span></span>
                                    {
                                        maxPrice
                                            ? <span className="product-card__compare-price"><span
                                                className="product-card__currency">{priceCurrency}</span><span
                                                className="product-card__price-amount">{maxPrice}</span></span>

                                            : null
                                    }
                                    {/*<span*/}
                                    {/*className="product-card__price-amount product-card__price-amount--extra">{`(${priceCurrency} ${price / value}/${unitText})`}</span>*/}
                                </div>
                            </div>

                            <div className="d-flex flex-column product-card__size-qty mr-md-4">
                                {/*<div className="d-flex">*/}
                                {/*<div className="form-group d-flex align-items-center">*/}
                                {/*<label htmlFor="product-card-size" className="mr-2">Size</label>*/}
                                {/*<select className="form-control" id="product-card-size">*/}
                                {/*<option itemType={`${CONTEXT}unitValue`}>*/}
                                {/*<span*/}
                                {/*itemProp={VALUE}>{value}</span>*/}
                                {/*<span*/}
                                {/*itemProp={UNIT_TEXT}>{unitText}</span>*/}
                                {/*</option>*/}
                                {/*</select>*/}
                                {/*</div>*/}
                                {/*</div>*/}

                                <div className="d-flex align-items-center">
                                    <span className="mr-2">Qty</span>

                                    {/** Large size Quantity Add/Subtract option*/}
                                    {/*<div className="input-group flex-nowrap product-qty__select  ">*/}
                                    {/*<div className="input-group-prepend">*/}
                                    {/*<button*/}
                                    {/*className="product-qty__btn product-qty__btn--left product-qty__btn--minus"*/}
                                    {/*type="button"*/}
                                    {/*onClick={this.decreaseQty}*/}
                                    {/*>-*/}
                                    {/*</button>*/}
                                    {/*</div>*/}
                                    {/*<input type="text" className="form-control product-qty__input" value={qty}*/}
                                    {/*onChange={(event) => this.setQty(event.target.value)}/>*/}
                                    {/*<div className="input-group-append">*/}
                                    {/*<button*/}
                                    {/*className="product-qty__btn product-qty__btn--right product-qty__btn--plus"*/}
                                    {/*type="button" onClick={this.increaseQty}*/}
                                    {/*>+*/}
                                    {/*</button>*/}
                                    {/*</div>*/}
                                    {/*</div>*/}
                                    <div
                                        className="input-group flex-nowrap product-qty__select  product-qty__select--sm  ">
                                        <div className="input-group-prepend">
                                            <button
                                                className="product-qty__btn product-qty__btn--left product-qty__btn--minus"
                                                onClick={() => {
                                                    this.decreaseQty();
                                                    // showSearchResults();
                                                }}
                                                type="button">-
                                            </button>
                                        </div>
                                        <input type="text" className="form-control product-qty__input"
                                               value={qty}
                                               onChange={(event) => {
                                                   this.setQty(event.target.value)
                                               }}/>

                                        <div className="input-group-append">
                                            <button
                                                className="product-qty__btn product-qty__btn--right product-qty__btn--plus"
                                                onClick={() => {
                                                    this.increaseQty();
                                                    // showSearchResults();
                                                }}
                                                type="button">+
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                    {/** Preview footer*/}
                    <div className="product-card__cta d-none d-lg-block">
                        <div className="btn-group btn-block" role="group">
                            <button type="button" className="btn btn-outline-secondary btn-block"
                                    onClick={() => {
                                        handleUndo(false);
                                        {/** This 1st condition is for generalName from search list */
                                        }
                                        const temp = queryString ? Object.assign({}, {...product}, {quantity: qty}, {generalName: queryString})
                                            : Object.assign({}, {...product}, {quantity: qty});
                                        showShoppingList(temp);
                                        // this.clearQty();
                                        setTimeout(() => {
                                            this.clearQty()
                                        }, 200);
                                    }}>
                                Add to Shopping list
                            </button>
                            {/** This dropdown menu is commented because it includes Add to certain Shopping list
                             option which is not included in this version*/}
                            {/*<div className="btn-group btn-group-lg position-static" role="group">*/}
                            {/*<button id="btnGroupDrop1" type="button"*/}
                            {/*onClick={this.toggleMenu}*/}
                            {/*// onBlur={this.hideMenu}*/}
                            {/*className="btn btn-outline-secondary dropdown-toggle" data-toggle="dropdown"*/}
                            {/*aria-haspopup={true} aria-expanded={false}/>*/}

                            {/*/!** Dropdown shopping list names*!/*/}
                            {/*{menuVisible ?*/}
                            {/*<div className="dropdown-menu dropdown-menu-right" aria-labelledby=""*/}
                            {/*>*/}
                            {/*<h6 className="dropdown-header">Select List</h6>*/}
                            {/*/!** Add event to these anchor tags for adding product in list*!/*/}
                            {/*<a className="dropdown-item" href="/" onClick={this.showMenu}*/}
                            {/*>Birthday</a>*/}
                            {/*<a className="dropdown-item" href="/" onClick={this.showMenu}*/}
                            {/*>BBQ Party</a>*/}
                            {/*<div className="dropdown-divider"/>*/}
                            {/*<div className="form-group px-3 py-1 mb-0">*/}
                            {/*<input type="text" className="form-control dropdown-item px-0"*/}
                            {/*placeholder="Create New List"/>*/}
                            {/*</div>*/}
                            {/*</div> :*/}
                            {/*null*/}
                            {/*}*/}
                            {/*</div>*/}
                        </div>
                    </div>

                    <div className="btn-group btn-block mt-2 d-flex d-lg-none" role="group">
                        <button type="button"
                                className="btn btn-primary btn-block"
                                onClick={() => {
                                    handleUndo(false);
                                    {/** This 1st condition is for generalName from search list */
                                    }
                                    const temp = queryString ? Object.assign({}, {...product}, {quantity: qty}, {generalName: queryString})
                                        : Object.assign({}, {...product}, {quantity: qty});
                                    showShoppingList(temp);
                                    // this.clearQty();
                                    setTimeout(() => {
                                        this.clearQty()
                                    }, 200);
                                }}>Add
                        </button>
                        {/** This dropdown menu is commented because it includes Add to certain Shopping list
                         option which is not included in this version*/}
                        {/*<div className="btn-group" role="group">*/}
                        {/*<button id="btnGroupDrop1" type="button" className="btn btn-primary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">*/}
                        {/*</button>*/}

                        {/*<div className="dropdown-menu dropdown-menu-right" aria-labelledby="">*/}
                        {/*<h6 className="dropdown-header">Select List</h6>*/}
                        {/*<a className="dropdown-item" href="#">Birthday</a>*/}
                        {/*<a className="dropdown-item" href="#">BBQ Party</a>*/}
                        {/*<div className="dropdown-divider"></div>*/}
                        {/*<div className="form-group px-3 py-1 mb-0">*/}
                        {/*<input type="text" className="form-control dropdown-item px-0" placeholder="Create New List"/>*/}
                        {/*</div>*/}
                        {/*</div>*/}

                        {/*</div>*/}
                    </div>


                </div>
            </div>
        );
    }
}


