import React from "react";
import {Link} from "react-router-dom";
import {requestList} from "../helpers/request_helpers";
import {LOCAL_CONSTANTS} from "../helpers/Constants";
import {MobileSearch} from "../HeaderSection/MobileSearch";
import {hideHamburgerMenu, isMobileView, redirectToPath, setTopMargin} from "../helpers/Utilities";

export const TOP_ID = "top-fixed";
export const NAV_ID = "category-nav";

export default class extends React.Component {
    /**
     * Menu bar for categories in header section
     * @returns {XML}
     */
    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            root: {},
            baseCategories: [],
            childCategories: [],
            childrenLeftMargin: [],
            currentMenuID: "",
            topMargin: undefined
        };
        this.setCategories = this.setCategories.bind(this);
        this.getChildCategories = this.getChildCategories.bind(this);
        this.handleMenuClick = this.handleMenuClick.bind(this);
        this.setCurrentActiveMenu = this.setCurrentActiveMenu.bind(this);
        this.setTopMargin = setTopMargin.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        const root = localStorage.getItem(LOCAL_CONSTANTS.ROOT);
        if (config && query && message && root) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message),
                root: JSON.parse(root)
            })
        }
    }

    componentDidMount() {
        this.setTopMargin();
        this.setCategories()
            .then(() => {
                this.setBaseCategoriesLength()
                    .then(response => {
                        this.setState({
                            childrenLeftMargin: response
                        })
                    });
            })
            .catch(error => {
                console.log(error);
            });

        // window.addEventListener("scroll", (event) => {
        //     const {topMargin} = this.state;
        //     const last_known_scroll_position = window.scrollY;
        //     const element = document.getElementById(TOP_ID);
        //     if (last_known_scroll_position > topMargin && window.innerWidth < 990) {
        //         if (!element.className.includes("fixed-top")) {
        //             element.className += "fixed-top"
        //         }
        //     } else {
        //         // if (element.className.includes("fixed-top")) {
        //         element.className = element.className.replace("fixed-top", "")
        //         // }
        //
        //     }
        //
        // })
        window.addEventListener("click", (event) => {
            const target = event.target;
            const {currentMenuID} = this.state;
            if (target.id === NAV_ID) {
                if (isMobileView()) {
                    hideHamburgerMenu();
                }
            } else {
                if (!isMobileView()) {
                    this.hideMenu(currentMenuID)
                }
            }
        })

    }

    /** This function return sun of length of each category that comes before it*/
    setBaseCategoriesLength = () => {
        const {baseCategories} = this.state;
        const tempList = [];
        const finalList = [];
        return new Promise((resolve, reject) => {
            baseCategories.map((category, index) => {
                const {identifier} = category;
                const menuElement = document.getElementById(this.getMenuID(identifier));
                if (index === 0) {
                    tempList.splice(index, 1, 0);
                    finalList.splice(index, 1, 0);
                } else if (index > 0 && menuElement) {
                    const tempElement = document.getElementById(this.getMenuID(baseCategories[index - 1].identifier));
                    tempList.splice(
                        index,
                        1,
                        tempElement.clientWidth
                    );
                    finalList.splice(index, 1, tempList.reduce((leftMargin, acc) => {
                        return leftMargin + acc
                    }));
                }
                if (finalList.length === baseCategories.length) {
                    resolve(finalList)
                }

            })
        })
    };

    getMenuID(identifier) {
        return "menu-" + identifier;
    }

    getOuterID(identifier) {
        return "outer-" + identifier;
    }

    getInnerID(identifier) {
        return "inner-" + identifier;
    }

    setCategories() {
        const {config, query} = this.state;
        const {listBaseCategories} = query;
        return new Promise((resolve, reject) => {
            requestList(listBaseCategories, config)
                .then(data => {
                    this.setState({
                        baseCategories: data
                    }, () => resolve("Done"))
                })
                .catch(error => {
                    reject(error);
                });
        })
    }

    getChildCategories(identifier) {
        const {config, query} = this.state;
        const {listChildCategories} = query;
        const {data} = listChildCategories;
        return new Promise((resolve, reject) => {
            requestList(Object.assign({}, listChildCategories, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(childCategories => {
                    resolve(childCategories);
                })
                .catch(error => reject(error))

        })
    }

    showSubMenu(identifier) {
        const outerElement = document.getElementById(this.getOuterID(identifier));
        const innerElement = document.getElementById(this.getInnerID(identifier));
        if (!outerElement.className.includes("show")
            && !innerElement.className.includes("show")
        ) {
            outerElement.className += "show";
            innerElement.className += " show";
            innerElement.style.display = "block";
        }

    }

    hideMenu(identifier) {
        const element1 = document.getElementById(this.getOuterID(identifier));
        const element2 = document.getElementById(this.getInnerID(identifier));
        if (element1 && element2) {
            element1.className = element1.className.replace("show", "");
            element2.className = element2.className.replace(" show", "");
            element2.style.display = "none";
            this.setState({
                childCategories: []
            })
        }

    }

    handleSubMenu = (identifier, categoryName) => {
        const {root, currentMenuID} = this.state;
        const {rootPath} = root;
        if (currentMenuID) {
            this.hideMenu(currentMenuID);
        }
        this.getChildCategories(identifier)
            .then((childCategories) => {
                this.setState({
                    childCategories
                }, () => {
                    const {childCategories} = this.state;
                    if (childCategories.length) {
                        // if (currentMenuID) {
                        //     console.log("From handle submenu");
                        //     this.hideMenu(currentMenuID);
                        // }
                        this.showSubMenu(identifier);
                        this.setCurrentActiveMenu(identifier);

                    } else {
                        this.handleMenuClick(identifier, `#${rootPath}/search/KiranaCategory/${categoryName}/${identifier}`);
                    }
                });
            })
            .catch(error => console.log(error));

    };

    setCurrentActiveMenu = (identifier) => {
        this.setState({
            currentMenuID: identifier
        })
    };

    handleMenuClick = (identifier, link) => {
        const {currentMenuID, root} = this.state;
        const {rootPath} = root;
        if (identifier === currentMenuID) {
            // this.hideMenu(identifier);
            redirectToPath(link);
            hideHamburgerMenu();
        }
    };

    getYTranslation = (index) => {
        const {childrenLeftMargin} = this.state;
        if (window.width > 991) {
            return `translate3d(${childrenLeftMargin[index]}px, 53px, 0px)`
        } else {
            return `translate3d(5px, 5px, 0px)`
        }
    };

    render() {
        const {baseCategories, childCategories, childrenLeftMargin, currentMenuID, topMargin, root} = this.state;
        const {handleSearchConfirm} = this.props;
        const {rootPath} = root;

        return (
            <div id={TOP_ID} className="nav--sticky "
                 style={{marginTop: `${topMargin}px`,}}
            >
                <MobileSearch/>
                {/** Sticky Menu bar*/}
                <div className="category-nav-holder js-category-nav"
                     style={{
                         backgroundColor: "#FFFFFF",
                         backgroundSize: 'auto',
                         boxShadow: '0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1)',
                         // paddingBottom: '11px',
                         // width: "100%",
                         top: `${topMargin}px`,
                         zIndex: 1000
                     }}>
                    <div className="container">
                        <div id={NAV_ID}
                             className="category-nav__wrapper ">
                            <nav className="category-nav">
                                <div className="px-0 mx-auto pb-3">
                                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 131 58" width="101px">
                                        <path fill="#C1282F" fillRule="evenodd"
                                              d="M45.6 40.6l-5.4 3-20-11.5-5.2 3 20.2 11.5-5.4 3.1L9.6 38.1l-4.2 2.5v-6.2l34.8-20.1 5.4 3-20 11.7 20 11.6zM5.4 22.3l24.3-14 5.4 3L5.4 28.5v-6.2zm44.3-8.8L26.8.4C26-.1 25-.1 24.2.4L1.3 13.5A3 3 0 0 0 0 15.8v26.4c0 1 .5 1.8 1.3 2.3l22.9 13.1a2.7 2.7 0 0 0 2.6 0l22.9-13.1c.8-.5 1.3-1.4 1.3-2.3V15.8c0-1-.5-1.8-1.3-2.3zM110.8 35h2.9v-3.1h-2.9v3zm13.8-10.4h-2.4V43h-2.8V31.9h-2.9v3c0 1-.3 1.6-.8 2.3-.6.5-1.3.8-2 .8h-2.9a3 3 0 0 1-2-.8c-.5-.7-.8-1.4-.8-2.3v-3c0-.8.3-1.6.8-2.2.6-.6 1.2-.9 2-.9h8.6v-4.2h-14.2V43h-2.4V24.6h-3.3v6.1c0 .9-.4 1.6-.9 2.2-.5.6-1.1.9-1.8.9H94v6.1h5.3V43H94c-.8 0-1.4-.3-2-.9-.4-.6-.7-1.3-.7-2.2V27.7H94v3h2.7v-6.1H81v3h4.2c.8 0 1.5.4 2 1 .6.6.9 1.3.9 2.1V37c0 .8-.3 1.6-.9 2.2-.5.5-1.2.8-2 .8h-2.8v-3h2.8v-6.2H81V43h-2.8v-3H74c-.8 0-1.4-.4-2-1a3 3 0 0 1-.8-2.1v-6.2c0-.8.3-1.5.8-2.1.6-.6 1.2-1 2-1h3v3.1h-3V37h4.3V24.6h-7.1V24h-3.3v18h-3V24H62v-3h3v-3a2.9 2.9 0 0 1 3-3h9c.7 0 1.4.3 2 .9.6.6 1.7 2.2 3.2 4.7H79a41 41 0 0 1-1-1.7c-.2-.2-.4-.5-.7-.6-.2-.2-.5-.3-.9-.3H68v3h3.4v.5H131v3.1h-2.8V43h-2.4V24.6h-1.2z"/>
                                    </svg>
                                </div>
                                {
                                    baseCategories.length
                                        ? baseCategories.map((baseCategory, index) => {
                                            const {categoryName, identifier} = baseCategory;
                                            const parentID = identifier;
                                            const outerID = this.getOuterID(identifier);
                                            const innerID = this.getInnerID(identifier);
                                            const menuID = this.getMenuID(identifier);
                                            return <div id={outerID} key={index}>
                                                <a
                                                    href="javascript:void(0)"
                                                    onClick={() => {
                                                        handleSearchConfirm(false);
                                                        this.handleSubMenu(identifier, categoryName);
                                                        this.setCurrentActiveMenu(identifier);
                                                    }}
                                                    // onMouseOver={this.handleSubMenu}
                                                    // onMouseLeave={() => {
                                                    //     this.hideMenu(identifier);
                                                    // }}
                                                    id={menuID} data-toggle="dropdown"
                                                    aria-haspopup={true}
                                                    aria-expanded={false}>
                                                    {categoryName}
                                                </a>

                                                {
                                                    isMobileView() ?
                                                        <div className="dropdown-menu meganav text-left pl-4 pt-5"
                                                             aria-labelledby={menuID}
                                                             xplacement="bottom-start"
                                                             id={innerID}
                                                             style={{
                                                                 position: "absolute",
                                                                 willChange: "transform",
                                                                 top: "0px",
                                                                 left: "0px",
                                                                 transform: "translate3d(5px, 5px, 0px)"
                                                             }}
                                                        >
                                                            <a className="dropdown-item font-weight-bold "
                                                               onClick={() => {
                                                                   this.hideMenu(identifier);
                                                               }}
                                                            >

                                                                <svg xmlns="http://www.w3.org/2000/svg" height="14px"
                                                                     viewBox="0 0 492 492">
                                                                    <path
                                                                        d="M464.344 207.418l.768.168H135.888l103.496-103.724c5.068-5.064 7.848-11.924 7.848-19.124 0-7.2-2.78-14.012-7.848-19.088L223.28 49.538c-5.064-5.064-11.812-7.864-19.008-7.864-7.2 0-13.952 2.78-19.016 7.844L7.844 226.914C2.76 231.998-.02 238.77 0 245.974c-.02 7.244 2.76 14.02 7.844 19.096l177.412 177.412c5.064 5.06 11.812 7.844 19.016 7.844 7.196 0 13.944-2.788 19.008-7.844l16.104-16.112c5.068-5.056 7.848-11.808 7.848-19.008 0-7.196-2.78-13.592-7.848-18.652L134.72 284.406h329.992c14.828 0 27.288-12.78 27.288-27.6v-22.788c0-14.82-12.828-26.6-27.656-26.6z"></path>
                                                                </svg>
                                                                &nbsp;&nbsp;{categoryName}
                                                            </a>
                                                            {
                                                                childCategories.length
                                                                    ? childCategories.map((category, index) => {
                                                                        const {categoryName, identifier} = category;
                                                                        return <Link
                                                                            key={index}
                                                                            to={`/search/KiranaCategory/${categoryName}/${identifier}`}
                                                                            className="dropdown-item"
                                                                            onClick={() => {
                                                                                hideHamburgerMenu();
                                                                            }}
                                                                        >
                                                                            {categoryName}
                                                                        </Link>
                                                                    })
                                                                    : null
                                                            }
                                                        </div>
                                                        : (
                                                            childCategories.length
                                                                ?
                                                                <div className="dropdown-menu meganav text-left"
                                                                     aria-labelledby={menuID}
                                                                     xPlacement="bottom-start"
                                                                     id={innerID}
                                                                     style={{
                                                                         position: "absolute",
                                                                         willChange: "transform",
                                                                         top: "0px",
                                                                         left: "0px",
                                                                         // display: "block",
                                                                         width: "fit-content",
                                                                         padding: 0,
                                                                         // transform: "translate3d(0px, 53px, 0px)"
                                                                         transform: `translate3d(${childrenLeftMargin[index]}px, 53px, 0px)`
                                                                         // transform: this.getYTranslation(index)
                                                                     }}
                                                                    // onMouseLeave={() => this.hideMenu(parentID)}
                                                                >
                                                                    {
                                                                        childCategories.map((category, index) => {
                                                                            const {categoryName, identifier} = category;
                                                                            return <Link
                                                                                key={index}
                                                                                to={`/search/KiranaCategory/${categoryName}/${identifier}`}
                                                                                className="dropdown-item"
                                                                                onClick={() => this.hideMenu(parentID)}
                                                                            >
                                                                                {categoryName}
                                                                            </Link>
                                                                        })
                                                                    }
                                                                </div>
                                                                : null
                                                        )

                                                }

                                            </div>

                                        })
                                        : null
                                }
                            </nav>
                        </div>
                    </div>
                </div>


            </div>        )
    }
}


