import React, {Component} from "react";
import {Link} from "react-router-dom";
import {requestList, requestValue} from "../helpers/request_helpers";
import moment from "moment";
import {LOCAL_CONSTANTS,ORDER_STATES} from "../helpers/Constants";

export class PurchaseHistory extends Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            customer: {},
            errorMessage: "",
            historyList: []
        };
        this.setDataResponse = this.setDataResponse.bind(this);
        this.setErrorMessage = this.setErrorMessage.bind(this);
        this.listOrderHistory = this.listOrderHistory.bind(this);
        this.getOrderStatus = this.getOrderStatus.bind(this);
        this.getOrderedProductCount = this.getOrderedProductCount.bind(this);
        this.getOrderName = this.getOrderName.bind(this);
        this.setOrderRowData = this.setOrderRowData.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        const {customer} = this.props;
        const {userName} = customer;
        if (userName) {
            this.setState({
                customer
            }, () => {
                this.listOrderHistory();
            })
        } else {

        }
    }

    listOrderHistory = () => {
        const {config, query, message, customer} = this.state;
        const {orderHistorySearchError} = message;
        const {userName} = customer;
        const {listOrderHistory} = query;
        const {data} = listOrderHistory;
        let tempList = [];
        requestList(Object.assign({}, listOrderHistory, {
            data: {
                ...data,
                userName
            }
        }), config)
            .then(response => {
                if (response.length) {
                    response.map((order, index) => {
                        this.setOrderRowData(order)
                            .then(rowData => {
                                tempList.splice(index, 1, rowData);
                            })
                            .catch(error => console.log(error))
                            .then(() => this.setDataResponse(tempList))
                    })
                } else {
                    this.setErrorMessage(orderHistorySearchError);
                }
            })

    };

    setOrderRowData = async (order) => {
        const {identifier, previousStateId, orderAmount, orderDate, modifiedDate} = order;
        return new Promise(async (resolve, reject) => {
            await this.getOrderName(identifier)
                .then(async listName => {
                    await  this.getOrderedProductCount(identifier)
                        .then(async productCount => {
                            await this.getOrderStatus(previousStateId)
                                .then(orderStatus => {
                                    resolve({
                                        identifier,
                                        listName,
                                        productCount,
                                        orderStatus,
                                        orderDate,
                                        modifiedDate,
                                        orderAmount
                                    })
                                }).catch(error => console.log(error))
                        }).catch(error => console.log(error))

                }).catch(error => console.log(error))
        })
    };

    getOrderStatus = (previousStateId) => {
        const {config, query} = this.state;
        const {getOrderStatus} = query;
        const {data} = getOrderStatus;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getOrderStatus, {
                data: {
                    ...data,
                    previousStateId
                }
            }), config)
                .then(response => {
                    const {orderStatus} = response;
                    if (orderStatus) {
                        resolve(orderStatus);
                    } else {
                        reject("No order status found.");
                    }
                })
                .catch(error => reject(error))
        })
    };

    getOrderedProductCount(identifier) {
        const {config, query} = this.state;
        const {getOrderedProductCount} = query;
        const {data} = getOrderedProductCount;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getOrderedProductCount, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(response => {
                    const {value} = response;
                    if (value) {
                        resolve(value);
                    } else {
                        reject("No products in order.");
                    }
                })
                .catch(error => reject(error))
        })
    }

    getOrderName(identifier) {
        const {config, query} = this.state;
        const {getOrderName} = query;
        const {data} = getOrderName;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getOrderName, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(response => {
                    const {listName} = response;
                    // if (listName) {
                    resolve(listName);
                    // } else {
                    //     reject("No list name");
                    // }
                })
                .catch(error => reject(error))
        })
    }

    setErrorMessage = (errorMessage) => {
        this.setState({
            historyList: [],
            errorMessage
        });
    };

    setDataResponse = (historyList) => {
        this.setState({
            historyList,
            errorMessage: ""
        })
    };

    render() {
        const {historyList, errorMessage} = this.state;
        return (
            <div className="row">
                <div className="col-md-12 mt-5">
                    {/** Show table if order history exists*/}
                    {
                        historyList.length
                            ? <OrderHistoryTable historyList={historyList}/>
                            : null
                    }
                    {/** Show error if error exists*/}
                    {/*{*/}
                        {/*errorMessage*/}
                            {/*? errorMessage*/}
                            {/*: null*/}
                    {/*}*/}
                </div>
            </div>
        )
    }
}

