export function showModal(id) {
    // this.setState({
    //     loginModalVisible: true
    // })
    const element = document.getElementById(id);
    element.style.display = "block";
}

export function hideModal(id) {
    // this.setState({
    //     loginModalVisible: false
    // })
    const element = document.getElementById(id);
    element.style.display = "none";

}

export function clearList() {
    this.setState({
        list: [],
        subtotal: 0,
        saving: 0
    }, () => {
        setShoppingList([]);
        setSubtotal(0);
        hideModal(IDENTIFIERS.CLEAR_MODAL)
    });
    // localStorage.setItem(LOCAL_CONSTANTS.SUB_TOTAL, 0);
    // localStorage.setItem(LOCAL_CONSTANTS.SHOPPING_LIST, '[]');
    // hideModal(IDENTIFIERS.CLEAR_MODAL);
    // window.location.reload();
}
;


export function composeOffer(offer) {
    // const {ofCollection, freeCollection} = this.state;
    const {name, offerType, offerDiscount, discountAmount, minPurchaseAmount, minPurchaseQuantity, ofCollection, freeCollection} = offer;
    switch (offerType) {
        case OFFER_TYPES.PERCENTAGE : {
            return (offerDiscount + "% OFF");
            break;
        }
        case OFFER_TYPES.FIXED_AMOUNT : {
            return (PRICE_CURRENCY + discountAmount + " OFF on Checkout");
            break;
        }
        case OFFER_TYPES.BUYX_GETY : {
            if (ofCollection && freeCollection) {
                return ("Buy " + ofCollection.collectionName + " Get " + freeCollection.collectionName + " Free");
            }
            break;
        }
        case OFFER_TYPES.FREE_DELIVERY : {
            return ("Free Delivery");
            break;
        }

    }
}


