import axios from "axios";
import {getAuthCode, logout} from "../helpers/Local_Utilities";

export function requestList(request, config) {
    return new Promise((resolve, reject) => {
        const {C_QUERY_END_POINT} = config;
        const formdata = new FormData();
        formdata.append("data", JSON.stringify(request));
        const authCode = getAuthCode();
        if (authCode) {
            axios({
                method: 'POST',
                url: C_QUERY_END_POINT,
                headers: {
                    "accessToken": authCode,
                    "Content-Type": "multipart/form-data"
                },
                data: formdata
            }).then(response => {
                const {data} = response;
                if (response.status === 200 && data) {
                    const {itemListElement, messageId} = data;
                    if (messageId !== "tokenExpired") {
                        if (itemListElement && itemListElement.length) {
                            resolve(itemListElement);
                        } else {
                            resolve([]);
                        }
                    } else {
                        logout();
                        window.location.reload();
                    }
                }
            })
                .catch(error => {
                    console.log(error);
                    reject("Error in returning list")
                });
        } else {
            axios.post(C_QUERY_END_POINT, formdata)
                .then(response => {
                    const {data} = response;
                    if (response.status === 200 && data) {
                        const {itemListElement} = data;
                        if (itemListElement && itemListElement.length) {
                            resolve(itemListElement);
                        } else {
                            resolve([]);
                        }
                    }
                })
                .catch(error => {
                    console.log(error);
                    reject("Error in returning list")
                });
        }
    })
}

export function requestValue(request, config) {
    return new Promise((resolve, reject) => {
        const {C_QUERY_END_POINT} = config;
        const formdata = new FormData();
        formdata.append("data", JSON.stringify(request));
        const authCode = getAuthCode();
        if (authCode) {
            axios({
                method: 'POST',
                url: C_QUERY_END_POINT,
                headers: {
                    "accessToken": authCode,
                    "Content-Type": "multipart/form-data"
                },
                data: formdata
            }).then(response => {
                // console.log("RESPONSE VALUE ", response);
                const {data, messageId} = response;
                if (messageId !== "tokenExpired") {
                    if (response.status === 200 && data) {
                        resolve(data);
                    } else {
                        reject("No value returned");
                    }
                } else {
                    console.log("False");
                    logout();
                    window.location.reload();
                }
            })
                .catch(error => {
                    console.log(error);
                    reject("Error in returning value")
                });
        } else {
            axios.post(C_QUERY_END_POINT, formdata)
                .then(response => {
                    // console.log("RESPONSE VALUE ", response);
                    const {data} = response;
                    if (response.status === 200 && data) {
                        resolve(data);
                    } else {
                        reject("No value returned");
                    }
                })
                .catch(error => {
                    console.log(error);
                    reject("Error in returning value")
                });
        }
    })
}

export function requestPaymentStatus(request, config) {
    return new Promise((resolve, reject) => {
        const {C_PAYMENT_END_POINT} = config;
        const formdata = new FormData();
        formdata.append("data", JSON.stringify(request));
        axios.post(C_PAYMENT_END_POINT, formdata)
            .then(response => {
                // console.log("RESPONSE VALUE ", response);
                const {data} = response;
                if (response.status === 200 && data) {
                    resolve(data);
                } else {
                    reject("No value returned");
                }
            })
            .catch(error => {
                console.log(error);
                reject("Error in returning value")
            });
    })
}

export function downloadFile(config, filePath) {
    const {C_DOWNLOAD_END_POINT} = config;
    const authCode = getAuthCode();
    if (authCode) {
        return axios({
            method: 'POST',
            url: C_DOWNLOAD_END_POINT,
            params: {data: filePath},
            headers: {"accessToken": authCode},
            responseType: 'blob'
        }).then(response => {
            return window.URL.createObjectURL(new Blob([response.data]));
        }).catch(error => {
            console.log(error);
            return "";
        });
    } else {
        return axios({
            method: 'POST',
            url: C_DOWNLOAD_END_POINT,
            params: {data: filePath},
            responseType: 'blob'
        }).then(response => {
            return window.URL.createObjectURL(new Blob([response.data]));
        }).catch(error => {
            console.log(error);
            return "";
        });
    }
}


