import React, {Component} from "react";
import {getUserName, LOCAL_CONSTANTS} from "../helpers/Constants";
import {downloadFile, requestList, requestValue} from "../helpers/request_helpers";
import {
    generateUserAddressData, generateUserCityData, generateUserCoordinatesData, generateUserUpdateData,
    getUserImageFileData, mutateFile,
    mutateValue, mutateValueWithToken
} from "../helpers/mutation_helpers";
import {UpdatePassword} from "./UpdatePassword";

export class UpdateCustomer extends Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            customer: {
                givenName: "",
                familyName: "",
                email: "",
                telephone: "",
            },
            hasAddress: {
                streetAddress: "",
                addressLocality: "",
                addressRegion: "",
            },
            addressCity: {
                name: "",
            },
            addressCoordinates: {
                latitude: 0,
                longitude: 0
            },
            mainImage: undefined,
            browserUrl: "",
            loading: false,
            errorMessage: "",
        };
        this.handleMainImageChange = this.handleMainImageChange.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentWillReceiveProps(nextProps) {
        const {customer} = nextProps;
        if (customer) {
            this.setState({
                customer
            }, () => {
                const {customer} = this.state;
                const {identifier} = customer;
                this.getCustomerMainImage(identifier);
                this.getCustomerAddress(identifier);
                this.getCustomerCity(identifier);
            })
        }
    }

    componentWillUnmount() {
        this.setDefaults();
    }

    setDefaults = () => {
        this.setState({
            customer: {
                givenName: "",
                familyName: "",
                email: "",
                telephone: "",
            },
            hasAddress: {
                streetAddress: "",
                addressLocality: "",
                addressRegion: "",
            },
            addressCity: {
                name: "",
            },
            addressCoordinates: {
                latitude: 0,
                longitude: 0
            },
            mainImage: undefined,
            browserUrl: "",
        })
    }
    getCustomerAddress = (identifier) => {
        const {config, query} = this.state;
        const {getUserAddress} = query;
        const {data} = getUserAddress;
        requestValue(Object.assign({}, getUserAddress, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(hasAddress => {
                this.setState({
                    hasAddress
                })
            })
            .catch(error => console.log(error))
    };

    getCustomerCity = (identifier) => {
        const {config, query, userName} = this.state;
        const {getUserCity} = query;
        const {data} = getUserCity;
        requestValue(Object.assign({}, getUserCity, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(addressCity => {
                this.setState({
                    addressCity
                })
            })
            .catch(error => console.log(error))
    };

    getCustomerCoordinates = (identifier) => {
        const {config, query, userName} = this.state;
        const {getUserGeoLocation} = query;
        const {data} = getUserGeoLocation;
        requestValue(Object.assign({}, getUserGeoLocation, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(getUserGeoLocation => {
                this.setState({
                    getUserGeoLocation
                })
            })
            .catch(error => console.log(error))
    };

    handleSubmit() {
        const {closeModal} = this.props;
        const {mainImage, customer, hasAddress, addressCity, addressCoordinates} = this.state;
        const {identifier, familyName, givenName, email, telephone,} = customer;
        const {streetAddress, addressLocality, addressRegion} = hasAddress;
        const {name} = addressCity;
        const {latitude, longitude} = addressCoordinates;
        const {config, message} = this.state;

        if (givenName || familyName || telephone || email) {
            const mutationData = generateUserUpdateData({identifier, givenName, familyName, email});
            mutateValueWithToken(mutationData, config)
                .then(response => {
                    console.log("Customer : ", response);
                })
                .catch(error => console.log(error))
        }

        if (streetAddress || addressLocality || addressRegion) {
            const mutationData = generateUserAddressData(hasAddress, identifier);
            mutateValueWithToken(mutationData, config)
                .then(response => console.log("Address : ", response))
                .catch(error => console.log(error))

        }

        if (name) {
            const mutationData = generateUserCityData(addressCity, identifier);
            mutateValueWithToken(mutationData, config)
                .then(response => console.log("City : ", response))
                .catch(error => console.log(error))
        }

        if (latitude || longitude) {
            const mutationData = generateUserCoordinatesData(addressCoordinates, identifier);
            mutateValueWithToken(mutationData, config)
                .then(response => console.log("Coordinates ", response))
                .catch(error => console.log(error))
        }

        if (mainImage) {
            const mutationData = getUserImageFileData(mainImage, identifier);
            console.log(mutationData);
            mutateFile(mutationData, config)
                .then(response => console.log("Image : ", response))
                .catch(error => console.log(error))

        }

        setTimeout(() => {
            closeModal();
        }, 1000);
    }

    handleMainImageChange = () => {
        const file = document.getElementById("mainImage").files[0];
        console.log(file);
        if (file) {
            this.setState({
                mainImage: file,
                browserUrl: window.URL.createObjectURL(file),
            }, () => console.log(this.state.mainImage, this.state.browserUrl))
        }
    };

    getCustomerMainImage(identifier) {
        const {config, query} = this.state;
        const {getMainImageUser} = query;
        const {data} = getMainImageUser;
        return new Promise((resolve, reject) => {

            requestValue(Object.assign({}, getMainImageUser, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(async mainImage => {
                    const {contentUrl} = mainImage;
                    {/** Download*/
                    }
                    if (contentUrl) {
                        await downloadFile(config, contentUrl)
                            .then(url => {
                                this.setState({
                                    browserUrl: url
                                })
                            })
                            .catch(error => console.log(" Image Download => " + error))
                    } else {
                        resolve("Done")
                    }
                })
                .catch(error => {
                    console.log(" Image Get => " + error);
                    reject(error);
                })
        })
    }

    changeCustomerValue(fieldName, value) {
        const {customer} = this.state;
        this.setState({
            customer: {
                ...customer,
                [fieldName]: value
            }
        })
    }

    changeAddressValue(fieldName, value) {
        const {hasAddress} = this.state;
        this.setState({
            hasAddress: {
                ...hasAddress,
                [fieldName]: value
            }
        })
    }

    changeCityValue(fieldName, value) {
        const {addressCity} = this.state;
        this.setState({
            addressCity: {
                ...addressCity,
                [fieldName]: value
            }
        })
    }

    changeCoordinatesValue(fieldName, value) {
        const {addressCoordinates} = this.state;
        this.setState({
            addressCoordinates: {
                ...addressCoordinates,
                [fieldName]: value
            }
        })
    }

    showPasswordModal() {
        // this.setState({
        //     passwordModalVisible: true
        // })
        const element = document.getElementById("passwordModal");
        element.style.display = "block";
    }

    hidePasswordModal() {
        // this.setState({
        //     passwordModalVisible: false
        // })
        const element = document.getElementById("passwordModal");
        element.style.display = "none";

    }


    render() {
        const {browserUrl, customer, hasAddress, addressCity, addressCoordinates} = this.state;
        const {familyName, givenName, email, telephone,} = customer;
        const {streetAddress, addressLocality, addressRegion} = hasAddress;
        const {name} = addressCity;
        const {latitude, longitude} = addressCoordinates;
        return (
            <form
                onSubmit={(event) => {
                    event.preventDefault();
                    this.handleSubmit();
                }}
                style={{
                    padding: "20px"
                }}
            >

                <div className="float-right">
                    <a href="javascript:;" onClick={this.showPasswordModal}>Change Password</a>
                </div>
                <label className="title caps">Details</label>
                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor="givenName">First Name</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="givenName"
                               value={givenName}
                               onChange={(event) => this.changeCustomerValue("givenName", event.target.value)}
                        /></div>
                    <div className="form-group col-md-6">
                        <label htmlFor="givenName">Last Name</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="familyName"
                               value={familyName}
                               onChange={(event) => this.changeCustomerValue("familyName", event.target.value)}
                        /></div>
                </div>
                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor="email">Email</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="email"
                               placeholder={email}
                               value={email}
                               onChange={(event) => this.changeCustomerValue("email", event.target.value)}

                        /></div>
                    <div className="form-group col-md-6">
                        <label htmlFor="telephone">Telephone</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="telephone"
                               readOnly={true}
                               value={telephone}

                        /></div>
                </div>
                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor="streetAddress">Street</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="streetAddress"
                            // readOnly={true}
                               value={streetAddress}
                               onChange={(event) => this.changeAddressValue("streetAddress", event.target.value)}

                        />
                    </div>
                    <div className="form-group col-md-6">
                        <label htmlFor="addressLocality">Address Locality</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="addressLocality"
                               value={addressLocality}
                               onChange={(event) => this.changeAddressValue("addressLocality", event.target.value)}

                        />
                    </div>
                </div>

                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor="name">District</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="name"
                            // readOnly={true}
                               value={name}
                               onChange={(event) => this.changeCityValue("name", event.target.value)}
                        />

                    </div>
                    <div className="form-group col-md-6">
                        <label htmlFor="addressRegion">Region</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="addressRegion"
                            // readOnly={true}
                               value={addressRegion}
                               onChange={(event) => this.changeAddressValue("addressRegion", event.target.value)}

                        />

                    </div>
                </div>

                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor="latitude">Latitude</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="latitude"
                            // readOnly={true}
                               value={latitude}
                               onChange={(event) => this.changeCoordinatesValue("latitude", event.target.value)}
                        />


                    </div>
                    <div className="form-group col-md-6">
                        <label htmlFor="longitude">Longitude</label>
                        <input type="text" className="form-control p-3" aria-describedby="name"
                               id="longitude"
                            // readOnly={true}
                               value={longitude}
                               onChange={(event) => this.changeCoordinatesValue("longitude", event.target.value)}

                        />
                    </div>
                </div>

                <label className="title caps">Profile Picture</label>

                <div className="form-group row">
                    <div className="col-md-4"/>
                    <input type="file" aria-describedby="mainImage"
                           id="mainImage"
                           className="col-md-4"
                           style={{lineHeight: 1.8}}
                           onChange={this.handleMainImageChange}
                    />
                    <div className="col-md-4"/>
                </div>
                {
                    browserUrl
                        ? <div className="form-group row">
                            <div className="col-md-4"/>
                            <div className="col-md-4">
                                <img src={browserUrl} alt="Broken or image not found" width="150"/>
                            </div>
                            <div className="col-md-4"/>
                        </div>
                        : null
                }
                <div className="row">
                    <div className="col-md-4"/>
                    <div className="col-md-4">
                        <button type="submit"
                                className="btn btn-primary btn-md btn-block sidebar__btn-place-order"
                                data-toggle="modal" data-target="#loginModal">
                            <span><i className="fas fa-save"/></span>
                            &nbsp;&nbsp;&nbsp;Save Changes
                        </button>
                    </div>
                    <div className="col-md-4"/>

                </div>
                <div className="modal fade show" id="passwordModal" tabIndex="-1" role="dialog"
                     aria-labelledby="passwordModal" style={{display: "none", overflowY: "auto"}}>
                    <div className="modal-dialog modal-lg" role="document">
                        <div className="modal-content">
                            <div className="modal-header modal-header--close-only">
                                <button type="button"
                                        onClick={this.hidePasswordModal} className="close" data-dismiss="modal"
                                        aria-label="Close">
                                    <span aria-hidden="true"></span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <p className="text-center">Update Password <br/>
                                </p>

                                <UpdatePassword closeModal={this.hidePasswordModal}/>
                            </div>
                        </div>
                    </div>
                </div>
            </form>
        );
    }
}


