import React from 'react';
import FeaturedBanner from "../Banners/FeaturedBanner";
import {ProductPreview} from "../Product/ProductPreview";
import {DirectionOfUse, } from "../StaticContent/staticContents";
import {productPreviewList} from "../sampleData/sampleData";
import {ListPreview} from "../ListsPreview/ListPreview";
import MenuBar from "../Menubar/Menubar";
import {requestList, requestValue} from "../helpers/request_helpers";
import {LoadingComponent} from "../StaticContent/staticContents";
import {LeftImageBanner, RightImageBanner} from "../Banners/Banner";
import {Link} from "react-router-dom";
import {setTopMargin} from "../helpers/Utilities";
import Slider from "react-slick";
import {setProductCardWidth} from "../helpers/Utilities";
import {LOCAL_CONSTANTS,PRODUCT_ORIGINS} from "../helpers/Constants";

const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 3
};

const LATEST_PRODUCTS_LINK = "/search/LatestProducts/Latest Products/0";
const TOP_PRODUCTS_LINK = "/search/TopProducts/Top Products/0";
const FEATURED_PRODUCTS_LINK = "/search/FeaturedProducts/Featured Products/0";

export class LandingPage extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            query: {},
            config: {},
            latestProductPagination: {start: 0, end: 5},
            topProductPagination: {start: 0, end: 5},
            featuredProductPagination: {start: 0, end: 5},
            latestProducts: [],
            topProducts: [],
            featuredProducts: [],
            popularList: [],
            recommendedList: [],
            featuredBannerList: [],
            bannerCategory: undefined,
            bannerProduct: undefined,
            bannerCollection: undefined,
            latestProductsLoading: false,
            topProductsLoading: false,
            featuredProductsLoading: false,
            loadMoreTopVisible: true,
            loadMoreLatestVisible: true,
            loadMoreFeaturedVisible: true,
            topMargin: undefined,
            productCardWidth: ""
        };
        this.setLatestProducts = this.setLatestProducts.bind(this);
        this.setTopProducts = this.setTopProducts.bind(this);
        this.setFeaturedProducts = this.setFeaturedProducts.bind(this);
        this.setPopularList = this.setPopularList.bind(this);
        this.setRecommendedList = this.setRecommendedList.bind(this);
        this.setBannerCategory = this.setBannerCategory.bind(this);
        this.getLatestProducts = this.getLatestProducts.bind(this);
        this.getTopProducts = this.getTopProducts.bind(this);
        this.getFeaturedProducts = this.getFeaturedProducts.bind(this);
        this.setBannerProduct = this.setBannerProduct.bind(this);
        this.setBannerCollection = this.setBannerCollection.bind(this);
        this.setTopMargin = setTopMargin.bind(this);
        this.setProductCardWidth = setProductCardWidth.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            }, () => {
                this.setPopularList();
                this.setRecommendedList();
                this.setTopProducts();
                this.setLatestProducts();
                this.setFeaturedProducts();
                this.setBannerCategory();
                // this.setBannerProduct();
                this.setBannerCollection();
            })
        }
    }


    componentDidMount() {
        this.setTopMargin();
    }


    setBannerCategory() {
        const {config, query} = this.state;
        const {getBannerCategory} = query;
        requestValue(getBannerCategory, config)
            .then(data => {
                this.setState({
                    bannerCategory: data
                })
            })
            .catch(error => console.log(error));
    }

    // Not needed now
    setBannerProduct() {
        const {config, query} = this.state;
        const {getBannerProduct} = query;
        requestValue(getBannerProduct, config)
            .then(data => {
                this.setState({
                    bannerProduct: data
                })
            })
            .catch(error => console.log(error));
    }

    setBannerCollection() {
        const {config, query} = this.state;
        const {getBannerCollection} = query;
        requestValue(getBannerCollection, config)
            .then(data => {
                this.setState({
                    bannerCollection: data
                })
            })
            .catch(error => console.log(error));
    }

    setFeaturedProducts(countsize) {
        const {featuredProductPagination} = this.state;
        const {start, end} = featuredProductPagination;

        if (countsize) {
            this.setState({
                featuredProductPagination: {
                    start: start + countsize,
                    end: end + countsize
                }
            }, () => {
                this.getFeaturedProducts();
            })
        } else {
            this.getFeaturedProducts();
        }

    }

    getFeaturedProducts() {
        const {config, query, featuredProducts, featuredProductPagination} = this.state;
        const {start, end} = featuredProductPagination;
        const {listFeaturedProducts} = query;
        const {pageLimit} = listFeaturedProducts;
        this.setState({
            featuredProductsLoading: true
        });
        requestList(Object.assign({}, listFeaturedProducts, {
            pageLimit: {
                ...pageLimit,
                start,
                end
            }
        }), config)
            .then(data => {
                if (data.length) {
                    this.setState({
                        featuredProducts: [...featuredProducts, ...data.filter(product =>
                            featuredProducts.findIndex(item => item["productID"] === product["productID"]) === -1)],
                        featuredProductsLoading: false

                    })
                } else {
                    this.setState({
                        loadMoreFeaturedVisible: false,
                        featuredProductsLoading: false
                    })
                }
            })
            .catch(error => {
                console.log(error);
                this.setState({
                    featuredProductsLoading: false,
                    loadMoreFeaturedVisible: false
                });
            });
    }

    setTopProducts(countsize) {
        const {topProductPagination} = this.state;
        const {start, end} = topProductPagination;

        if (countsize) {
            this.setState({
                topProductPagination: {
                    start: start + countsize,
                    end: end + countsize
                }
            }, () => {
                this.getTopProducts();
            })
        } else {
            this.getTopProducts();
        }

    }

    getTopProducts() {
        const {config, query, topProducts, topProductPagination} = this.state;
        const {start, end} = topProductPagination;
        const {listTopProducts} = query;
        const {pageLimit} = listTopProducts;
        this.setState({
            topProductsLoading: true
        });
        requestList(Object.assign({}, listTopProducts, {
            pageLimit: {
                ...pageLimit,
                start,
                end
            }
        }), config)
            .then(data => {
                if (data.length) {
                    this.setState({
                        topProducts: [...topProducts, ...data.filter(product =>
                            topProducts.findIndex(item => item["productID"] === product["productID"]) === -1)],
                        topProductsLoading: false

                    })
                } else {
                    this.setState({
                        topProductsLoading: false,
                        loadMoreTopVisible: false
                    })
                }
            })
            .catch(error => {
                console.log(error);
                this.setState({
                    topProductsLoading: false,
                    loadMoreTopVisible: false
                });
            });
    }

    setLatestProducts(countSize) {
        const {latestProductPagination} = this.state;
        const {start, end} = latestProductPagination;
        if (countSize) {
            this.setState({
                latestProductPagination: {
                    start: start + countSize,
                    end: end + countSize
                }
            }, () => {
                this.getLatestProducts();
            })
        } else {
            this.getLatestProducts();
        }
    }

    getLatestProducts() {
        const {latestProductPagination, config, query, latestProducts} = this.state;
        const {start, end} = latestProductPagination;
        const {listLatestProducts} = query;
        const {pageLimit} = listLatestProducts;
        this.setState({
            latestProductsLoading: true
        });
        requestList(Object.assign({}, listLatestProducts, {
            pageLimit: {
                ...pageLimit,
                start,
                end
            }
        }), config)
            .then(data => {
                if (data.length) {
                    this.setState({
                        latestProducts: [...latestProducts, ...data.filter(product =>
                            latestProducts.findIndex(item => item["productID"] === product["productID"]) === -1
                        )],
                        latestProductsLoading: false
                    }, () => this.setProductCardWidth())
                } else {
                    this.setState({
                        latestProductsLoading: false,
                        loadMoreLatestVisible: false
                    })
                }
            })
            .catch(error => {
                console.log(error);
                this.setState({
                    latestProductsLoading: false,
                    loadMoreLatestVisible: false
                });
            });
    }

    setPopularList() {
        const {config, query} = this.state;
        const {getPopularList} = query;

        requestList(getPopularList, config)
            .then(data => {
                this.setState({
                    popularList: data
                }, () => {
                    const element = document.getElementById("popular-list-btn");
                    if (element) {
                        element.click();
                    }
                })
            })
            .catch(error => console.log(error));
    }

    setRecommendedList() {
        const {config, query} = this.state;
        const {getRecommendedList} = query;

        requestList(getRecommendedList, config)
            .then(data => {
                this.setState({
                    recommendedList: data
                })
            })
            .catch(error => console.log(error));
    }

    setActive(event, listName) {

        const tabcontent = document.getElementsByClassName("list-content");
        for (let i = 0; i < tabcontent.length; i++) {
            tabcontent[i].style.display = "none";
        }

        const tablinks = document.getElementsByClassName("nav-link");
        for (let i = 0; i < tablinks.length; i++) {
            tablinks[i].className = tablinks[i].className.replace(" active", "");
        }
        document.getElementById(listName).style.display = "flex";
        event.currentTarget.className += " active";
    }

    render() {
        const {showShoppingList, handleUndo, handleSearchConfirm} = this.props;
        const {
            config, query,
            latestProducts, topProducts, featuredProducts,
            bannerCategory, bannerProduct, bannerCollection,
            popularList, recommendedList,
            latestProductsLoading, topProductsLoading, featuredProductsLoading,
            loadMoreFeaturedVisible, loadMoreLatestVisible, loadMoreTopVisible,
            topMargin, productCardWidth
        } = this.state;

        return (
            <div className="all-page">
                <div style={{flex: "auto", marginTop: `${topMargin}px`}}/>

                {/** Featured Banner*/}
                <div style={{flex: "auto"}}>
                    <FeaturedBanner showShoppingList={showShoppingList}/>
                </div>
                {/**Static bar for instructions on how to order*/}
                <div>
                    <DirectionOfUse showShoppingList={showShoppingList}/>
                </div>

                {/** Featured Products*/
                }
                <div className="product-list section section--padded-2">
                    <div className="container">
                        {/** Section Header*/}
                        {
                            !featuredProductsLoading
                                ? <div>
                                    {
                                        featuredProducts.length
                                            ? <div>
                                                <div className="row">
                                                    <div className="col">
                                                        <h2 className="section__header"><span>Featured Products</span>
                                                            <small><Link to={FEATURED_PRODUCTS_LINK}
                                                                         title="View all Featured Products">
                                                                View All</Link>
                                                            </small>
                                                        </h2>
                                                    </div>
                                                </div>

                                                {/** Product preview list*/}
                                                {/** Map the list of product to <ProductPreview/> */}
                                                <div className="row scroll-x">
                                                    {
                                                        featuredProducts.map((product, index) => {
                                                            // console.log("reloaded ", product);
                                                            return <ProductPreview
                                                                showShoppingList={showShoppingList}
                                                                product={product}
                                                                handleUndo={handleUndo}
                                                                classname="col-xl-2 col-lg-4 col-md-6"
                                                                origin={PRODUCT_ORIGINS.FEATURED}
                                                            />
                                                        })
                                                    }
                                                    <ViewAll link={FEATURED_PRODUCTS_LINK} width={productCardWidth + "px"}/>
                                                </div>
                                            </div>
                                            : null
                                    }
                                    {/** Load More button*/}
                                    {/*{featuredProducts.length*/}

                                    {/*? <div*/}
                                    {/*style={{*/}
                                    {/*display: "grid",*/}
                                    {/*gridTemplateColumns: "1fr 1fr 1fr",*/}
                                    {/*gridTemplateRows: "auto",*/}
                                    {/*justifyItems: "center",*/}
                                    {/*margin: "10px 15px"*/}
                                    {/*}}*/}
                                    {/*>*/}
                                    {/*<div/>*/}
                                    {/*{*/}
                                    {/*loadMoreFeaturedVisible*/}
                                    {/*? <button*/}
                                    {/*className="btn btn-primary  btn-block"*/}
                                    {/*title="Click to load more latest products"*/}
                                    {/*onClick={(event) => {*/}
                                    {/*event.preventDefault();*/}
                                    {/*this.setFeaturedProducts(12);*/}
                                    {/*}}*/}
                                    {/*style={{*/}
                                    {/*boxShadow: 'rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(0, 0, 0, 0.1) 0px 6px 20px 0px;'*/}
                                    {/*}}>*/}
                                    {/*Load More*/}
                                    {/*</button>*/}
                                    {/*: null*/}
                                    {/*}*/}
                                    {/*</div>*/}
                                    {/*: null}*/}
                                </div>
                                : <LoadingComponent/>
                        }

                    </div>

                </div>

                {/** Category or Collection banners*/
                }
                {
                    bannerCategory && bannerCategory.categoryName
                        ? <RightImageBanner showShoppingList={showShoppingList}
                                            category={bannerCategory}
                        />
                        : null
                }

                {/** Top Products*/
                }
                <div className="product-list section section--padded-2">
                    <div className="container">
                        {/** Section Header*/}
                        {
                            !topProductsLoading
                                ? <div>
                                    {
                                        topProducts.length
                                            ? <div>
                                                <div className="row ">
                                                    <div className="col">
                                                        <h2 className="section__header"><span>Top Products</span>
                                                            <small><Link to={TOP_PRODUCTS_LINK}
                                                                         title="View all Top Products"
                                                            >View All</Link>
                                                            </small>
                                                        </h2>
                                                    </div>
                                                </div>

                                                {/** Product preview list*/}
                                                {/** Map the list of product to <ProductPreview/> */}
                                                <div className="row scroll-x">
                                                    {
                                                        topProducts.map((product, index) =>
                                                            <ProductPreview
                                                                showShoppingList={showShoppingList}
                                                                product={product}
                                                                handleUndo={handleUndo}
                                                                classname="col-xl-2 col-lg-4 col-md-6"
                                                                origin={PRODUCT_ORIGINS.TOP}
                                                            />
                                                        )
                                                    }
                                                    <ViewAll link={TOP_PRODUCTS_LINK} width={productCardWidth + "px"}/>
                                                </div>
                                            </div>
                                            : null
                                    }
                                    {/** Load More button*/}
                                    {/*{topProducts.length*/}
                                    {/*? <div style={{*/}
                                    {/*display: "grid",*/}
                                    {/*gridTemplateColumns: "1fr 1fr 1fr",*/}
                                    {/*gridTemplateRows: "auto",*/}
                                    {/*justifyItems: "center",*/}
                                    {/*margin: "10px 15px"*/}
                                    {/*}}>*/}
                                    {/*<div/>*/}
                                    {/*{*/}
                                    {/*loadMoreTopVisible*/}
                                    {/*? <button title="Click to load more top products"*/}
                                    {/*className="btn btn-primary  btn-block"*/}
                                    {/*onClick={(event) => {*/}
                                    {/*event.preventDefault();*/}
                                    {/*this.setTopProducts(12);*/}
                                    {/*}}*/}
                                    {/*style={{boxShadow: 'rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(0, 0, 0, 0.1) 0px 6px 20px 0px;'}}>*/}
                                    {/*Load More*/}
                                    {/*</button>*/}
                                    {/*: null*/}
                                    {/*}*/}
                                    {/*</div>*/}
                                    {/*: null}*/}
                                </div>
                                : <LoadingComponent/>
                        }
                    </div>

                </div>

                {/** Change banner product to collection later*/}
                {/*{*/}
                {/*bannerProduct && bannerProduct.productName*/}
                {/*? <LeftImageBanner showShoppingList={showShoppingList}*/}
                {/*product={bannerProduct}*/}
                {/*/>*/}
                {/*: null*/}
                {/*}*/}

                {
                    bannerCollection && bannerCollection.collectionName
                        ? <LeftImageBanner showShoppingList={showShoppingList}
                                           collection={bannerCollection}
                        />
                        : null
                }


                {/** Latest Products*/
                }
                <div className="product-list section section--padded-2">
                    <div className="container">
                        {/** Section Header*/}
                        {
                            !latestProductsLoading
                                ? <div>
                                    {
                                        latestProducts.length
                                            ? <div>
                                                <div className="row">
                                                    <div className="col">
                                                        <h2 className="section__header"><span>Latest Products</span>
                                                            <small><Link to={LATEST_PRODUCTS_LINK}
                                                                         title="View all Latest Products">
                                                                View All</Link>
                                                            </small>
                                                        </h2>
                                                    </div>
                                                </div>
                                                {/** Product preview list*/}
                                                {/** Map the list of product to <ProductPreview/> */}
                                                <div className="row scroll-x">
                                                    {
                                                        latestProducts.map((product, index) => {
                                                                return <ProductPreview
                                                                    showShoppingList={showShoppingList}
                                                                    product={product}
                                                                    handleUndo={handleUndo}
                                                                    classname="col-xl-2 col-lg-4 col-md-6"
                                                                    origin={PRODUCT_ORIGINS.LATEST}
                                                                />
                                                            }
                                                        )
                                                    }
                                                    <ViewAll link={LATEST_PRODUCTS_LINK} width={productCardWidth + "px"}/>
                                                </div>
                                            </div>
                                            : null
                                    }


                                    {/** Load More button*/}
                                    {/*{latestProducts.length*/}
                                    {/*? <div*/}
                                    {/*style={{*/}
                                    {/*display: "grid",*/}
                                    {/*gridTemplateColumns: "1fr 1fr 1fr",*/}
                                    {/*gridTemplateRows: "auto",*/}
                                    {/*justifyItems: "center",*/}
                                    {/*margin: "10px 15px"*/}
                                    {/*}}*/}
                                    {/*>*/}
                                    {/*<div/>*/}
                                    {/*{*/}
                                    {/*loadMoreLatestVisible*/}
                                    {/*? <button*/}
                                    {/*className="btn btn-primary btn-md  btn-block"*/}
                                    {/*title="Click to load more latest products"*/}
                                    {/*onClick={(event) => {*/}
                                    {/*event.preventDefault();*/}
                                    {/*this.setLatestProducts(12);*/}
                                    {/*}}*/}
                                    {/*style={{*/}
                                    {/*boxShadow: 'rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(0, 0, 0, 0.1) 0px 6px 20px 0px;'*/}
                                    {/*}}>*/}
                                    {/*Load More*/}
                                    {/*</button>*/}
                                    {/*: null*/}
                                    {/*}*/}
                                    {/*</div>*/}
                                    {/*: null}*/}
                                </div>
                                : <LoadingComponent/>
                        }

                    </div>

                </div>

                {/** Popular lists and recommended lists*/}
                {
                    popularList.length || recommendedList.length
                        ? <section className="fontpage-list section">

                            <div className="container">
                                {/** Section headers with menus*/}

                                <ul className="nav nav-pills mb-4">
                                    {
                                        popularList && popularList.length
                                            ? <li className="nav-item">
                                                <a className="nav-link active" id="popular-list-btn"
                                                   title="Show Popular Lists"
                                                   onClick={(event) =>
                                                       this.setActive(event, "popular-lists__tabs")}
                                                >
                                                    Popular Lists
                                                </a>
                                            </li>
                                            : null
                                    }
                                    {
                                        recommendedList && recommendedList.length
                                            ? <li className="nav-item">
                                                <a className="nav-link" id="recommended-list-btn"
                                                   onClick={(event) => this.setActive(event, "recommended-lists__tabs")}
                                                   title="Show Recommended Lists"
                                                >
                                                    Recommended Lists
                                                </a>
                                            </li>
                                            : null
                                    }
                                </ul>


                                {/** Section body with list previews list-content row scroll-x */}
                                <div className="tabs " id="list__tabs-container">
                                    {
                                        popularList && popularList.length
                                            ? <ul id="popular-lists__tabs" className="nav list-content row" role="tabList">
                                                {
                                                    popularList.map((list, index) => {
                                                        return <ListPreview list={list} index={index}
                                                                            handleSearchConfirm={handleSearchConfirm}/>
                                                    })
                                                }
                                            </ul>
                                            : null
                                    }

                                    {
                                        recommendedList && recommendedList.length
                                            ? <ul id="recommended-lists__tabs" className="nav list-content row"
                                                  role="tabList">
                                                {
                                                    recommendedList.map(list => {
                                                        return <ListPreview list={list}
                                                                            handleSearchConfirm={handleSearchConfirm}/>
                                                    })
                                                }
                                            </ul>
                                            : null
                                    }
                                </div>
                            </div>
                        </section>
                        : null
                }
            </div>);
    }
}

const ViewAll = (props) => {
    const {link, width} = props;
    return <div className="col-xs-6 col-md-auto col-sm-4 col-6 d-flex flex-flow-column "
                style={{
                    flexShrink: 1,
                    // flexGrow: 0,
                    // width: "50%",
                    width,
                    display: "inline-block"

                }}>
        <div className="product-card">
            <p className="middle-center-aligned"><Link to={link}>View All</Link></p>
        </div>
    </div>
};

const Template = () => {
    return <section>
        <h4>Congratulations, Your order has been placed successfully!</h4>
        <p>
            Your profile credentials are : <br/>
            <b>Username : </b> jyotikatuladhar <br/>
            <b>Password : </b> 001100 <br/>
        </p>
    </section>
};



