import React, {Component} from "react";
import {loginUser} from "../helpers/mutation_helpers";
import {
    hideModal,
} from "../StaticContent/staticContents";
import sha256 from "js-sha256";
import {requestValue} from "../helpers/request_helpers";
import {redirectToPath} from "../helpers/Utilities";
import KiranaLogoColor from "../style/images/kirana_logo_color.svg";
import {login, setDefaultValues, setUserName} from "../helpers/Local_Utilities";
import { IDENTIFIERS, LOCAL_CONSTANTS,} from "../helpers/Constants";

export class LoginPage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            userName: "",
            userPassword: "",
            loading: false,
            errorVisible: false,
            errorMessage: ""
        };
        this.handleSubmit = this.handleSubmit.bind(this);
        this.checkFirstLogin = this.checkFirstLogin.bind(this);
        this.changeFieldValue = this.changeFieldValue.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        const root = localStorage.getItem(LOCAL_CONSTANTS.ROOT);
        if (config && query && message && root) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message),
                root: JSON.parse(root)
            })
        }
    }


    handleSubmit() {
        this.setState({
            // userName: document.getElementById("userName").value,
            // userPassword: document.getElementById("userPassword").value,
            loading: true
        }, () => {
            const {userName, userPassword, root, config, message} = this.state;
            const {rootPath} = root;
            // document.getElementById("userName").value = userName;
            // document.getElementById("userPassword").value = userPassword;
            const {loginSuccess, loginError} = message;
            loginUser({userName, userPassword: sha256.hex(userPassword)}, config)
                .then(response => {
                    const {authenticationCode} = response;
                    login(authenticationCode);
                    setUserName(userName);
                    hideModal(IDENTIFIERS.LOGIN_MODAL);
                    this.setState({
                        loading: false
                    });
                    setDefaultValues();
                    this.checkFirstLogin()
                        .then(response => {
                            const {firstLogin} = response;
                            if (firstLogin) {
                                redirectToPath(rootPath + "/profile");
                            } else {
                                window.location.reload();
                            }
                        })
                        .catch(error => console.log(error));
                })
                .catch(error => {
                    console.log(error);
                    this.setState({
                        errorVisible: true,
                        errorMessage: loginError
                    });
                    // document.getElementById("userPassword-invalid").style.display = "block";
                })
        })
    }

    checkFirstLogin = () => {
        const {config, query, userName} = this.state;
        const {checkFirstLogin} = query;
        const {data} = checkFirstLogin;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, checkFirstLogin, {
                data: {
                    ...data,
                    userName
                }
            }), config)
                .then(response => {
                    resolve(response);
                })
                .catch(error => {
                    reject(error)
                })

        })
    };

    changeFieldValue(fieldName, value) {
        this.setState({
            [fieldName]: value
        })
    }

    render() {
        const {errorMessage, errorVisible} = this.state;
        return (
            <div>
                <div style={{display: "flex", justifyContent: "center", alignItems: "center"}}>
                    <img src={KiranaLogoColor} width="150px"/>
                </div>
                <br/>

                <form
                    onSubmit={(event) => {
                        event.preventDefault();
                        this.handleSubmit();
                    }}
                    style={{
                        padding: "20px"
                    }}
                >
                    <div className="form-group">
                        <div className="input-group mb-3">
                            <div className="input-group-prepend">
                                                    <span className="input-group-text"
                                                          id="login_user_name"><i className="fas fa-user"/></span>
                            </div>
                            <input type="text" className="form-control p-3"
                                   placeholder="Your username"
                                   aria-label="Your username"
                                   required={true}
                                   id="userName"
                                   onChange={(event) => this.changeFieldValue("userName", event.target.value)}
                            />
                            <div className="invalid-feedback" id="userName-invalid">Please provide a user name
                            </div>
                        </div>
                    </div>
                    <div className="form-group">
                        <div className="input-group mb-3">
                            <div className="input-group-prepend">
                                                    <span className="input-group-text"
                                                          id="login_user_password"><i className="fas fa-key"/></span>
                            </div>
                            <input type="password" className="form-control p-3"
                                   placeholder="Your password"
                                   aria-label="Your password"
                                   required={true}
                                   id="userPassword"
                                   onChange={(event) => this.changeFieldValue("userPassword", event.target.value)}
                            />

                        </div>
                    </div>
                    {
                        errorVisible
                            ? <div className="alert alert-danger" role="alert">
                                {errorMessage}
                            </div>
                            : null

                    }
                    <button type="submit"
                            className="btn btn-primary btn-lg btn-block sidebar__btn-place-order"
                            data-toggle="modal" data-target="#loginModal">
                        <span><i className="fas fa-sign-in-alt" style={{fontSize: "15px"}}/></span>
                        &nbsp;Login
                    </button>
                </form>
            </div>
        );
    }
}


