import {CLASSNAMES, LOCAL_CONSTANTS} from "./Constants";

export function setDefaultValues() {
    setCheckoutList([]);
    setShoppingList([]);
    setSubtotal(0);
    setCartcount(0);
}

export function setSearchList(list){
    localStorage.setItem(LOCAL_CONSTANTS.SEARCH_LIST, JSON.stringify(list));
}

export function getSearchList(list){
    return JSON.parse(localStorage.getItem(LOCAL_CONSTANTS.SEARCH_LIST));
}

export function setSaving(saving){
    localStorage.setItem(LOCAL_CONSTANTS.SAVING, saving);
}

export function getSaving(){
    return Number(localStorage.getItem(LOCAL_CONSTANTS.SAVING));
}

export function setCartcount(count) {
    localStorage.setItem(LOCAL_CONSTANTS.CART_COUNT, count);
}

export function getCartCount() {
    return JSON.parse(localStorage.getItem(LOCAL_CONSTANTS.CART_COUNT))
}

export function setAutoSearchStatus(status) {
    localStorage.setItem(LOCAL_CONSTANTS.AUTO_SEARCH, status);
}

export function getAutoSearchStatus() {
    return JSON.parse(localStorage.getItem(LOCAL_CONSTANTS.AUTO_SEARCH))
}

export function setSearchConfirmStatus(status) {
    localStorage.setItem(LOCAL_CONSTANTS.SEARCH_CONFIRM, status);
}

export function getSearchConfirmStatus() {
    return JSON.parse(localStorage.getItem(LOCAL_CONSTANTS.SEARCH_CONFIRM))
}

export function getSubtotal() {
    return Number(JSON.parse(localStorage.getItem(LOCAL_CONSTANTS.SUB_TOTAL)));
}

export function setSubtotal(subtotal) {
    localStorage.setItem(LOCAL_CONSTANTS.SUB_TOTAL, JSON.stringify(subtotal));
}

export function getShoppingList() {
    return JSON.parse(localStorage.getItem(LOCAL_CONSTANTS.SHOPPING_LIST));
}

export function setShoppingList(array) {
    localStorage.setItem(LOCAL_CONSTANTS.SHOPPING_LIST, JSON.stringify(array));
}

export function setCheckoutList(list) {
    localStorage.setItem(LOCAL_CONSTANTS.CHECKOUT_LIST, JSON.stringify(list));
}

export function getCheckoutList() {
    return JSON.parse(localStorage.getItem(LOCAL_CONSTANTS.CHECKOUT_LIST));
}

export function login(authenticationCode) {
    setAuthCode(authenticationCode);
    localStorage.setItem(LOCAL_CONSTANTS.LOGGED_IN, true);
}

export function logout() {
    localStorage.setItem(LOCAL_CONSTANTS.LOGGED_IN, false);
    localStorage.setItem(LOCAL_CONSTANTS.USERNAME, "");
    localStorage.setItem(LOCAL_CONSTANTS.AUTH_CODE, "");
    // window.location.reload();

}

export function getLoginStatus() {
    return JSON.parse(localStorage.getItem(LOCAL_CONSTANTS.LOGGED_IN));
}

export function setUserName(userName) {
    localStorage.setItem(LOCAL_CONSTANTS.USERNAME, userName);
}

export function getUserName() {
    return localStorage.getItem(LOCAL_CONSTANTS.USERNAME);
}

export function setAuthCode(authenticationCode) {
    localStorage.setItem(LOCAL_CONSTANTS.AUTH_CODE, authenticationCode);
}

export function getAuthCode() {
    return localStorage.getItem(LOCAL_CONSTANTS.AUTH_CODE);
}


export function setListName(listName) {
    localStorage.setItem(LOCAL_CONSTANTS.LIST_NAME, listName);
}

export function getListName() {
    return localStorage.getItem(LOCAL_CONSTANTS.LIST_NAME);
}



