import React, {Component} from "react";
import {setTopMargin} from "../helpers/Utilities";
import MenuBar from "../Menubar/Menubar";
import {Link} from "react-router-dom";
import { LOCAL_CONSTANTS, PRICE_CURRENCY} from "../helpers/Constants";
import {PurchaseHistory} from "./PurchaseHistory";
import {UpdateCustomer} from "./UpdateCustomer";
import {CurrentOrderTrack} from "./CurrentOrderTrack";
import {downloadFile, requestList, requestValue} from "../helpers/request_helpers";
import UserImage from "../style/images/profile.png";
import {getUserName} from "../helpers/Local_Utilities";

export class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            userName: "",
            topMargin: undefined,
            editModal: false,
            currentOrderList: [],
            customer: {},
            hasAddress: {}
        };
        this.setTopMargin = setTopMargin.bind(this);
        this.getCurrentOrders = this.getCurrentOrders.bind(this);
        this.getCustomerDetail = this.getCustomerDetail.bind(this);
        this.getCustomerAddress = this.getCustomerAddress.bind(this);
        this.hideEditModal = this.hideEditModal.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        const userName = localStorage.getItem(LOCAL_CONSTANTS.USERNAME);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message),
                userName: userName
            })
        }
    }

    componentDidMount() {
        const {userName} = this.state;
        this.setTopMargin();
        if(userName){
            this.getCustomerDetail();
            this.getCurrentOrders();
        }
        window.scrollTo(0, 0);

    }


    showEditModal() {
        // this.setState({
        //     editModalVisible: true
        // })
        const element = document.getElementById("editModal");
        element.style.display = "block";
    }

    hideEditModal() {
        // this.setState({
        //     editModalVisible: false
        // })
        const element = document.getElementById("editModal");
        element.style.display = "none";
        this.getCustomerDetail();

    }

    getCustomerDetail = () => {
        const {config, query, userName} = this.state;
        const {getCustomerDetail} = query;
        const {data} = getCustomerDetail;
        requestValue(Object.assign({},
            getCustomerDetail, {
                data: {
                    ...data,
                    userName
                }
            }), config)
            .then(detail => {
                console.log(detail);
                const {identifier} = detail;
                this.getCustomerAddress(identifier);

                this.getCustomerMainImage(identifier)
                    .then(mainImage => {
                        this.setState({
                            customer: {
                                ...detail,
                                mainImage
                            }
                        })
                    })

            })
            .catch(error => console.log(error))
    };

    getCustomerMainImage(identifier) {
        const {config, query} = this.state;
        const {getMainImageUser} = query;
        const {data} = getMainImageUser;
        return new Promise((resolve, reject) => {

            requestValue(Object.assign({}, getMainImageUser, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(async mainImage => {
                    const {contentUrl} = mainImage;
                    {/** Download*/
                    }
                    if (contentUrl) {
                        await downloadFile(config, contentUrl)
                            .then(url => {
                                resolve({
                                    ...mainImage,
                                    contentUrl: url
                                })
                            })
                            .catch(error => console.log(" Image Download => " + error))
                    } else {
                        resolve("Done")
                    }
                })
                .catch(error => {
                    console.log(" Image Get => " + error);
                    reject(error);
                })
        })
    }


    getCurrentOrders = () => {
        const {config, query, userName} = this.state;
        const {trackCurrentOrders} = query;
        const {data} = trackCurrentOrders;
        requestList(Object.assign({}, trackCurrentOrders, {
            data: {
                ...data,
                userName
            }
        }), config)
            .then(currentOrderList => {
                this.setState({
                    currentOrderList
                })
            })
            .catch(error => console.log(error))
    };


    getCustomerAddress = (identifier) => {
        const {config, query} = this.state;
        const {getUserAddress} = query;
        const {data} = getUserAddress;
        requestValue(Object.assign({}, getUserAddress, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(hasAddress => {
                this.setState({
                    hasAddress
                })
            })
            .catch(error => console.log(error))
    };


    render() {
        const {handleSearchConfirm} = this.props;
        const {topMargin, currentOrderList, customer, hasAddress} = this.state;
        const {givenName, familyName, telephone, email, mainImage} = customer;
        const {contentUrl} = mainImage || {};
        const {streetAddress, addressLocality} = hasAddress;
        return (
            <div className="all-page" >
                <div style={{flex: "auto", marginTop: `${topMargin}px`}}/>
                <div className="layout">
                    <div className="page-wrapper">
                        <div className="container">
                            <div className="row profile">
                                <div className="col-md-9">
                                    <div className="row">
                                        <div className="col-md-6">
                                            <div className="profile__image float-left mr-3">
                                                <img src={contentUrl ? contentUrl : UserImage}/>
                                            </div>
                                            <div className="profile__info float-left">
                                                <h1 className="h5">{givenName} {familyName}</h1>
                                                <p>
                                                    <a href="mailto:">{email}</a><br/>
                                                    <span>{telephone}</span></p>
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            {
                                                streetAddress && addressLocality
                                                    ? <div className="profile__address float-left mr-3">
                                                        <label className="title caps">Delivery Address</label>
                                                        <p>
                                                            <label className="mr-3 small dim">Address </label>
                                                            <span>{streetAddress}, {addressLocality}</span><br/>
                                                            {/*<label className="mr-3 small dim">Address 2</label>*/}
                                                            {/*<span>Charkhal, Dillibazar</span>*/}
                                                        </p>

                                                    </div>
                                                    : null
                                            }
                                        </div>
                                    </div>
                                </div>
                                <div className="col-md-2">
                                    <button className="btn btn-outline-primary btn-block" onClick={this.showEditModal}>
                                        Edit Info
                                    </button>
                                </div>
                            </div>
                            <div className="col-md-12">
                                <hr/>
                            </div>

                            {
                                currentOrderList.length
                                    ? <div className="mt-5">
                                        <div className="title caps">Your Current Order Status</div>
                                        {
                                            currentOrderList.map(order => <CurrentOrderTrack order={order}/>)
                                        }
                                    </div>
                                    : null
                            }
                            {/*<CurrentOrderTrack order={{*/}
                            {/*orderStatus: "Confirmed",*/}
                            {/*orderNumber: "f214321",*/}
                            {/*orderAmount: 5000,*/}
                            {/*identifier: "322387rvjhbg",*/}
                            {/*orderDate: "2018-04-14T11:20:30+01:00"*/}
                            {/*}}/>*/}
                            {/*<CurrentOrderTrack order={{*/}
                            {/*orderStatus: "Delivered",*/}
                            {/*orderNumber: "f214321erg",*/}
                            {/*orderAmount: 5000,*/}
                            {/*identifier: "322387rvjhbgsdg",*/}
                            {/*orderDate: "2018-04-14T11:20:30+01:00"*/}
                            {/*}}/>*/}

                            <PurchaseHistory customer={{userName: getUserName()}}/>
                            <br/>
                        </div>
                    </div>
                </div>

                <div className="modal fade show" id="editModal" tabIndex="-1" role="dialog"
                     aria-labelledby="editModal" style={{display: "none", overflowY: "auto"}}>
                    <div className="modal-dialog modal-lg" role="document">
                        <div className="modal-content">
                            <div className="modal-header modal-header--close-only">
                                <button type="button"
                                        onClick={this.hideEditModal} className="close" data-dismiss="modal"
                                        aria-label="Close">
                                    <span aria-hidden="true"></span>
                                </button>
                            </div>
                            <div className="modal-body">
                                <p className="text-center">Hello, {getUserName()}! <br/>
                                </p>
                                <UpdateCustomer customer={customer} closeModal={this.hideEditModal}/>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}


