import React from "react";
import close from "../style/images/close.svg";
import {SingleList} from "./SingleList";
import {
    LOCAL_CONSTANTS,
} from "../helpers/Constants";
import {removeShoppingList,setTopMargin} from "../helpers/Utilities";
import {
    getAutoSearchStatus,
    setAutoSearchStatus
} from "../helpers/Local_Utilities";

export default class extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            root: {},
            autoSearch: false,
            topMargin: undefined
        };
        this.toggleAutoSearch = this.toggleAutoSearch.bind(this);
        this.setTopMargin = setTopMargin.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            }, () => {

            })
        }
    }

    componentDidMount() {
        this.setTopMargin();
        this.setState({
            autoSearch: JSON.parse(localStorage.getItem("autoSearch"))
        });
        {/** Click the Back button in ShoppingList for responsive views!*/
        }
        if (window.innerWidth < 991.98) {
            document.getElementById("shopping-list-back-btn").click();
        }
    }

    toggleAutoSearch() {
        const {autoSearch} = this.state;
        setAutoSearchStatus(!autoSearch);
        this.setState({autoSearch: getAutoSearchStatus()})
    }

    render() {
        const {
            productToAdd, showAlert, undo, clearProductAdded, handleUndo,
            handleSearchConfirm, getSearchList, showShoppingList, clearShoppingList
        } = this.props;
        const {autoSearch, topMargin} = this.state;
        return (
            <div style={{zIndex: 2000}}>
                {/** Preview in the side*/}
                <div className="list-create-card__holder homepage-list-create-card"
                     id="homepage_list_create_card"
                     onClick={() => showShoppingList()}
                >
                    <div className="list-create-card__inner">

                        <div className="list-create-card ">
                            <div className="list-create-card__head list-create-card-ruled">
                                <div className="list-create-card__title-item-count">
                                    <div className="list-create-card__list-title" contentEditable="true">Shopping List
                                    </div>
                                    <div className="list-create-card__item-count">0 items</div>
                                </div>

                                {/*<div className="list-create-card__icons js-list-create-card__icons">*/}
                                {/*<ul className="d-flex flex-row">*/}
                                {/*<li><a href="#" className="p-2"><img src="../style/images/ic_upload.svg"/></a>*/}
                                {/*</li>*/}
                                {/*<li className="dropdown">*/}
                                {/*<a href="#"*/}
                                {/*className="list-create-card__overflow-dropdown dropdown-toggle p-2"*/}
                                {/*data-toggle="dropdown"><img src="../style/images/ic_overflow.svg"/></a>*/}
                                {/*/!*<div className="dropdown-menu" aria-labelledby="bd-versions">*!/*/}
                                {/*/!*<a className="dropdown-item" href="/">Share</a>*!/*/}
                                {/*/!*<a className="dropdown-item" href="/">Save List</a>*!/*/}
                                {/*/!*</div>*!/*/}
                                {/*</li>*/}
                                {/*<li><a href="#" className="p-2 list-create-card__caret"><img*/}
                                {/*src="../style/images/ic_caret.svg"/></a></li>*/}
                                {/*</ul>*/}
                                {/*</div>*/}
                                {/*<div className="list-create-card__closed-icons">*/}
                                {/*<ul className="d-flex flex-row">*/}
                                {/*<li><a href="#" className="p-2 list-create-card__caret"><img*/}
                                {/*src="../style/images/ic_caret.svg"/></a></li>*/}
                                {/*</ul>*/}
                                {/*</div>*/}
                            </div>

                            <div className="list-create-card__body">
                                <div
                                    className="list-create-card__list-item list-create-card-ruled list-create-card__list-item--empty">
                                    <div className="list-create-card__action-left" role="button"/>
                                    <div className="list-create-card__field">
                                        <input type="text" className="list-create-card__input"
                                               placeholder="Add new item"/>
                                    </div>
                                </div>
                                {/*<div className="list-create-card__list-item list-create-card-ruled">*/}
                                {/*<div className="list-create-card__action-left" role="button"/>*/}
                                {/*<div className="list-create-card__field  d-flex">*/}
                                {/*<input type="text" className="list-create-card__input" value="Daal"*/}
                                {/*placeholder="Add new item"/>*/}
                                {/*</div>*/}
                                {/*</div>*/}
                                {/*<div*/}
                                {/*className="list-create-card__list-item list-create-card-ruled list-create-card__has-items">*/}
                                {/*<div className="list-create-card__action-left" role="button"/>*/}

                                {/*<div className="list-create-card__field d-flex">*/}
                                {/*<input type="text" className="list-create-card__input" readOnly value="Rice"/>*/}
                                {/*<div className="list-create-card__price">Rs. 1440</div>*/}
                                {/*</div>*/}

                                {/*<div className="list-create-card__items mb-2">*/}
                                {/*<div className="list-create-card__item media">*/}
                                {/*<div className="list-create-card__item-image mr-3">*/}
                                {/*<img src="images/gyan-basmati-rice.png" alt="Image"/>*/}
                                {/*<a href="/"/>*/}
                                {/*</div>*/}
                                {/*<div className="media-body">*/}
                                {/*1 x Banno Biryani Long Grain Rice, 5kg <span*/}
                                {/*className="list-create-card__item-price">Rs. 120</span>*/}

                                {/*</div>*/}


                                {/*</div>*/}
                                {/*</div>*/}
                                {/*</div>*/}
                                <br/>
                            </div>

                            {/*<div className="list-create-card__footer js-list-create-card__footer">*/}
                            {/*<a href="#" className="btn btn-footer-cta btn-block">Add to current shopping list</a>*/}
                            {/*</div>*/}

                        </div>
                    </div>
                </div>

                {/** Actual Shopping list side bar*/}
                <div id="shopping-list"
                     className="homepage-sidebar page-sidebar js-homepage-sidebar">
                    <aside className="sidebar js-sidebar" style={{paddingTop: `${topMargin + 35}px`}}>

                        <h3 className="title">
                            <span className="icon icon-bag"/>
                            <span style={{cursor: 'pointer'}}>Shopping Lists &nbsp;
                                <span
                                    id="shopping-list-back-btn"
                                    className="sidebar__back js-sidebar-back"
                                    onClick={removeShoppingList}>Back  </span>
                            </span>
                        </h3>

                        <div className="sidebar__list-disclaimer">
                            <div className="clearfix mt-3"/>
                            <div className="filter__option-list-item" style={{float: "right"}}>
                                <label className="custom-control custom-checkbox">
                                    <input type="checkbox" className="custom-control-input"
                                           name="autoSearch" id="autoSearch" value="change"
                                           checked={autoSearch}
                                           onChange={(event) => {
                                               this.toggleAutoSearch();
                                           }}/>
                                    <span className="custom-control-label">Auto Search</span>
                                </label>
                                <br/>
                            </div>

                        </div>
                        {/** Main body of shopping list*/}
                        <SingleList status='current'
                                    autoSearch={autoSearch}
                                    showAlert={showAlert}
                                    undo={undo}
                                    handleUndo={handleUndo}
                                    clearProductAdded={clearProductAdded}
                                    productToAdd={productToAdd}
                                    handleSearchConfirm={handleSearchConfirm}
                                    getSearchList={getSearchList}
                                    removeShoppingList={removeShoppingList}
                                    clearShoppingList={clearShoppingList}

                        />
                        {/** Have to send the name of this list as prop to set the id of this div*/}
                        {/** Have to send JSON LD of shopping list*/}
                        {/*<SingleList name="Dashain"/>*/}

                    </aside>
                </div>
            </div>
        );
    }
}




