import React from "react";
import {requestList, requestValue, downloadFile} from "../helpers/request_helpers";
import {Link} from "react-router-dom";
import {DefaultImage} from "../StaticContent/staticContents";
import {redirectToPath} from "../helpers/Utilities";
import {LOCAL_CONSTANTS} from "../helpers/Constants";

export class Search extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            root: {},
            productSearchResults: [],
            productError: "",
            categorySearchResults: [],
            categoryError: "",
            searchQuery: ""
        };
        this.searchProducts = this.searchProducts.bind(this);
        this.searchCategories = this.searchCategories.bind(this);
        this.setProductResponse = this.setProductResponse.bind(this);
        this.setProductError = this.setProductError.bind(this);
        this.setDefaultProductList = this.setDefaultProductList.bind(this);
        this.setDefaultCategories = this.setDefaultCategories.bind(this);
        this.setCategoryResponse = this.setCategoryResponse.bind(this);
        this.setCategoryError = this.setCategoryError.bind(this);
        this.listCategoryProducts = this.listCategoryProducts.bind(this);
        this.showSearchResults = this.showSearchResults.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        const root = localStorage.getItem(LOCAL_CONSTANTS.ROOT);

        if (config && query && message && root) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message),
                root: JSON.parse(root)
            }, () => {
                this.setDefaultProductList();
                this.setDefaultCategories();
            })
        }
    }

    componentDidMount() {
        window.addEventListener("mouseup", (event) => {
            const temp1 = document.getElementById("search-autocomplete");
            if ((event.target !== temp1) && (event.target.parentNode !== temp1)) {
                temp1.className = temp1.className.replace(" autocomplete--show", "");
            }
            const temp2 = document.getElementById("responsive-search");
            if (event.target !== temp2 && event.target.parentNode !== temp2) {
                temp2.className = temp2.className.replace(" global-nav__search-form--show", "");
            }
        });

    }

    setDefaultCategories() {
        const {query, config} = this.state;
        const {listDefaultCategories} = query;
        this.setCategoryResponse([]);
        requestList(listDefaultCategories, config)
            .then(data => {
                this.setCategoryResponse(data);
            })
            .catch(error => {
                console.log(error)
            })
    }

    setDefaultProductList() {
        const {query, config} = this.state;
        const {listDefaultProducts} = query;
        this.setProductResponse([]);
        requestList(listDefaultProducts, config)
            .then(data => this.setProductResponse(data))
            .catch(error => {
                this.setProductError();
                console.log(error)
            })
    }

    showSearchResults() {
        const id = "search-autocomplete";
        const classname = " autocomplete--show";
        if (!document.getElementById(id).className.includes(classname)) {
            document.getElementById(id).className =
                document.getElementById(id).className + classname;
            this.setDefaultProductList();
        }
    }

    showResponsiveSearchOption() {
        document.getElementById("responsive-search").className =
            document.getElementById("responsive-search").className + " global-nav__search-form--show";
    }

    listCategoryProducts(identifier) {
        const {query, config} = this.state;
        const {listCategoryProducts} = query;
        const {data} = listCategoryProducts;
        this.setProductResponse([]);
        requestList(Object.assign({}, listCategoryProducts, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(productList => {
                this.setProductResponse(productList)
            })
            .catch((error) => {
                this.setProductError();
                console.log(error)
            })
    }

    searchCategories(searchQuery) {
        const {query, config} = this.state;
        const {searchCategories} = query;
        const {data} = searchCategories;
        this.setCategoryResponse([]);
        requestList(Object.assign({}, searchCategories, {
                data: {
                    ...data,
                    categoryName: searchQuery
                }
            }),
            config)
            .then(data => this.setCategoryResponse(data))
            .catch(error => {
                this.setCategoryError();
                console.log(error)
            })
    }

    searchProducts(searchQuery) {
        const {query, config} = this.state;
        const {searchProducts} = query;
        const {data} = searchProducts;
        this.setProductResponse([]);
        requestList(Object.assign({}, searchProducts, {
                data: {
                    ...data,
                    productName: searchQuery
                }
            }),
            config)
            .then(data => this.setProductResponse(data))
            .catch(error => {
                this.setProductError();
                console.log(error)
            })
    }

    setCategoryResponse(data) {
        this.setState({categorySearchResults: data, categoryError: ""})
    }

    setCategoryError() {
        const {message} = this.state;
        const {categorySearchError} = message;
        this.setState({
            categorySearchResults: [],
            categoryError: categorySearchError
        })
    }

    setProductResponse(data) {
        this.setState({productSearchResults: data, productError: ""})
    }

    setProductError() {
        const {message} = this.state;
        const {productSearchError} = message;
        this.setState({
            productSearchResults: [],
            productError: productSearchError
        })
    }

    render() {
        const {
            config, query, productSearchResults, productError,
            categorySearchResults, categoryError,
            searchQuery, root
        } = this.state;
        const {rootPath} = root;
        const {showShoppingList, handleUndo} = this.props;
        const LeftPane = (props) => <div className="autocomplete__suggestions">
            <h6 className="autocomplete__suggestion-header">What do you want to buy today?</h6>
            {/*<a href="#"*/}
            {/*className="autocomplete__suggestion-item autocomplete__suggestion-item--active autocomplete__suggestion-item--has-offer"><span*/}
            {/*className="autocomplete-offer-category">Offers and Discounts</span></a>*/}
            {categorySearchResults && categorySearchResults.length
                ? categorySearchResults.map((item, index) => {
                        const {categoryName, identifier} = item;
                        return <Link to={`/search/KiranaCategory/${categoryName}/${identifier}`}
                                     key={index}
                                     onMouseEnter={() => this.listCategoryProducts(identifier)}
                                     className="autocomplete__suggestion-item">
                            {categoryName}
                        </Link>
                    }
                )
                : null
            }
            {
                categoryError.length
                    ? <div style={{margin: "20% 25px"}}>{categoryError}</div>
                    : null
            }
        </div>;

        return (
            <div>
                <div className="global-nav__search">
                    <div className="global-nav__search-trigger js-global-nav__search-trigger"
                         onClick={this.showResponsiveSearchOption}/>
                    <form id="responsive-search"
                          className="global-nav__search-form js-global-nav__search-form ">
                        <input className="form-control" id="global_search" type="search"
                               onClick={this.showSearchResults}
                               onChange={(event) => {
                                   event.preventDefault();
                                   const searchQuery = event.target.value;
                                   if (searchQuery.length >= 3) {
                                       this.setState({searchQuery: searchQuery});
                                       this.searchProducts(searchQuery);
                                       this.searchCategories(searchQuery);
                                   } else if (searchQuery.length === 0) {
                                       this.setDefaultProductList();
                                       this.setDefaultCategories();
                                   }
                               }}
                               onKeyUp={(event) => {
                                   {/** Have to make separate request response for this action here*/
                                   }
                                   event.preventDefault();
                                   if (event.keyCode === 13) {
                                       const searchQuery = event.target.value;
                                       redirectToPath(`#${rootPath}/search/KiranaProduct/${searchQuery}/0`);
                                   }
                               }}
                               // onMouseEnter={this.showSearchResults}
                               placeholder="Search for products or categories"/>

                        <div id="search-autocomplete" tabIndex="-1" className="autocomplete autocomplete--suggestions">
                            <div className="autocomplete__body">
                                <LeftPane/>
                                <div className="autocomplete__results">
                                    {
                                        productSearchResults && productSearchResults.length
                                            ? productSearchResults.map((item, index) => {
                                                return <SearchProductPreview
                                                    key={index}
                                                    product={item}
                                                    showShoppingList={showShoppingList}
                                                    handleUndo={handleUndo}
                                                    queryString={searchQuery}
                                                    showSearchResults={this.showSearchResults}
                                                />
                                            })
                                            : null
                                    }
                                    {
                                        productError.length
                                            ? <div style={{margin: "25% 25px"}}>{productError}</div>
                                            : null
                                    }
                                </div>
                            </div>
                        </div>

                        <div className="autocomplete autocomplete--results">
                            <div className="autocomplete__body">
                                <div className="autocomplete__suggestions">

                                    {/*<a href="#"*/}
                                    {/*className="autocomplete__suggestion-item autocomplete__suggestion-item--active"><span*/}
                                    {/*className="autocomplete-matched">Daal</span></a>*/}

                                    {/*<a href="#" className="autocomplete__suggestion-item"><span*/}
                                    {/*className="autocomplete-matched">Daal</span><span*/}
                                    {/*className="autocomplete-brand autocomplete-suffix"> by STC</span></a>*/}

                                    {/*<a href="#" className="autocomplete__suggestion-item"><span*/}
                                    {/*className="autocomplete-matched">Daal</span><span*/}
                                    {/*className="autocomplete-category autocomplete-suffix"> in dal and pulses</span></a>*/}

                                    {/*<a href="#"*/}
                                    {/*className="autocomplete__suggestion-item autocomplete__suggestion-item--has-offer"><span*/}
                                    {/*className="autocomplete-matched">Daal</span><span*/}
                                    {/*className="autocomplete-offer-category autocomplete-suffix"> in offers and discounts</span></a>*/}

                                    {/*<a href="#"*/}
                                    {/*className="autocomplete__suggestion-item autocomplete__suggestion-item--view-all">View*/}
                                    {/*all results for <span className="autocomplete-matched">Daal</span></a>*/}
                                </div>
                                <div className="autocomplete__results">
                                    {
                                        productSearchResults && productSearchResults.length
                                            ? productSearchResults.map((item,index) => {
                                                return <SearchProductPreview
                                                    key={index}
                                                    product={item}
                                                    showShoppingList={showShoppingList}
                                                    handleUndo={handleUndo}
                                                    queryString={searchQuery}
                                                    showSearchResults={this.showSearchResults}
                                                />
                                            })
                                            : null
                                    }
                                    {
                                        productError.length
                                            ? <div style={{margin: "25% 25px"}}>{productError}</div>
                                            : null
                                    }

                                </div>
                            </div>
                        </div>
                    </form>
                </div>

            </div>
        );

    }
}

class SearchProductPreview extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            product: {},
            menuVisible: false,
            qty: 1
        };
        this.setProductPrice = this.setProductPrice.bind(this);
        this.setProductQValue = this.setProductQValue.bind(this);
        this.getMainImage = this.getMainImage.bind(this);
        this.increaseQty = this.increaseQty.bind(this);
        this.decreaseQty = this.decreaseQty.bind(this);
        this.setQty = this.setQty.bind(this);
        this.clearQty = this.clearQty.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }


    componentDidMount() {
        const {product} = this.props;
        this.setState({
            product
        }, () => {
            const {product, config, query} = this.state;
            const {productID, productName, identifier} = product;
            this.setProductQValue()
                .then(() => this.setProductPrice())
                .then(() => this.getMainImage());
        });
    }

    getMainImage() {
        const {config, query, product} = this.state;
        const {getMainImageProduct} = query;
        const {identifier, productName} = product;
        const {data} = getMainImageProduct;

        requestValue(Object.assign({}, getMainImageProduct, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(mainImage => {
                this.setState({
                    product: {
                        ...product,
                        mainImage
                    }
                }, async () => {
                    const {product} = this.state;
                    const {mainImage} = product;
                    const {contentUrl} = mainImage;
                    {/** Download*/
                    }
                    if (contentUrl) {
                        await downloadFile(config, contentUrl)
                            .then(url => {
                                this.setState({
                                    product: {
                                        ...product,
                                        mainImage: {
                                            ...mainImage,
                                            contentUrl: url
                                        }
                                    }
                                })
                            })
                            .catch(error => console.log(productName + " Image Download => " + error))
                    } else {
                        this.setState({
                            product: {
                                ...product,
                                mainImage: {
                                    ...mainImage,
                                    contentUrl: DefaultImage
                                }
                            }
                        })
                    }
                })
            })
            .catch(error => console.log(productName + "Image Get " + error))

    }

    setProductQValue() {
        const {config, query, product} = this.state;
        const {getProductQValue} = query;
        const {productID} = product;
        const {data} = getProductQValue;

        return new Promise((resolve, reject) => {

            requestValue(Object.assign({}, getProductQValue, {
                data: {
                    ...data,
                    productID
                }
            }), config)
                .then(data => {
                    this.setState({
                        product: {
                            ...product,
                            unitValue: data
                        }
                    }, () => {
                        resolve("Unit value success");
                    })
                })
                .catch(error => reject(error));
        })
    }

    setProductPrice() {
        const {config, query, product} = this.state;
        const {getProductPrice} = query;
        const {productID} = product;
        const {data} = getProductPrice;

        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getProductPrice, {
                data: {
                    ...data,
                    productID
                }
            }), config)
                .then(data => {
                    this.setState({
                        product: {
                            ...product,
                            productPrice: data
                        }
                    }, () => {
                        resolve("Product price success");
                    })
                })
                .catch(error => reject(error));
        })
    }

    /** Set input quantity to State*/
    setQty(qty) {
        this.setState({qty});
    }

    /** Here need to handle for cases with limited availability*/
    increaseQty() {
        const {qty} = this.state;
        this.setState({
            qty: +qty + +1
        });

    }

    /** Minimum quantity is 1 */
    decreaseQty() {
        const {qty} = this.state;
        if (qty > 1) {
            this.setState({
                qty: qty - 1
            })
        }
    }

    clearQty() {
        this.setState({qty: 1});
    }


    render() {
        const {
            config,
            query,
            product,
            menuVisible,
            qty
        } = this.state;
        const {
            showShoppingList, handleUndo, queryString, showSearchResults
        } = this.props;
        const {productID, identifier, productName, unitValue, productPrice, mainImage} = product;
        const {price, maxPrice, priceCurrency} = productPrice || {};
        const {contentUrl} = mainImage || {};
        const {unitText, value} = unitValue || {};
        return <div className="autocomplete__result-item">
            <div className="autocomplete__item-image">
                <Link to={`/detail:${identifier}`} style={{textDecoration: "none"}}>
                    <img itemProp="contentUrl" src={contentUrl}/>
                </Link>

            </div>

            <div className="autocomplete__item-info">
                <div className="autocomplete__item-top-info">
                        <span className="autocomplete__item-offer">
                            {/*15% off */}
                            <span style={{float: "right"}}>{value}{unitText}</span></span>
                </div>
                <div className="autocomplete__item-title">
                    <Link to={`/detail:${identifier}`}
                          style={{textDecoration: "none"}}>
                        {productName}</Link>
                </div>
                <div
                    className="autocomplete__item-price autocomplete__item-price--discounted">

                    {maxPrice ? <span className="price-previous">{priceCurrency} {maxPrice}</span> : null}

                    <span className="price-current">{priceCurrency} {price}</span>
                </div>
            </div>

            <div className="autocomplete__item-qty-add">
                <div className="autocomplete__item-qty mb-2">

                    <div
                        className="input-group flex-nowrap product-qty__select  product-qty__select--sm  ">
                        <div className="input-group-prepend">
                            <button
                                className="product-qty__btn product-qty__btn--left product-qty__btn--minus"
                                onClick={() => {
                                    this.decreaseQty();
                                    showSearchResults();
                                }}
                                type="button">-
                            </button>
                        </div>
                        <input type="text" className="form-control product-qty__input"
                               value={qty}
                               onChange={(event) => {
                                   this.setQty(event.target.value)
                               }}/>

                        <div className="input-group-append">
                            <button
                                className="product-qty__btn product-qty__btn--right product-qty__btn--plus"
                                onClick={() => {
                                    this.increaseQty();
                                    showSearchResults();
                                }}
                                type="button">+
                            </button>
                        </div>
                    </div>

                </div>
                <div className="autocomplete__item-add">
                    <div className="btn-group btn-block" role="group">
                        <button type="button" className="btn btn-sm btn-primary btn-block"
                                style={{padding: "0.3rem"}}
                                onClick={() => {
                                    handleUndo(false);
                                    const temp = queryString
                                        ? Object.assign({},
                                            {...product},
                                            {quantity: qty},
                                            {generalName: queryString})
                                        : Object.assign({},
                                            {...product},
                                            {quantity: qty}
                                        );
                                    showShoppingList(temp);
                                    // this.clearQty();
                                    setTimeout(() => {
                                        this.clearQty()
                                    }, 200);
                                    showSearchResults();
                                }}>
                            Add
                        </button>
                        {/*<div className="btn-group btn-group-sm" role="group">*/}
                        {/*<button id="autocomplete_add" type="button"*/}
                        {/*className="btn btn-sm btn-outline-primary dropdown-toggle"*/}
                        {/*data-toggle="dropdown" aria-haspopup="true"*/}
                        {/*onClick={() => {*/}
                        {/*this.setState({menuVisible: !menuVisible});*/}
                        {/*showSearchResults();*/}
                        {/*}}*/}
                        {/*aria-expanded="false">*/}
                        {/*</button>*/}

                        {/*{*/}
                        {/*menuVisible ? <div className="dropdown-menu dropdown-menu-right"*/}
                        {/*aria-labelledby="autocomplete_add">*/}
                        {/*<h6 className="dropdown-header">Select List</h6>*/}
                        {/*<a className="dropdown-item" href="#">Birthday</a>*/}
                        {/*<a className="dropdown-item" href="#">BBQ Party</a>*/}
                        {/*<div className="dropdown-divider"></div>*/}
                        {/*<div className="form-group px-3 py-1 mb-0">*/}
                        {/*<input type="text"*/}
                        {/*className="form-control dropdown-item px-0"*/}
                        {/*placeholder="Create New List"/>*/}
                        {/*</div>*/}
                        {/*</div>*/}
                        {/*: null*/}
                        {/*}*/}

                        {/*</div>*/}
                    </div>
                </div>
            </div>
        </div>

    }

};



