import React from "react";

/**
 * Notification bar for new notices of offers and stuff
 */
export class NotificationBar extends React.Component {

    render() {
        return <div className="notification js-notification notification--fixed">
            <div className="container">
                <div className="row">
                    <div className="col-8">{this.props.title}</div>
                    <div className="col-4 text-right">Order at 01-4411000</div>
                </div>
            </div>
        </div>

    }
}


