import React from "react";
import {NotificationBar} from "../NotificationBar/NotificationBar";
import NavBar from "./NavBar/NavBar";
import {setTopMargin} from "../helpers/Utilities";
import MenuBar from "../Menubar/Menubar";
import {LOCAL_CONSTANTS} from "../helpers/Constants";

export const HEADER_ID = "header-section";

export class Headers extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            topMargin: undefined
        };
        this.setTopMargin = setTopMargin.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        this.setTopMargin();
        // window.addEventListener("scroll", (event) => {
        //     const {topMargin} = this.state;
        //     const last_known_scroll_position = window.scrollY;
        //     const element = document.getElementById(HEADER_ID);
        //     if (last_known_scroll_position > topMargin && window.innerWidth > 990) {
        //         if (!element.className.includes("fixed-top") && last_known_scroll_position > 90) {
        //             element.className = element.className.replace("header-top", "fixed-top");
        //         }
        //     } else {
        //         // if (element.className.includes("fixed-top")) {
        //         element.className = element.className.replace("fixed-top", "header-top");
        //         // }
        //
        //     }
        //
        // })
    }

    render() {
        const {query, config, topMargin} = this.state;
        const {showShoppingList, handleUndo, handleSearchConfirm} = this.props;
        return (
            <div>
                <div id={HEADER_ID} className="header js-header fixed-top" style={{
                    opacity: this.props.headerOpacity,
                    zIndex: 1600,
                    height: "auto"
                }}>
                    {/** Header contains NotificationBar, NavBar, MenuBar*/}

                    {/** Show this notification bar when there is notification
                     and create a link to this to explore more about the content*/}
                    {/*<NotificationBar title="New Offers available for today, Click to find out more."*/}
                    {/*style={{flex: "auto"}}/>*/}
                    <NavBar style={{flex: "auto"}}
                            showShoppingList={showShoppingList}
                            handleUndo={handleUndo}
                            handleSearchConfirm={handleSearchConfirm}
                    />
                </div>
                <MenuBar handleSearchConfirm={handleSearchConfirm}/>
            </div>
        );
    }
}


