import  React, {Component} from 'react';
import './App.css';
import "./style/main.css";
import "./style/newstyle.css";
import "./style/main-latest.css";

import {Route, Switch, HashRouter} from "react-router-dom";
import {LoadingComponent,} from "./StaticContent/staticContents";
import {MainPage} from "./MainPage/MainPage";
import QueryRequest from "./helpers/request_template";
import MessageTemplate from "./helpers/message_template";
import {LOCAL_CONSTANTS} from "./helpers/Constants";

class App extends Component {
    constructor(props) {
        super(props);
        this.state = {
            query: undefined,
            config: undefined,
            message: undefined,
            root: undefined
        };
    }

    componentWillMount() {
        if (QueryRequest) {
            localStorage.setItem(LOCAL_CONSTANTS.QUERY, JSON.stringify(QueryRequest));
            this.setState({
                query: localStorage.getItem(LOCAL_CONSTANTS.QUERY)
            })
        }

        fetch(process.env.PUBLIC_URL + "/server_config.json")
            .then(data => data.json())
            .catch(error => console.log(error))
            .then(config => {
                    if (config) {
                        localStorage.setItem(LOCAL_CONSTANTS.CONFIG, JSON.stringify(config));
                    }
                    this.setState({
                        config: localStorage.getItem(LOCAL_CONSTANTS.CONFIG)
                    })
                }
            );
        if (MessageTemplate) {
            localStorage.setItem(LOCAL_CONSTANTS.MESSAGE, JSON.stringify(MessageTemplate));
            this.setState({
                message: localStorage.getItem(LOCAL_CONSTANTS.MESSAGE)
            })
        }
        fetch(process.env.PUBLIC_URL + "/root_config.json")
            .then(data => data.json())
            .catch(error => console.log(error))
            .then(root => {
                if (root) {
                    localStorage.setItem(LOCAL_CONSTANTS.ROOT, JSON.stringify(root));
                }
                this.setState({
                    root: localStorage.getItem(LOCAL_CONSTANTS.ROOT)
                })
            });
        // navigator.geolocation.getCurrentPosition(position => {
        //     console.log(position)
        // }, error => {
        //     console.log(error)
        // })
    }

    componentDidMount() {
        const {opacity, config, query, message, root} = this.state;
        if (config && query && message && root) {
            this.setState({headerHeight: document.getElementById('header-section').clientHeight});
        }
        let lastScrollTop = 0;
        // window.addEventListener("scroll", () => { // or window.addEventListener("scroll"....
        //     let st = window.pageYOffset || document.documentElement.scrollTop; // Credits: "https://github.com/qeremy/so/blob/master/so.dom.js#L426"
        //     if (st >= lastScrollTop && window.scrollY > this.state.headerHeight && opacity !== 0) {
        //         {/** Down scroll option => Hide Header bar*/}
        //         this.setState({
        //             opacity: 0
        //         });
        //     } else {
        //         {/** Up scroll option => Show Header bar*/
        //         }
        //         this.setState({
        //             opacity: 1
        //         });
        //     };
        //     lastScrollTop = st <= 0 ? 0 : st; // For Mobile or negative scrolling
        // }, false);

    }


    render() {
        const {
            opacity, headerHeight, productToAdd,
            undo, checkoutList, query, config,
            searchConfirmed, searchList, message, root
        } = this.state;
        if (config && query && message && root) {
            return (
                <HashRouter>
                    <Switch>
                        <div>
                            <Route path="/"
                                   hasChildren
                                   render={(props) => <MainPage/>}/>

                            {/*<Route path="*" render={() =>*/}
                            {/*<div className="container-login100">*/}
                            {/*<div>*/}
                            {/*<div className="flex-column align-items-center justify-content-center">*/}
                            {/*<img src={KiranaLogoColor} width="150px"/>*/}
                            {/*</div>*/}
                            {/*<br/>*/}
                            {/*<div className="flex-column align-items-center justify-content-center">*/}
                            {/*<h3>Oops. Page Not Found. &nbsp;*/}
                            {/*<Link to="https://www.merokirana.com" replace>Try this.</Link>*/}
                            {/*</h3>*/}
                            {/*</div>*/}
                            {/*</div>*/}
                            {/*</div>}*/}
                            {/*/>*/}
                        </div>
                    </Switch>
                </HashRouter>
            );
        } else {
            return <div className="container-login100"><LoadingComponent/></div>
        }
    }
}

export default App;



