import React from "react";
import MenuBar from "../Menubar/Menubar";
import {Link} from "react-router-dom";
import {ProductPreview} from "../Product/ProductPreview";
import {productPreviewList} from "../sampleData/sampleData";
import {
    LoadingComponent,
} from "../StaticContent/staticContents";
import {requestList} from "../helpers/request_helpers";
import {isMobileView, redirectToPath, setTopMargin} from "../helpers/Utilities";
import {getSearchConfirmStatus, getSearchList, getShoppingList,} from "../helpers/Local_Utilities";
import {LOCAL_CONSTANTS, PRODUCT_ORIGINS} from "../helpers/Constants";
const PRODUCTS_ID = "products-wrapper";

export class SearchResult extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            modalVisible: false,
            productList: [],
            errorResponse: "",
            productListPagination: {start: 0, end: 12},
            queryObject: {},
            loadBtnVisible: true,
            searchList: [],
            searchCounter: 0,
            productLoading: false,
            nextBtnVisible: false,
            topMargin: undefined
        };
        this.toggleModal = this.toggleModal.bind(this);
        this.setQueryResult = this.setQueryResult.bind(this);
        this.setErrorResponse = this.setErrorResponse.bind(this);
        this.setDataResponse = this.setDataResponse.bind(this);
        this.searchProductList = this.searchProductList.bind(this);
        this.clearData = this.clearData.bind(this);
        this.handleSearchList = this.handleSearchList.bind(this);
        this.increaseCounter = this.increaseCounter.bind(this);
        this.searchProducts = this.searchProducts.bind(this);
        this.setTopMargin = setTopMargin.bind(this);

    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        const root = localStorage.getItem(LOCAL_CONSTANTS.ROOT);
        if (config && query && message && root) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message),
                root: JSON.parse(root)
            })
        }
    }

    componentDidMount() {
        if (window.innerWidth >= 991.98) {
            this.props.showShoppingList();
        } else {
            const element = document.getElementById(PRODUCTS_ID);
            if (!element.className.includes("scroll=x")) {
                element.className += "scroll-x";
            }
        }
        this.setTopMargin();
        window.scrollTo(0, 0);

        const {queryObject, handleSearchConfirm} = this.props;
        const searchList = getSearchList();
        const searchConfirm = getSearchConfirmStatus();

        this.setState({
            searchList : searchList ? searchList :  [],
            queryObject,
            nextBtnVisible: searchConfirm
        }, () => {
            this.searchProductList();
            const {queryObject, searchList} = this.state;
            const {identifier} = queryObject;
            if (identifier >= searchList.length - 1) {
                this.setState({
                    nextBtnVisible: false
                }, () => handleSearchConfirm(false))
            }
        });
    }


    componentWillReceiveProps(nextProps) {
        const {queryObject, handleSearchConfirm} = nextProps;
        const queryObjectState = this.state;
        if ((queryObjectState["queryObject"]["identifier"] !== queryObject["identifier"]) || (queryObjectState["queryObject"]["name"] !== queryObject["name"])) {
            this.clearData();
            const searchList = getSearchList();
            const searchConfirm = getSearchConfirmStatus();

            this.setState({
                searchList : searchList ? searchList :  [],
                queryObject,
                nextBtnVisible: searchConfirm
            }, () => {
                this.searchProductList();
                const {queryObject, searchList} = this.state;
                const {identifier} = queryObject;
                if (identifier >= searchList.length - 1) {
                    this.setState({
                        nextBtnVisible: false
                    }, () => {
                        handleSearchConfirm(false);
                    })
                }

            });
        }
    }

    clearData() {
        this.setState({
            modalVisible: false,
            productList: [],
            errorResponse: "",
            productListPagination: {start: 0, end: 12},
            queryObject: {},
            loadBtnVisible: true,
            searchList: [],
            searchCounter: 0
        })
    }

// This is to search product according to the params of URL
    searchProductList(countSize) {
        const {productListPagination, query} = this.state;
        const {searchProducts} = query;
        const {pageLimit} = searchProducts || {};
        const {start, end} = productListPagination;
        if (countSize) {
            this.setState({
                productListPagination: {
                    start: start + countSize,
                    end: end + countSize
                }
            }, () => {
                this.searchProducts();
            });
        } else {
            this.searchProducts();
        }

    }

    searchProducts() {
        const {handleSearchConfirm} = this.props;
        const {query, queryObject, config, productListPagination} = this.state;
        const {type, name, identifier} = queryObject;
        document.title = name + " - Online Shopping in Nepal ";
        const {
            listCategoryProducts, searchProducts, listKiranaListProducts,
            listOfferProducts, listLatestProducts, listTopProducts,
            listFeaturedProducts, listSimilarProducts,
            listCollectionProducts
        } = query || {};
        const {start, end} = productListPagination;
        const {pageLimit} = listCategoryProducts;
        this.setState({
            productLoading: true
        });
        switch (type) {
            case "KiranaCategory" : {
                const {data} = listCategoryProducts;
                config && this.setQueryResult(Object.assign({}, listCategoryProducts, {
                    data: {
                        ...data,
                        identifier
                    },
                    pageLimit: {
                        ...pageLimit,
                        start,
                        end
                    }
                }), config);
                handleSearchConfirm(false);
                break;
            }
            case "KiranaProduct" : {
                const {data} = searchProducts;
                config && this.setQueryResult(Object.assign({}, searchProducts, {
                    data: {
                        ...data,
                        productName: name
                    },
                    pageLimit: {
                        ...pageLimit,
                        start,
                        end
                    }
                }), config);
                break;
            }
            case "KiranaList" : {
                const {data} = listKiranaListProducts;
                config && this.setQueryResult(Object.assign({}, listKiranaListProducts, {
                    data: {
                        ...data,
                        identifier
                    },
                    pageLimit: {
                        ...pageLimit,
                        start,
                        end
                    }
                }), config);
                handleSearchConfirm(false);
                break;
            }
            case "KiranaCollection" : {
                const {data} = listCollectionProducts;
                config && this.setQueryResult(Object.assign({}, listCollectionProducts, {
                    data: {
                        ...data,
                        identifier
                    },
                    pageLimit: {
                        ...pageLimit,
                        start,
                        end
                    }
                }), config);
                handleSearchConfirm(false);
                break;
            }
            case "KiranaOffer" : {
                const {data} = listOfferProducts;
                config && this.setQueryResult(Object.assign({}, listOfferProducts, {
                    data: {
                        ...data,
                        identifier
                    },
                    pageLimit: {
                        ...pageLimit,
                        start,
                        end
                    }
                }), config);
                handleSearchConfirm(false);
                break;
            }
            case "LatestProducts" : {
                config && this.setQueryResult(Object.assign({},
                    listLatestProducts, {
                        pageLimit: {
                            ...pageLimit,
                            start,
                            end
                        }
                    }), config);
                handleSearchConfirm(false);
                break;
            }
            case "TopProducts" : {
                config && this.setQueryResult(Object.assign({},
                    listTopProducts, {
                        pageLimit: {
                            ...pageLimit,
                            start,
                            end
                        }
                    }), config);
                handleSearchConfirm(false);
                break;
            }
            case "FeaturedProducts" : {
                config && this.setQueryResult(Object.assign({},
                    listFeaturedProducts, {
                        pageLimit: {
                            ...pageLimit,
                            start,
                            end
                        }
                    }), config);
                handleSearchConfirm(false);
                break;
            }
            case "SimilarProducts" : {
                const {data} = listSimilarProducts;
                config && this.setQueryResult(Object.assign({}, listSimilarProducts, {
                    data: {
                        ...data,
                        productID: identifier
                    },
                    pageLimit: {
                        ...pageLimit,
                        start,
                        end
                    }
                }), config);
                handleSearchConfirm(false);
                break;
            }
        }

    }

    setQueryResult(request, config) {
        const {message} = this.state;
        const {productSearchError} = message;
        const {loadBtnVisible} = this.state;
        requestList(request, config)
            .then(data => {
                if (!data.length) {
                    this.setState({
                        // productList: [],
                        errorResponse: productSearchError,
                        loadBtnVisible: false,
                        productLoading: false

                    })
                } else {
                    this.setDataResponse(data);
                }
            })
            .catch(error => {
                console.log(error);
                // this.setErrorResponse();
                this.setState({
                    errorResponse: productSearchError,
                    productLoading: false
                })
            })
    }

    setDataResponse(data) {
        const {productList} = this.state;
        this.setState({
            productList: [...productList, ...data.filter(product =>
                productList.findIndex(item => item["productID"] === product["productID"]) === -1)
            ],
            errorResponse: "",
            productLoading: false
        })
    }

    setErrorResponse() {
        const {message} = this.state;
        const {productSearchError} = message;
        this.setState({
            productList: [],
            errorResponse: productSearchError,
            productLoading: false

        })
    }

    toggleModal() {
        const {modalVisible} = this.state;
        this.setState({
            modalVisible: !modalVisible
        }, () => console.log(this.state.modalVisible));
    }

    handleSearchList = () => {
        const {searchCounter, searchList, root} = this.state;
        const {rootPath} = root;
        redirectToPath(`#${rootPath}/search/KiranaProduct/${searchList[searchCounter]}/${searchCounter}`)
        // const link = document.createElement('a');
        // link.href = `/search/KiranaProduct/${searchList[searchCounter]}/${searchCounter}`;
        // link.click();

    };

    increaseCounter() {
        const {searchCounter, queryObject, searchList} = this.state;
        {/** */
        }
        const {identifier} = queryObject;
        console.log("identifier count ", identifier);
        this.setState({
            searchCounter: Number(identifier) + 1
        }, () => {
            this.handleSearchList();
        });
    }

    render() {
        const {
            showShoppingList,
            config, query, handleUndo, queryObject, handleSearchConfirm
        } = this.props;
        const {type, name, identifier} = queryObject;
        const {
            modalVisible, productList, errorResponse, loadBtnVisible, searchList, searchCounter,
            productLoading, nextBtnVisible, topMargin
        } = this.state;
        return (
            <div className="all-page">
                {/** Menubar section*/}
                <div style={{flex: "auto", marginTop: `${topMargin}px`}}/>
                <div className="layout page page--has-sidebar">
                    <div className="page-wrapper">
                        <div className="container">
                            <div className="row">
                                {/** Filter or sort by section*/}
                                {/*{*/}
                                {/*(type !== "KiranaList")*/}
                                {/*? <FilterSection/>*/}
                                {/*: <div className="col-lg-3"/>*/}
                                {/*}*/}
                                <div className="col-lg-2"/>
                                <ResultSection showShoppingList={showShoppingList}
                                               toggleModal={this.toggleModal}
                                               productList={productList}
                                               queryObject={queryObject}
                                               name={(type === "KiranaProduct") ? name : null}
                                               handleUndo={handleUndo}
                                               errorResponse={errorResponse}
                                               increaseCounter={this.increaseCounter}
                                               searchList={searchList}
                                               searchCounter={searchCounter}
                                               productLoading={productLoading}
                                               nextBtnVisible={nextBtnVisible}
                                />

                            </div>
                            <div className="row">
                                <div className="col-lg-3"/>
                                <div className="col-md-9">
                                    {productList.length
                                        ? <div
                                            style={{
                                                display: "grid",
                                                gridTemplateColumns: "1fr 1fr 1fr",
                                                gridTemplateRows: "auto",
                                                justifyItems: "center",
                                                margin: "15px"
                                            }}
                                        >
                                            <div/>
                                            {
                                                loadBtnVisible
                                                    ? <button
                                                        className="btn btn-primary  btn-block"
                                                        title="Click to load more latest products"
                                                        onClick={() => this.searchProductList(6)}
                                                        style={{
                                                            boxShadow: 'rgba(0, 0, 0, 0.1) 0px 2px 4px 0px, rgba(0, 0, 0, 0.1) 0px 6px 20px 0px;'
                                                        }}>
                                                        Load More
                                                    </button>
                                                    : <div
                                                        style={{padding: "20px"}}>
                                                        {/*Sorry, no more products to load.*/}
                                                    </div>
                                            }
                                        </div>
                                        : null}
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
                {/*{modalVisible ? <ModalFilter toggleModal={this.toggleModal}/> : null}*/}
            </div>
        );
    }
}

class ResultSection extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            searchCounter: 0,
            productLoading: false,
            productList: [],
            errorResponse: ""
        };
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    // componentDidMount() {
    //     const {searchCounter, productLoading, productList, errorResponse} = this.props;
    //     console.log("This ", this.props);
    //     this.setState({
    //         searchCounter,
    //         productLoading,
    //         productList,
    //         errorResponse
    //     })
    // }

    componentWillReceiveProps(nextProps) {
        const {searchCounter, productLoading, productList, errorResponse} = nextProps;
        // console.log("NExt", nextProps);
        this.setState({
            searchCounter,
            productLoading,
            productList,
            errorResponse
        })
    }

    shouldComponentUpdate(nextProps, nextState) {
        // console.log("nextProps ", nextState);
        {/** Compare these and return false if same*/
        }
        // console.log("nextState ", nextState);
        return !(nextState === nextProps);
    }

    render() {
        {
            const {
                showShoppingList, toggleModal,
                handleUndo, queryObject, increaseCounter,
                nextBtnVisible
            } = this.props;
            const {searchCounter, config, query, productLoading, productList, errorResponse} = this.state;
            const searchList = getShoppingList();
            const {type, name} = queryObject;
            return (
                <div className="col-lg-10">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="row">
                                <div className="col-sm-6 col-lg-12 col-md-8">
                                    <div className="page__header">
                                        <h2 className="page__sub-head">
                                            {/*{console.log(searchList.length, searchList)}*/}
                                            {
                                                (nextBtnVisible)
                                                    ? <span>Search Results
                                        <button style={{float: "right", width: "100px"}}
                                                className="btn btn-primary btn-block"
                                                onClick={() => increaseCounter()}
                                        >
                                        Next 
                                    </button>
                                                    </span>
                                                    : null
                                            }

                                        </h2>
                                        <h1 className="page__title">{name}</h1>
                                    </div>
                                </div>

                                {/** filter options */}
                                {/*<div className="col-sm-6 col-md-4 col-lg-12 text-sm-right d-lg-none">*/}
                                {/*<div className="filter-toggle">*/}

                                {/*<button type="button" className="btn btn-outline-secondary mt-2 mt-md-0"*/}
                                {/*onClick={() => {*/}
                                {/*toggleModal();*/}
                                {/*console.log("Toggled");*/}
                                {/*}}>*/}
                                {/*Filter Results*/}
                                {/*</button>*/}
                                {/*</div>*/}
                                {/*</div>*/}


                            </div>
                        </div>
                        <div
                            className="col-md-12"
                        >
                            <div className="products-wrapper">
                                <div id={PRODUCTS_ID} className="row "
                                     style={{
                                         // display: "inline-flex", flexWrap: "wrap",
                                         // marginLeft: "10px"

                                         // display: "inline-flex",
                                         // alignItems: "center",
                                         // justifyContent: "start",
                                         // padding: "10px",
                                         // flexWrap: "wrap"
                                     }}
                                >
                                    {!productLoading
                                        ? (productList.length
                                                ? productList.map((product, index) => {
                                                    return <ProductPreview product={product}
                                                                           queryString={(type === "KiranaProduct") ? name : null}
                                                                           showShoppingList={showShoppingList}
                                                                           key={index}
                                                                           handleUndo={handleUndo}
                                                                           nameLimit={30}
                                                                           classname="col-xl-2 col-lg-4 col-md-6"
                                                                           origin={PRODUCT_ORIGINS.SEARCH}
                                                    />
                                                    //     <div itemScope
                                                    // // className="col-xl-6 col-md-6"
                                                    // // style={{margin: "10px"}}
                                                    // itemType="http://semantro.com/KiranaProduct">

                                                    // </div>
                                                })
                                                : <div className="container"
                                                       style={{margin: "10px 20px 10px 0"}}>{errorResponse}</div>
                                        )
                                        : <LoadingComponent/>}


                                    {/*{(!productList.length || errorResponse)*/}
                                    {/*? <div className="container">{errorResponse} </div>*/}
                                    {/*: null}*/}

                                </div>
                            </div>
                        </div>
                    </div>
                </div>);
        }
    }
};

