import axios from "axios";
import https from "https";
import {requestPaymentStatus,  requestValue} from "./request_helpers";
import {LOCAL_CONSTANTS,} from "../helpers/Constants";

const convert = require('xml-js');

const NABIL_ENDPOINT = "https://nabiltest.compassplus.com:8444/Exec";

const HOSTED_ADDRESS = "http://103.198.9.169";

export const PAYMENT_STATUS = {
    APPROVED: "APPROVED",
    CANCEL: "CANCELED",
    DECLINE: "DECLINED",
    EXPIRE: "EXPIRED"
};

export const PAYMENT_METHOD = {
    CASH: "Cash",
    NABIL: "Nabil"
};


const options = {compact: true, spaces: 4};

export function payWithNabil(orderAmount, paymentUrl) {
    const {config, query, message} = this.state;
    const {getPaymentInfo} = query;
    const {data} = getPaymentInfo;

    return new Promise((resolve, reject) => {
        requestValue(Object.assign({}, getPaymentInfo, {
            data: {
                ...data,
                orderAmount,
                paymentUrl
            }
        }), config)
            .then(detail => {
                const {Status} = detail;
                setPaymentDetail(detail);
                if (Status === "00") {
                    resolve(detail)
                } else {
                    const {ErrorMessage} = detail;
                    reject(ErrorMessage);
                }
            })
            .catch(error => {
                reject(error);
            })

    })
}

export function redirectToNabil(detail) {
    const {OrderID, SessionID, URL} = detail;
    const element = document.createElement("a");
    element.href = URL + "?" + encodeQueryData({OrderID, SessionID});
    // element.setAttribute("target", "_blank");
    document.body.appendChild(element);
    element.click();
    // document.body.removeChild(element);

}

export function getPaymentStatus() {
    const detail = getPaymentDetail();
    const {OrderID, SessionID} = detail;
    const {config, query} = this.state;
    const {getPaymentStatus} = query;
    const {data} = getPaymentStatus;
    return new Promise((resolve, reject) => {
        if (OrderID && SessionID) {
            // requestPaymentStatus({OrderID, SessionID, actionName: "getPaymentStatus"}, config)
            requestValue(Object.assign({}, getPaymentStatus, {
                data: {
                    ...data,
                    orderNumber: OrderID,
                    confirmationNumber: SessionID
                }
            }), config)
                .then(response => {
                    resolve(response);
                })
                .catch(error => {
                    reject(error);
                })
        } else {
            reject("NO PAYMENT DETAIL ")
        }
    })
}

export function getRedirectUrl(rootPath, listName) {
    return `${HOSTED_ADDRESS}${rootPath}#${rootPath}/checkPayment:true/checkout:${listName}`
}

function encodeQueryData(data) {
    const ret = [];
    for (let d in data)
        ret.push(encodeURIComponent(d) + '=' + encodeURIComponent(data[d]));
    return ret.join('&');
}

export function getNabilUrl(detail) {
    const {OrderID, SessionID, URL} = detail;
    return URL + "?" + encodeQueryData({OrderID, SessionID});
}

export function setPaymentDetail(detail) {
    localStorage.setItem(LOCAL_CONSTANTS.PAYMENT_DETAIL, JSON.stringify(detail));
}

export function getPaymentDetail() {
    const detail = localStorage.getItem(LOCAL_CONSTANTS.PAYMENT_DETAIL);
    return detail ? JSON.parse(detail) : {};
}


