import React from "react";
import DefaultImage from "../style/images/favicon_b&w.jpg";
import {downloadFile, requestValue} from "../helpers/request_helpers";
import {Link} from "react-router-dom";
import {LOCAL_CONSTANTS} from "../helpers/Constants";
import {getShoppingList, setShoppingList,} from "../helpers/Local_Utilities";

export class ProductInputItem extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            newValue: "",
            product: {
                mainImage: {
                    contentUrl: ""
                },
                quantity: 1
            }
        };
        this.setNewValue = this.setNewValue.bind(this);
        this.getMainImage = this.getMainImage.bind(this);
        this.setNewValue = this.setNewValue.bind(this);
        this.increaseQty = this.increaseQty.bind(this);
        this.decreaseQty = this.decreaseQty.bind(this);
        this.setQty = this.setQty.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }

    }


    componentDidMount() {
        const {product} = this.props;
        this.setState({
            product
        }, () => {
            const {product} = this.state;
            if (typeof product === "object") {
                // delete product.mainImage;
                const {identifier} = product;
                this.getMainImage(identifier);
            }
        })
    }

    componentWillReceiveProps(nextProps) {
        const {product} = nextProps;
        if (typeof product === "object" && ((product.identifier !== this.props.product.identifier) || (product.quantity !== this.props.product.quantity))) {
            this.setState({
                product
            }, () => {
                const {product} = this.state;
                if (typeof product === "object") {
                    // delete product.mainImage;
                    const {identifier} = product;
                    this.getMainImage(identifier);
                }
            })
        } else if (typeof product === "string") {
            this.setState({
                product
            }, () => {

            })
        }
    }

    getMainImage(identifier) {
        const {config, query, product} = this.state;
        const {getMainImageProduct} = query;
        const {data} = getMainImageProduct;
        requestValue(Object.assign({}, getMainImageProduct, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(mainImage => {
                this.setState({
                    product: {
                        ...product,
                        mainImage
                    }
                }, async () => {
                    const {product} = this.state;
                    const {mainImage} = product;
                    const {contentUrl} = mainImage;
                    {/** Download*/
                    }
                    if (contentUrl) {
                        await downloadFile(config, contentUrl)
                            .then(url => {
                                this.setState({
                                    product: {
                                        ...product,
                                        mainImage: {
                                            ...mainImage,
                                            contentUrl: url
                                        }
                                    }
                                })
                            })
                            .catch(error => console.log("Image Download => " + error))
                    }
                })
            })
            .catch(error => console.log("Image Get => " + error))
    }

    setNewValue(newValue) {
        this.setState({
            newValue
        })
    }

    /** Set input quantity to State*/
    setQty(quantity) {
        if (quantity > 0) {
            const {product} = this.state;
            const {index, updateList} = this.props;
            this.setState({
                product: {
                    ...product,
                    quantity
                }
            }, () => {
                const {index, updateProduct} = this.props;
                const shoppingList = getShoppingList();
                setShoppingList(shoppingList);
                updateProduct(this.state.product, index);

            });
        }
    }

    /** Here need to handle for cases with limited availability*/
    increaseQty() {
        const {product} = this.state;
        const {quantity} = product;
        const {index, updateProduct} = this.props;
        this.setState({
            product: {
                ...product,
                quantity: +quantity + +1
            }
        }, () => {
            updateProduct(this.state.product, index);
        });
    }

    /** Minimum quantity is 1 */
    decreaseQty() {
        const {product} = this.state;
        const {quantity} = product;
        const {index, updateProduct} = this.props;
        if (quantity > 1) {
            this.setState({
                product: {
                    ...product,
                    quantity: quantity - 1
                }
            }, () => {
                updateProduct(this.state.product, index);

            });
        }
    }

    clearQty() {
        this.setState({qty: 1});
    }

    render() {
        const {newValue, product} = this.state;
        const {index,} = this.props;
        const {identifier} = product;
        if (product && typeof product === 'string') {
            return (<div className="list-create-card__list-item list-create-card-ruled">
                <div className="list-create-card__action-left"
                     role="button"
                     style={{cursor: 'pointer'}}
                     title="Delete"
                     onClick={() => this.props.onDelete(index)}/>
                <div className="list-create-card__field  d-flex">
                    <input type="text" className="list-create-card__input" value={product}
                           placeholder="Add new item"/>
                </div>
            </div>);
        } else if (product && typeof product === 'object') {
            const {productName, productID, quantity, unitValue, productPrice, mainImage, generalName} = product;
            const {price, priceCurrency} = productPrice || {};
            const {unitText, value} = unitValue || {};
            const {contentUrl} = mainImage || {};
            return (<div className="list-create-card__list-item list-create-card-ruled list-create-card__has-items">
                <div className="list-create-card__action-left"
                     role="button"
                     title="Delete"
                     style={{cursor: 'pointer'}}
                     onClick={() => this.props.onDelete(index)}/>

                <div className="list-create-card__field d-flex">
                    <input type="text" className="list-create-card__input" readOnly value={generalName || ""}/>

                    <div
                        className="list-create-card__price">
                        {`${priceCurrency} ${price * quantity}`}

                    </div>

                </div>


                <div className="list-create-card__items mb-2">
                    <div className="list-create-card__item media">
                        <div className="list-create-card__item-image mr-3">
                            <img src={contentUrl ? contentUrl : DefaultImage} alt="Image1"
                                 onError={() => {
                                     console.log("Image error");
                                     // this.getMainImage(identifier);
                                 }}/>
                            <Link to={`/detail:${identifier}`}/>
                        </div>
                        <div className="media-body">
                            {/*{`${quantity} x ${productName}`}<br/>*/}
                            {`${productName}`}<br/>
                            <span
                                className="list-create-card__item-price">
                                {value} {unitText} &nbsp; - &nbsp;
                                {`${priceCurrency} ${price}`}
                                </span>
                            {/*<span className="list-create-card__input"*/}
                            {/*style={{float: "right"}}>{unitValue["value"]} {unitValue["unitText"]}</span>*/}
                            {/** Quantity add button*/}
                            <div
                                className="input-group flex-nowrap product-qty__select  product-qty__select--sm  ">
                                <div className="input-group-prepend">
                                    <button
                                        className="product-qty__btn product-qty__btn--left product-qty__btn--minus"
                                        onClick={() => {
                                            this.decreaseQty();
                                            // showSearchResults();
                                        }}
                                        type="button">-
                                    </button>
                                </div>
                                <input type="text" className="form-control product-qty__input"
                                    // defaultValue={quantity}
                                       value={quantity}
                                       onChange={(event) => {
                                           this.setQty(event.target.value)
                                       }}/>

                                <div className="input-group-append">
                                    <button
                                        className="product-qty__btn product-qty__btn--right product-qty__btn--plus"
                                        onClick={() => {
                                            this.increaseQty();
                                            // showSearchResults();
                                        }}
                                        type="button">+
                                    </button>
                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>);
        }
        else return (<div className="list-create-card__list-item list-create-card-ruled">
                <div className="list-create-card__action-left"
                     role="button"
                     style={{cursor: 'pointer'}}
                     title="Delete"
                     onClick={() => this.props.onDelete(index)}/>
                <div className="list-create-card__field  d-flex">
                    <input type="text" className="list-create-card__input"
                           placeholder="Add new item"/>
                </div>
            </div>);

    }
}


