import axios from "axios";
import {getAuthCode} from "./Local_Utilities";

const CONTEXT = "http://semantro.com/";

const TYPES = {
    "KIRANA_MUTATION": "KiranaMutation",
    "KIRANA_LIST": "KiranaList",
    "KIRANA_PRODUCT": "KiranaProduct",
    "KIRANA_ORDER": "KiranaOrder",
    "ORDER_QUANTITY": "OrderQuantity",
    "KIRANA_CATEGORY": "KiranaCategory",
    "KIRANA_COLLECTION": "KiranaCollection",
    "ITEMLIST_TEXT": ["ItemList", "Text"],
    "ITEMLIST_PRODUCT": ["ItemList", "KiranaProduct"],
    "ITEMLIST_CATEGORY": ["ItemList", "KiranaCategory"],
    "ITEMLIST_ORDERQUANTITY": ["ItemList", "OrderQuantity"],
    "QUANTITATIVE_VALUE": "QuantitativeValue",
    "PRODUCT_PRICE": "PriceSpecification",
    "KIRANA_TAG": "KiranaTag",
    "KIRANA_USER": "KiranaUser",
    "IMAGE_OBJECT": "ImageObject",
    "KIRANA_ADDRESS": "KiranaAddress",
    "CITY": "City",
    "GEO_COORDINATES": "GeoCoordinates",
    "KIRANA_OFFER": "KiranaOffer",
    "KIRANA_IMAGEOBJECT": "ImageObject",
    "USER_ROLE": "UserRole"

};

const ACTIONS = {
    "ADD_LIST": "addKiranaList",
    "ADD_ORDER": "createOrder",
    "GET_DISCOUNTED_AMOUNT": "getDiscountedAmount",

    "ADD_CUSTOMER": "addKiranaCustomer",
    "UPDATE_USER": "updateUser",
    "UPDATE_USER_ADDRESS": "updateUserAddress",
    "UPDATE_USER_CITY": "updateUserCity",
    "UPDATE_USER_COORDINATES": "updateUserCoordinates",
    "ADD_USER_IMAGE": "addUserImage",

};

// const LISTTYPE: {
//     "AUTO": "Auto",
//     "MANUAL": "Manual"
// };


export const ROLES = {
    "VENDOR": "VENDOR",
    "CUSTOMER": "CUSTOMER",
    "SYSTEM_USER": "SYSTEM_USER"
};


export function getUserImageFileData(mainImage, identifier) {
    return {
        data: {
            "@context": CONTEXT,
            "@type": TYPES.KIRANA_MUTATION,
            actionName: ACTIONS.ADD_USER_IMAGE,
            data: {
                "@context": CONTEXT,
                "@type": TYPES.KIRANA_USER,
                identifier,
                hasImage: {
                    "@context": CONTEXT,
                    "@type": TYPES.KIRANA_IMAGEOBJECT,
                    representativeOfPage: true
                }
            }
        },
        file: mainImage
    }
}

export function generateUserAddData(newUser) {
    delete  newUser.confirmPassword;
    return {
        "@context": CONTEXT,
        "@type": TYPES.KIRANA_MUTATION,
        actionName: ACTIONS.ADD_CUSTOMER,
        data: {
            "@context": CONTEXT,
            "@type": TYPES.KIRANA_USER,
            ...newUser,
            userName: newUser.telephone,
            hasRole: {
                "@context": CONTEXT,
                "@type": TYPES.USER_ROLE,
                roleType: ROLES.CUSTOMER
            }
        }
    }
}

export function generateUserUpdateData(newUser) {
    return {
        "@context": CONTEXT,
        "@type": TYPES.KIRANA_MUTATION,
        "actionName": ACTIONS.UPDATE_USER,
        data: {
            "@context": CONTEXT,
            "@type": TYPES.KIRANA_USER,
            ...newUser
        }
    }

}

export function generateUserCoordinatesData(newUser, identifier) {
    return {
        "@context": CONTEXT,
        "@type": TYPES.KIRANA_MUTATION,
        "actionName": ACTIONS.UPDATE_USER_ADDRESS,
        data: {
            "@context": CONTEXT,
            "@type": TYPES.KIRANA_USER,
            identifier,
            addressCoordinates: {
                "@context": CONTEXT,
                "@type": TYPES.GEO_COORDINATES,
                ...newUser
            }
        }
    }
}

export function generateUserCityData(newUser, identifier) {
    return {
        "@context": CONTEXT,
        "@type": TYPES.KIRANA_MUTATION,
        "actionName": ACTIONS.UPDATE_USER_ADDRESS,
        data: {
            "@context": CONTEXT,
            "@type": TYPES.KIRANA_USER,
            identifier,
            addressCity: {
                "@context": CONTEXT,
                "@type": TYPES.CITY,
                ...newUser
            }
        }
    }
}

export function generateUserAddressData(newUser, identifier) {
    return {
        "@context": CONTEXT,
        "@type": TYPES.KIRANA_MUTATION,
        "actionName": ACTIONS.UPDATE_USER_ADDRESS,
        data: {
            "@context": CONTEXT,
            "@type": TYPES.KIRANA_USER,
            identifier,
            hasAddress: {
                "@context": CONTEXT,
                "@type": TYPES.KIRANA_ADDRESS,
                ...newUser
            }
        }
    }
}


export function generateDiscountedAmountData(list) {
    return {
        "@context": CONTEXT,
        "@type": TYPES.KIRANA_MUTATION,
        "actionName": ACTIONS.GET_DISCOUNTED_AMOUNT,
        data: {
            "@context": CONTEXT,
            "@type": TYPES.ORDER_QUANTITY,
            itemListElement: list.map(item => {
                return {
                    "@context": CONTEXT,
                    "@type": TYPES.ORDER_QUANTITY,
                    ...item
                }
            })

        }
    }
}

export function generateOrderData(order, listId, checkOutList, customer) {
    const {userName} = customer;
    return {
        "@context": CONTEXT,
        "@type": TYPES.KIRANA_MUTATION,
        "actionName": ACTIONS.ADD_ORDER,
        "data": {
            "@context": CONTEXT,
            "@type": TYPES.KIRANA_ORDER,
            ...order,
            orderList: {
                "@context": CONTEXT,
                "@type": TYPES.KIRANA_LIST,
                listId
            },
            ofCustomer: {
                "@context": CONTEXT,
                "@type": TYPES.KIRANA_USER,
                ...customer
            },
            hasQuantity: {
                "@context": CONTEXT,
                "@type": TYPES.ITEMLIST_ORDERQUANTITY,
                itemListElement: checkOutList.map(product => {
                    const {productID, quantity, generalName, offer} = product;
                    const {identifier} = offer || {};
                    if (identifier) {
                        return {
                            "@context": CONTEXT,
                            "@type": TYPES.ORDER_QUANTITY,
                            productID,
                            quantity,
                            offerId: identifier,
                            generalName
                        }
                    } else {
                        return {
                            "@context": CONTEXT,
                            "@type": TYPES.ORDER_QUANTITY,
                            productID,
                            quantity,
                            generalName
                        }
                    }
                })
            }

        }
    }
}

export function generateListData(list, listName) {
    const tempList = [...list];
    return {
        "@context": CONTEXT,
        "@type": TYPES.KIRANA_MUTATION,
        "actionName": ACTIONS.ADD_LIST,
        "data": {
            "@context": CONTEXT,
            "@type": TYPES.KIRANA_LIST,
            "listType": "Auto",
            listName,
            containsProduct: {
                "@context": CONTEXT,
                "@type": TYPES.ITEMLIST_PRODUCT,
                itemListElement: tempList.map(product => {
                    {/** Delete product quantity because KiranaList contains only product Data*/
                    }
                    const {productID} = product;
                    return {
                        "@context": CONTEXT,
                        "@type": TYPES.KIRANA_PRODUCT,
                        productID
                    }
                })
            }
        }
    }
}

export function mutateValue(request, config) {
    const {C_MUTATION_END_POINT} = config;
    const formdata = new FormData();
    // const {data} = request;
    formdata.append("data", JSON.stringify(request));
    return new Promise((resolve, reject) => {
        axios.post(C_MUTATION_END_POINT, formdata)
            .then(response => {
                const {data} = response;
                if (typeof data === "string") {
                    const {status, message, identifier} = JSON.parse(data);
                    if (status === "success" || identifier) {
                        resolve(JSON.parse(data));
                    } else {
                        reject(message);
                    }
                } else {
                    const {status, message} = data;
                    if (status === "success") {
                        resolve(data);
                    } else {
                        reject(message);
                    }
                }
            })
            .catch(error => {
                // console.log(error);
                reject(error)
            });
    })
}


export function mutateValueWithToken(request, config) {
    const {C_MUTATION_END_POINT} = config;
    const formdata = new FormData();
    // const {data} = request;
    formdata.append("data", JSON.stringify(request));
    return new Promise((resolve, reject) => {
        const authCode = getAuthCode();
        if (authCode) {
            axios({
                method: 'POST',
                url: C_MUTATION_END_POINT,
                headers: {
                    "accessToken": authCode,
                    "Content-Type": "multipart/form-data"
                },
                data: formdata
            }).then(response => {
                const {data} = response;
                if (typeof data === "string") {
                    const {status, message, identifier} = JSON.parse(data);
                    if (status === "success" || identifier) {
                        resolve(JSON.parse(data));
                    } else {
                        reject(message);
                    }
                } else {
                    const {status, message} = data;
                    if (status === "success") {
                        resolve(data);
                    } else {
                        reject(message);
                    }
                }
            })
                .catch(error => {
                    // console.log(error);
                    reject(error)
                });
        }
    })
}

export function mutateFile(request, config) {
    const {C_MUTATION_END_POINT} = config;
    const formdata = new FormData();
    const {data, file} = request;
    formdata.append("data", JSON.stringify(data));
    formdata.append("file", file);
    return new Promise((resolve, reject) => {
        const authCode = getAuthCode();
        if (authCode) {
            axios({
                method: 'POST',
                url: C_MUTATION_END_POINT,
                headers: {
                    "accessToken": authCode,
                    "Content-Type": "multipart/form-data"
                },
                data: formdata
            }).then(response => {
                const {data} = response;
                resolve(data);
            }).catch(error => {
                console.log(error);
                reject("Mutation " + error)
            });
        }
    })
}

export function mutateList(list, config) {
    return new Promise((resolve, reject) => {
        Promise.all(list.map(request => {
                if (Object.keys(request).includes("file")) {
                    return mutateFile(request, config);
                } else {
                    return mutateValue(request["data"], config);
                }
            })
        )
            .then(values => {
                console.log(values);
                resolve(values);
            })
            .catch(error => {
                console.log(error);
                reject(error);
            })
    })
}

export function loginUser(request, config) {
    const {C_LOGIN_END_POINT} = config;
    const formdata = new FormData();
    formdata.append("data", JSON.stringify(request));
    return new Promise((resolve, reject) => {
        axios.post(C_LOGIN_END_POINT, formdata)
            .then(response => {
                const {data} = response;
                console.log(data);
                if (typeof data === "string") {
                    const {loginStatus, authenticationCode} = JSON.parse(data);
                    if (loginStatus === "success" || authenticationCode) {
                        resolve(JSON.parse(data));
                    } else {
                        reject(loginStatus);
                    }
                } else {
                    const {loginStatus, authenticationCode} = data;
                    if (loginStatus === "success" || authenticationCode) {
                        resolve(data);
                    } else {
                        reject(loginStatus);
                    }
                }
            })
            .catch(error => {
                // console.log(error);
                reject(error)
            });
    })
}



