                                {/*/!*{*!/*/}
                                {/*/!*subtotal*!/*/}
                                {/*/!*? <span*!/*/}
                                {/*/!*className="cart__amount d-none d-lg-flex">{priceCurrency} {subtotal}</span>*!/*/}
                                {/*/!*: null*!/*/}
                                {/*/!*}*!/*/}
                                {/*</div>*/}

