import React from "react";
import listBackgroundImage from "../style/images/list_background_1.png";
import {downloadFile, requestList, requestValue} from "../helpers/request_helpers";
import {Link} from "react-router-dom";
import {DefaultImage, DefaultListImage} from "../StaticContent/staticContents";
import {expandOverflow, originalClass} from "../helpers/Utilities";
import {LOCAL_CONSTANTS} from "../helpers/Constants";

export class ListPreview extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            list: {
                mainImage: {
                    contentUrl: ""
                }
            },
            productList: []
        };
        this.setListProducts = this.setListProducts.bind(this);
        this.getMainImage = this.getMainImage.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        const {list} = this.props;
        this.setState({
            list
        }, () => {
            this.setListProducts();
            this.getMainImage();
        })
    }

    setListProducts() {
        const {config, query, list} = this.state;
        const {identifier} = list;
        const {listKiranaListProducts} = query;
        const {data} = listKiranaListProducts;
        requestList(Object.assign({}, listKiranaListProducts, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(data => {
                this.setState({
                    productList: data
                })
            })
            .catch(error => console.log(error));
    }

    getMainImage() {
        const {config, query, list} = this.state;
        const {getMainImageKiranaList} = query;
        const {data} = getMainImageKiranaList;
        const {identifier, listName} = list;
        requestValue(Object.assign({}, getMainImageKiranaList, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(async mainImage => {
                const {contentUrl} = mainImage;
                if (contentUrl) {
                    await downloadFile(config, contentUrl)
                        .then(url => {
                            this.setState({
                                mainImage: {
                                    ...mainImage,
                                    contentUrl: url
                                }
                            }, () => {
                                console.log("IMAGE SET ", this.state.list.mainImage);
                            })
                        })
                }
            })
            .catch(error => {
                console.log(listName + "Image ", error);
            })

    }

    render() {
        const {list, productList} = this.state;
        const {index, handleSearchConfirm} = this.props;
        const {listName, identifier, mainImage} = list || {};
        const {contentUrl} = mainImage || {};
        let tempListName = "";
        if (!listName) {
            tempListName = "Kirana List ";
        }
        return (
            <li className="col-xl-3 col-lg-4 col-md-6"
                style={{width: "50%"}}
            >
                <div className="nav-item view-list-collapse__li">
                    <div className="list-card"
                         style={{
                             // boxShadow: '0 2px 4px 0 rgba(0, 0, 0, 0.1), 0 6px 20px 0 rgba(0, 0, 0, 0.1)',
                         }}>

                        <Link to={`/search/KiranaList/${listName || tempListName}/${identifier}`}
                              title="Click to view products"
                        >
                            <div className="list-card__head"
                                 style={{
                                     backgroundImage: contentUrl ? `url${contentUrl}` : `url(${DefaultListImage}`,
                                     backgroundPosition: "center",
                                     // backgroundSize : "auto 100%",
                                     backgroundRepeat: "no-repeat"

                                 }}>
                                <div className="list-card__container">

                                    <h2 className="list-card__title">{listName || tempListName}</h2>


                                    <span className="list-card__info">{`${productList.length} items`}</span>
                                </div>
                            </div>
                        </Link>

                        <div style={{paddingLeft: '1rem', paddingRight: '1rem'}}>
                            <div className={`list-card__list-detail text-muted ${originalClass}`}
                                 id={identifier}
                                 onClick={() => expandOverflow(identifier)}>
                                {
                                    productList.length ?
                                        <span>
                                        Contains &nbsp;
                                            {productList.map((item) => {

                                                let productName = item.productName.split(" ")[0];
                                                return productName.charAt(0) + productName.substring(1).toLowerCase();
                                            })
                                                .filter((item, index) => index <= 7)
                                                .join(", ")}
                                            &nbsp; {productList.length > 3 ? `and more` : null}
                                    </span>
                                        : null
                                }
                                {/*Contains Pril, Mr Muscles, Dettol, Harpic, Lizol and more*/}
                            </div>

                            <div className="list-card__footer d-flex align-items-center justify-content-between">
                                <Link to={`/search/KiranaList/${listName || tempListName}/${identifier}`}
                                      title="Click to view products"
                                >
                                    {/*<i className="icon icon-add"/> */}
                                    &nbsp;View this list</Link>
                                {/*<span className="small text-muted">2108 users</span>*/}
                            </div>
                        </div>
                    </div>

                </div>
                {/*<ListProductPreview/>*/}

            </li>
        );
    }
}


