import React, {Component} from "react";
import {LOCAL_CONSTANTS} from "../helpers/Constants";
import {mutateValueWithToken} from "../helpers/mutation_helpers";
import sha256 from "js-sha256";

const OLD_PASSWORD = "oldPassword";
const USER_PASSWORD = "userPassword";
const CONFIRM_PASSWORD = "confirmPassword";


export class UpdatePassword extends Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            userName: "",
            userPassword: "",
            oldPassword: "",
            confirmPassword: "",
            errorVisible: false,
            errorMessage: ""
        }
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        const userName = localStorage.getItem(LOCAL_CONSTANTS.USERNAME);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message),
                userName
            })
        }
    }

    componentDidMount() {
        const userPassword = document.getElementById(USER_PASSWORD);
        const confirmPassword = document.querySelector("input")

        confirmPassword.addEventListener('input', () => {
            confirmPassword.setCustomValidity("");
            confirmPassword.checkValidity();
        });

        confirmPassword.addEventListener('invalid', () => {
            if (confirmPassword.value === "") {
                confirmPassword.setCustomValidity("Enter confirm password!")
            } else {
                confirmPassword.setCustomValidity("Passwords don't match. Please enter again!")
            }
        })

    }

    changeFieldValue(fieldName, value) {
        this.setState({
            [fieldName]: value
        })
    }

    handleSubmit() {
        const {closeModal} = this.props;
        const {
            userName, oldPassword, userPassword, confirmPassword,
            config, query, message
        } = this.state;
        const {changeUserPassword} = query;
        const {data} = changeUserPassword;
        if (userPassword === confirmPassword) {
            this.setState({
                errorVisible: false
            });
            const mutationData = Object.assign({}, changeUserPassword, {
                data: {
                    ...data,
                    userName,
                    userPassword : sha256.hex(userPassword),
                    oldPassword: sha256.hex(oldPassword)
                }
            })
            console.log(mutationData);
            mutateValueWithToken(mutationData, config)
                .then(response => {
                    console.log(response)
                })
                .catch(error => {
                    console.log(error)
                })
        } else {
            const {passwordsDontMatch} = message;
            this.setState({
                errorMessage: passwordsDontMatch,
                errorVisible: true
            })
        }
    }

    render() {
        const {closeModal} = this.props;
        const {
            userName, userPassword, oldPassword, confirmPassword,
            errorVisible, errorMessage
        } = this.state;
        return (
            <form
                onSubmit={(event) => {
                    event.preventDefault();
                    this.handleSubmit();
                }}
                style={{
                    padding: "20px"
                }}
            >
                <label className="title caps">Account</label>

                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor={OLD_PASSWORD}>Current Password</label>
                        <input type="password" className="form-control p-3" aria-describedby="name"
                               id={OLD_PASSWORD}
                               value={oldPassword}
                               onChange={(event) => this.changeFieldValue(OLD_PASSWORD, event.target.value)}
                        />

                    </div>
                </div>

                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor={USER_PASSWORD}>New Password</label>
                        <input type="password" className="form-control p-3" aria-describedby="name"
                               id={USER_PASSWORD}
                               value={userPassword}
                               onChange={(event) => this.changeFieldValue(USER_PASSWORD, event.target.value)}
                        />

                    </div>
                </div>

                <div className="form-row">
                    <div className="form-group col-md-6">
                        <label htmlFor={CONFIRM_PASSWORD}>Confirm Password</label>
                        <input type="password" className="form-control p-3" aria-describedby="name"
                               id={CONFIRM_PASSWORD}
                               value={confirmPassword}
                               onChange={(event) => this.changeFieldValue(CONFIRM_PASSWORD, event.target.value)}
                        />
                    </div>
                </div>

                {errorVisible
                    ? <div className="alert alert-danger" role="alert">
                        {errorMessage}
                    </div>
                    : null
                }

                <div className="row">
                    {/*<div className="col-md-3"/>*/}
                    <div className="col-md-6">
                        <button type="submit"
                                disabled={!(oldPassword && userPassword && confirmPassword)}
                                className="btn btn-primary btn-md btn-block sidebar__btn-place-order"
                                data-toggle="modal" data-target="#loginModal">
                            <span><i className="fas fa-save"/></span>
                            &nbsp;&nbsp;&nbsp;Save Changes
                        </button>
                    </div>
                    <div className="col-md-6">
                        <button onClick={closeModal}
                                className="btn btn-md btn-block sidebar__btn-place-order"
                                data-toggle="modal" data-target="#loginModal">
                            <span><i className="fas fa-times"/></span>
                            &nbsp;&nbsp;&nbsp;Cancel
                        </button>
                    </div>
                    {/*<div className="col-md-3"/>*/}

                </div>
            </form>
        );
    }
}


