import React, {Component} from "react";
import {setTopMargin} from "../helpers/Utilities";
import MenuBar from "../Menubar/Menubar";
import {requestList, requestValue} from "../helpers/request_helpers";
import {getProductMainImage} from "../helpers/Utilities";
import {LOCAL_CONSTANTS} from "../helpers/Constants";
import {Link} from "react-router-dom";
import DefaultImage from "../style/images/favicon_b&w.jpg";

export class OrderDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            topMargin: undefined,
            order: {},
            productList: [],
            listName : ""

        };
        this.setTopMargin = setTopMargin.bind(this);
        this.getProductMainImage = getProductMainImage.bind(this);
        this.setProductRowData = this.setProductRowData.bind(this);
        this.getOrderProducts = this.getOrderProducts.bind(this);
        this.getOrderName = this.getOrderName.bind(this);

    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        this.setTopMargin();
        const {identifier} = this.props;
        if (identifier) {
            this.setState({
                order: {
                    identifier
                }
            }, () => {
                const {order} = this.state;
                const {identifier} = order;
                this.getOrderProducts(identifier);
                this.getOrderName(identifier)
                    .then(listName => {
                        this.setState({
                            listName
                        })
                    })

            })
        }
    }

    getOrderProducts = (identifier) => {
        const {config, query} = this.state;
        const {listOrderedProducts} = query;
        const {data} = listOrderedProducts;
        let tempList = [];

        requestList(Object.assign({}, listOrderedProducts, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(productlist => {
                productlist.map((orderQuantity, index) => {
                    const {productID, quantity} = orderQuantity;
                    this.setProductRowData(productID)
                        .then(data => {
                            tempList.splice(index, 0, {
                                ...data,
                                quantity
                            });
                            this.setState({
                                productList: tempList
                            })
                        })
                        .catch(error => console.log(error))
                })
            })
    };

    setProductRowData = (productID) => {
        const {config, query} = this.state;
        const {getProductDetailById} = query;
        const {data} = getProductDetailById;
        return new Promise((resolve, reject) => {

            requestValue(Object.assign({}, getProductDetailById, {
                data: {
                    ...data,
                    productID
                }
            }), config)
                .then(async detail => {
                    const {identifier, productName} = detail;
                    await this.getProductQValue(productID)
                        .then(async unitValue => {
                            await this.getProductPrice(productID)
                                .then(productPrice => {
                                    this.getProductMainImage(identifier)
                                        .then(mainImage => {
                                            resolve({
                                                identifier,
                                                productName,
                                                unitValue,
                                                productPrice,
                                                mainImage
                                            })
                                        })
                                })
                                .catch(error => reject(error))
                        })
                        .catch(error => reject(error))

                })
                .catch(error => reject(error))
        })
    };

    getProductPrice = (productID) => {
        const {config, query} = this.state;
        const {getProductPrice} = query || {};
        const {data} = getProductPrice;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getProductPrice, {
                data: {
                    ...data,
                    productID
                }
            }), config)
                .then(data => {
                    resolve(data);
                })
                .catch(error => {
                    console.log("Error in UnitValue");
                    reject(error);
                })
        })
    };

    getProductQValue = (productID) => {
        const {config, query} = this.state;
        const {getProductQValue} = query || {};
        const {data} = getProductQValue;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getProductQValue, {
                data: {
                    ...data,
                    productID
                }
            }), config)
                .then(data => {
                    resolve(data);
                })
                .catch(error => {
                    console.log("Error in UnitValue");
                    reject(error);
                })
        })
    };

    getOrderName(identifier) {
        const {config, query} = this.state;
        const {getOrderName} = query;
        const {data} = getOrderName;
        return new Promise((resolve, reject) => {
            requestValue(Object.assign({}, getOrderName, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(response => {
                    const {listName} = response;
                    // if (listName) {
                    resolve(listName);
                    // } else {
                    //     reject("No list name");
                    // }
                })
                .catch(error => reject(error))
        })
    }
    render() {
        const {handleSearchConfirm, showShoppingList} = this.props;
        const {topMargin, productList, listName} = this.state;
        return (
            <div className="all-page">
                <div style={{flex: "auto", marginTop: `${topMargin}px`}}/>

                <div className="layout">
                    <div className="page-wrapper">
                        <div className="container">
                            <div className="row profile">
                                <div className="col-md-9">
                                    <div className="profile__info float-left">
                                        <h1 className="h5">{listName ? listName : "Kirana List"}</h1>
                                    </div>
                                </div>
                                <div className="col-md-2">
                                    <a href="javascript:void(0)" className="btn btn-outline-primary btn-block"
                                       onClick={() => {
                                           productList.length
                                           ? productList.map(product => showShoppingList(product))
                                               : null
                                       }}
                                    >Order This List</a>
                                </div>
                            </div>

                            <div className="row">
                                <div className="col-md-12">
                                    <div className="table-title mt-3 float-left"><label className="title caps">Shopping
                                        List</label></div>
                                    <table className="table table-striped table__history">
                                        <thead>
                                        <tr>
                                            <th scope="col">Item Name</th>
                                            <th scope="col">Quantity</th>
                                            <th scope="col">per Amount</th>
                                            <th scope="col">Total Amount</th>
                                        </tr>
                                        </thead>
                                        <tbody>
                                        {
                                            productList.length
                                                ? productList.map((product, index) => {
                                                    if (index % 2 === 0) {
                                                        return <EvenRow product={product} key={index}/>

                                                    } else {
                                                        return <OddRow product={product} key={index}/>

                                                    }
                                                })
                                                : null
                                        }
                                        </tbody>
                                    </table>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const EvenRow = (props) => {
    const {product} = props;
    const {identifier,productName, quantity, unitValue, productPrice, mainImage} = product;
    const {unitText, value} = unitValue;
    const {price, priceCurrency} = productPrice;
    const {contentUrl} = mainImage;
    return <tr>
        <td>
            <div className="list-create-card__item media">
                <div className="list-create-card__item-image mr-3">
                    <img src={contentUrl ? contentUrl : DefaultImage} alt="Image"/>
                    <Link to={`/detail:${identifier}`}/>
                </div>
                <div className="media-body">
                    <p>{productName}<br/>
                        <small className="dim">{value}{unitText}</small>
                    </p>
                </div>
            </div>
        </td>
        <td>{quantity}</td>
        <td>{priceCurrency} {price}</td>
        <td><strong>{priceCurrency}{quantity * price}</strong>
        </td>
    </tr>
};

const OddRow = (props) => {
    const {product} = props;
    const {identifier,productName, quantity, unitValue, productPrice,mainImage} = product;
    const {unitText, value} = unitValue;
    const {price, priceCurrency} = productPrice;
    const {contentUrl} = mainImage;
    return <tr>
        <td>
            <div className="list-create-card__item media">
                <div className="list-create-card__item-image mr-3">
                    <img src={contentUrl ? contentUrl : DefaultImage} alt="Image"/>
                    <Link to={`/detail:${identifier}`}/>
                </div>
                <div className="media-body">
                    <p>{productName}<br/>
                        <small className="dim">{value}{unitText}</small>
                    </p>
                </div>
            </div>
        </td>
        <td>{quantity}</td>
        <td>{priceCurrency} {price}</td>
        <td><strong>{priceCurrency}{quantity * price}</strong>
        </td>
    </tr>
}


