import React, {Component, Suspense, lazy} from "react";
import {Route, Switch, HashRouter, BrowserRouter, Router} from "react-router-dom";
import {ProductDetail} from "../ProductDetail/ProductDetail";
import {CheckOut} from "../CheckOut/CheckOut";
import {Headers} from "../HeaderSection/Headers";
import {Footer} from "../Footer/Footer";
import {LandingPage} from "../LandingPage/LandingPage";
import ShoppingList from "../ShoppingList/ShoppingList";
import {SearchResult} from "../SearchResult/SearchResult";
import {ProductAddAlert} from "../Message/ProductAddAlert";
import {
    LoadingComponent, hideModal,
} from "../StaticContent/staticContents";
import {LoginPage} from "../Login/LoginPage";
import {_handleKeyDown, redirectToPath, startTawkTo} from "../helpers/Utilities";
import {Profile} from "../Profile/Profile";
import {OrderDetail} from "../OrderDetail/OrderDetail";
import {startLoyaltyScript} from "../Loyalty Lion/loyalty_helpers";
import {
    getCheckoutList, setCheckoutList,
    getShoppingList, hideLoginModal, login, logout,
    setShoppingList, setSearchConfirmStatus,
} from "../helpers/Local_Utilities";
import {IDENTIFIERS, LOCAL_CONSTANTS,} from "../helpers/Constants";

export const MAINPAGE_ID = "mainpage";

// const LandingPage = lazy(() => import("../LandingPage/LandingPage"))

export class MainPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            query: undefined,
            config: undefined,
            message: undefined,
            root: {
                rootPath: undefined
            },
            yPosition: 0,
            opacity: 1,
            headerHeight: 0,
            productToAdd: {},
            undo: false,
            productAdded: false,
            searchConfirmed: false,
            searchList: [],
            subtotal: 0,
            cartCount: 0

        };
        this.showShoppingList = this.showShoppingList.bind(this);
        this.clearProductAdded = this.clearProductAdded.bind(this);
        this.handleSearchConfirm = this.handleSearchConfirm.bind(this);
        this.getSearchList = this.getSearchList.bind(this);
        this.hideModal = hideModal.bind(this);

    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        const root = localStorage.getItem(LOCAL_CONSTANTS.ROOT);
        if (config && query && message && root) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message),
                root: JSON.parse(root)
            })
        }
        // startLoyaltyScript();
        startTawkTo();
    }

    componentDidMount() {
        if (!getShoppingList()) {
            setShoppingList([]);
        }
        if (!getCheckoutList()) {
            setCheckoutList([]);
        }
        document.addEventListener("keydown", _handleKeyDown.bind(this));

    }

    componentWillUnmount() {
        this.clearProductAdded();
    }

    showShoppingList = (product) => {
        const text = ' homepage-sidebar--open';
        const id = 'shopping-list';
        if (!document.getElementById(id).className.includes(text)) {
            document.getElementById(id).className += text;
        }
        // if (product) {
        this.setState({
            productToAdd: product
        }, () => {

        });
        // }
    };

    clearProductAdded = () => {
        setTimeout(() => {
            this.setState({
                ...this.state,
                productToAdd: {}
            })
        }, 2000);
    };

    showAlert() {
        const id = "product-add-alert";
        const text = " show";
        const element = document.getElementById(id);
        if (!document.getElementById(id).className.includes(text)) {
            document.getElementById(id).className += text;
        }
    }

    handleUndo = (undo) => {
        this.setState({undo})
    };

    handleProductAdd = (added) => {
        this.setState({productAdded: added})
    };

    handleSearchConfirm = (confirm) => {
        setSearchConfirmStatus(confirm);
    };

    getSearchList = (list) => {
        const {root} = this.state;
        const {rootPath} = root;
        this.setState({
            searchList: list
        }, () => {
            const {searchList} = this.state;
            redirectToPath(`#${rootPath}/search/KiranaProduct/${searchList[0]}/${0} `)
            // const link = document.createElement('a');
            //
            // link.href = `/search/KiranaProduct/${searchList[0]}/${0} `;
            // link.click();
        })
    };

    clearShoppingList = (clearList) => {
        this.clearThisList = clearList;
    };

    render() {
        const {
            opacity, headerHeight, productToAdd,
            undo, checkoutList, query, config,
            searchConfirmed, searchList, message, root
        } = this.state;
        const {rootPath} = root;
        return (
            <div id={MAINPAGE_ID}
                 className="notification-bar-enabled " style={{overflowY: "auto"}}>
                <HashRouter basename={rootPath}>
                    <Switch>
                        <div>
                            <Headers headerOpacity={opacity}
                                     headerHeight={headerHeight}
                                     showShoppingList={this.showShoppingList}
                                     handleUndo={this.handleUndo}
                                     handleSearchConfirm={this.handleSearchConfirm}
                            />
                            <Route exact path="/"
                                   hasChildren
                                   render={() =>
                                       <LandingPage topMargin={headerHeight}
                                                    showShoppingList={this.showShoppingList}
                                                    handleUndo={this.handleUndo}
                                                    handleSearchConfirm={this.handleSearchConfirm}

                                       />}/>
                            <Route exact path="/checkPayment:status/checkout:listName"
                                   hasChildren
                                   render={({match}) => {
                                       const {listName, status} = match.params;
                                       return <CheckOut
                                           topMargin={headerHeight}
                                           // checkoutList={checkoutList}
                                           showShoppingList={this.showShoppingList}
                                           headerOpacity={opacity}
                                           listName={listName.replace(":", "")}
                                           handleSearchConfirm={this.handleSearchConfirm}
                                           checkPaymentStatus={status.replace(":", "")}
                                       />
                                   }}/>
                            <Route exact path="/search/:type/:name/:identifier"
                                   hasChildren
                                   render={({match}) => {
                                       const {type, name, identifier} = match.params;
                                       return <SearchResult
                                           topMargin={headerHeight}
                                           showShoppingList={this.showShoppingList}
                                           queryObject={{
                                               type,
                                               name,
                                               identifier
                                           }}
                                           queryString={"Query "}
                                           handleUndo={this.handleUndo}
                                           searchConfirmed={searchConfirmed}
                                           handleSearchConfirm={this.handleSearchConfirm}
                                           // searchList={searchList}
                                       />
                                   }
                                   }/>
                            <Route exact path="/detail:identifier"
                                   hasChildren
                                   render={({match}) => <ProductDetail
                                       topMargin={headerHeight}
                                       identifier={match.params.identifier.replace(":", "")}
                                       showShoppingList={this.showShoppingList}
                                       handleUndo={this.handleUndo}
                                       handleSearchConfirm={this.handleSearchConfirm}
                                   />}/>
                            <Route exact path="/profile"
                                   hasChildren
                                   render={() => <Profile
                                       handleSearchConfirm={this.handleSearchConfirm}
                                   />}/>
                            <Route exact path="/orderdetail:identifier"
                                   hasChildren
                                   render={({match}) => {
                                       const {identifier} = match.params;
                                       return <OrderDetail
                                           identifier={identifier.replace(":", "")}
                                           handleSearchConfirm={this.handleSearchConfirm}
                                           showShoppingList={this.showShoppingList}

                                       />
                                   }}/>
                            {/*<Route exact path="/payment/:status/:telephone"*/}
                            {/*render={({match}) => {*/}
                            {/*const {status, telephone} = match;*/}
                            {/*console.log("Matched ", status, telephone);*/}
                            {/*return <PaymentMessage paymentStatus={status} telephone={telephone}/>*/}
                            {/*}}/>*/}
                            <Footer showShoppingList={this.showShoppingList}/>
                            <ShoppingList topMargin={headerHeight}
                                          productToAdd={productToAdd}
                                          showAlert={this.showAlert}
                                          showShoppingList={this.showShoppingList}
                                          undo={undo}
                                          handleUndo={this.handleUndo}
                                          clearProductAdded={this.clearProductAdded}
                                          searchConfirmed={searchConfirmed}
                                          handleSearchConfirm={this.handleSearchConfirm}
                                          getSearchList={this.getSearchList}
                                          clearShoppingList={this.clearShoppingList}
                            />
                            <ProductAddAlert handleUndo={this.handleUndo}
                                             clearProductAdded={this.clearProductAdded}
                                             productToAdd={productToAdd}/>

                        </div>
                    </Switch>
                </HashRouter>
                <div className="modal fade show" id={IDENTIFIERS.LOGIN_MODAL} tabIndex="-1" role="dialog"
                     aria-labelledby={IDENTIFIERS.LOGIN_MODAL} style={{display: "none"}}>
                    <div className="modal-dialog modal-sm" role="document">
                        <div className="modal-content">
                            <div className="modal-header modal-header--close-only">
                                <button type="button"
                                        onClick={() => hideModal(IDENTIFIERS.LOGIN_MODAL)} className="close"
                                        data-dismiss="modal"
                                        aria-label="Close">
                                    <span aria-hidden="true"></span>
                                </button>
                            </div>
                            <div className="modal-body">
                                {/*<p className="text-center">Hello! <br/>*/}
                                {/*Please enter your account details.*/}
                                {/*</p>*/}
                            </div>
                            <LoginPage/>
                        </div>
                    </div>

                </div>
                <div className="btn-float" id="floating-btn__homepage_list_open"
                     onClick={() => this.showShoppingList()}>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 33 33">
                        <path
                            d="M27.049 30.134H5.951c-.99 0-1.81-.743-1.908-1.728L2.659 14.537a.498.498 0 0 1 .498-.55h26.688a.5.5 0 0 1 .498.55L28.96 28.406a1.916 1.916 0 0 1-1.911 1.728zM3.708 14.988l1.329 13.319a.914.914 0 0 0 .913.827h21.099a.915.915 0 0 0 .914-.827l1.329-13.319H3.708zM7.604 9.106a.497.497 0 0 1-.383-.179.498.498 0 0 1 .061-.704l6.246-5.24a.5.5 0 1 1 .642.766l-6.246 5.24a.498.498 0 0 1-.32.117zM25.397 9.106a.502.502 0 0 1-.321-.117l-6.247-5.24a.502.502 0 0 1-.062-.705.502.502 0 0 1 .705-.062l6.247 5.24a.502.502 0 0 1-.322.884z"/>
                        <path
                            d="M31.275 14.988H1.725A1.727 1.727 0 0 1 0 13.263v-3.198C0 9.114.774 8.34 1.725 8.34h29.55c.951 0 1.725.774 1.725 1.725v3.198c0 .951-.773 1.725-1.725 1.725zM1.725 9.339c-.4 0-.725.325-.725.725v3.198c0 .4.325.725.725.725h29.55a.726.726 0 0 0 .725-.725v-3.198c0-.4-.325-.725-.725-.725H1.725zM9.255 26.048a.5.5 0 0 1-.5-.5v-7.25a.5.5 0 0 1 1 0v7.25c0 .277-.223.5-.5.5zM14.085 26.048a.5.5 0 0 1-.5-.5v-7.25a.5.5 0 0 1 1 0v7.25c0 .277-.223.5-.5.5zM18.915 26.048a.5.5 0 0 1-.5-.5v-7.25a.5.5 0 0 1 1 0v7.25a.5.5 0 0 1-.5.5zM23.745 26.048a.5.5 0 0 1-.5-.5v-7.25a.5.5 0 0 1 1 0v7.25a.5.5 0 0 1-.5.5z"/>
                    </svg>
                    <div>
                    </div>
                </div>
                <div className="modal fade show" id={IDENTIFIERS.CLEAR_MODAL} tabIndex="-1" role="dialog"
                     aria-labelledby={IDENTIFIERS.CLEAR_MODAL}
                     style={{paddingRight: "16px", display: "none"}}>
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title" id="exampleModalLabel">Are you sure you want to clear the
                                    List?</h5>
                                <button type="button" className="close"
                                        onClick={() => this.hideModal(IDENTIFIERS.CLEAR_MODAL)}
                                        data-dismiss="modal" aria-label="Close">
                                    <span aria-hidden="true"></span>
                                </button>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary"
                                        onClick={() => this.hideModal(IDENTIFIERS.CLEAR_MODAL)}
                                        data-dismiss="modal">No, thanks!
                                </button>
                                <button type="button" className="btn btn-primary"
                                        onClick={this.clearThisList}
                                >Yes
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}


