import React, {Component} from "react";
import RightImage from "../style/images/promotion_bg2.png";
import LeftImage from "../style/images/promotion_bg.png";
import {downloadFile, requestValue} from "../helpers/request_helpers";
import {Link} from "react-router-dom";
import {LOCAL_CONSTANTS} from "../helpers/Constants";

{/** Currently this banner is for bannerCategory only.*/
}

export class RightImageBanner extends Component {
    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            category: {
                mainImage: {}
            },
        };
        this.getMainImage = this.getMainImage.bind(this);
    }


    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        const {showShoppingList, category} = this.props;
        if (category) {
            this.setState({
                category
            }, () => {
                this.getMainImage();
            })
        }
    }

    // componentWillReceiveProps(nextProps) {
    //     const {showShoppingList, config, query, category} = nextProps;
    //     config && query && category && this.setState({
    //         config,
    //         query,
    //         category
    //     }, () => {
    //         console.log("Category ", this.state.category);
    //         this.getMainImage();
    //     })
    // }

    getMainImage() {
        const {config, query, category} = this.state;
        const {getMainImageCategory} = query;
        const {identifier, categoryName} = category;
        const {data} = getMainImageCategory;
        return new Promise((resolve, reject) => {

            requestValue(Object.assign({}, getMainImageCategory, {
                data: {
                    ...data,
                    identifier
                }
            }), config)
                .then(mainImage => {
                    // console.log(productName, mainImage);
                    this.setState({
                        category: {
                            ...category,
                            mainImage
                        }
                    }, async () => {
                        const {category} = this.state;
                        const {mainImage} = category;
                        // console.log("Category image ", mainImage);
                        const {contentUrl} = mainImage;
                        {/** Download*/
                        }
                        if (contentUrl) {
                            await downloadFile(config, contentUrl)
                                .then(url => {
                                    this.setState({
                                        category: {
                                            ...category,
                                            mainImage: {
                                                ...mainImage,
                                                contentUrl: url
                                            }
                                        }
                                    }, () => resolve("Success"))
                                })
                                .catch(error => console.log(categoryName + " Image Download => " + error))
                        } else {
                            resolve("Done")
                        }
                    })
                })
                .catch(error => {
                    console.log(categoryName + " Image Get => " + error);
                    reject(error);
                })
        })
    }

    render() {
        const {showShoppingList, config, query} = this.props;
        const {category} = this.state;
        const {categoryName, identifier, description, mainImage} = category;
        const {contentUrl} = mainImage || {};
        return (
            <div className="offer-banner offer-banner--image-right offer-banner--dark offer-banner-1-0"
                 style={{backgroundImage: `url(${contentUrl})`}}>
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6 offer-banner__info text-lg-left text-center">
                            <h3 className="offer-banner__title">{categoryName}</h3>
                            <p className="offer-banner__desc">{description}</p>
                            <div className="offer-banner__cta">
                                <Link to={`search/KiranaCategory/${categoryName}/${identifier}`}
                                      className="btn btn-light"
                                    // onClick={showShoppingList}
                                >
                                    View Category
                                </Link>

                                {/*<a href="#" className="btn btn-link btn-link-light">Learn More</a>*/}

                            </div>
                        </div>
                        <div className="col-lg-6"/>
                    </div>
                </div>
            </div>
        )
    }
}

