import React, {Component} from "react";
import {Link} from "react-router-dom";
import moment from "moment";
import {PRICE_CURRENCY} from "../helpers/Constants";

export class CurrentOrderTrack extends Component {
    constructor(props) {
        super(props);
        this.state = {
            order: {}
        };
    }

    componentDidMount() {
        const {order} = this.props;
        if (order) {
            this.setState({
                order
            })
        }
    }

    render() {
        const {order} = this.state;
        const {orderStatus, orderAmount, identifier, orderNumber, orderDate} = order;
        const temp = orderStatus ? orderStatusActive[orderStatus] : {};
        const {done, todo} = temp || {};
        return (
            <div className="current-order">
                <ol className="progtrckr" data-progtrckr-steps="5">
                    {
                        done
                            ? done.map(doneStatus => <li className="progtrckr-done">{doneStatus}</li>)
                            : null
                    }
                    {
                        todo
                            ? todo.map(todoStatus => <li className="progtrckr-todo">{todoStatus}</li>)
                            : null
                    }

                </ol>
                <br/>
                <div className="row">
                    <div className="col-md-3">
                        <label className="mr-3 small dim">Order Number</label>
                        <span>{orderNumber}</span><br/>
                    </div>
                    <div className="col-md-3">
                        <label className="mr-3 small dim">Ordered Date</label>
                        <span>{moment(orderDate).format("ll")}</span><br/>
                    </div>
                    <div className="col-md-3">
                        <label className="mr-3 small dim">Order Amount</label>
                        <span>{PRICE_CURRENCY} {orderAmount}</span><br/>
                    </div>
                    <div className="col-md-2">
                        <Link to={`/orderdetail:${identifier}`}
                              className="btn btn-outline-primary btn-block">View this Order</Link>

                    </div>
                </div>
            </div>

        );
    }
}

const orderStatusActive = {
    "OrderPlaced": {
        done: ["Placed"],
        todo: ["Confirmed", "Packaged", "Shipment", "Delivered"]
    },
    "OrderModified": {
        done: ["Placed"],
        todo: ["Confirmed", "Packaged", "Shipment", "Delivered"]
    },
    "OrderConfirmed": {
        done: ["Placed", "Confirmed"],
        todo: ["Packaged", "Shipment", "Delivered"]
    },
    "OrderFulfilled": {
        done: ["Placed", "Confirmed"],
        todo: ["Packaged", "Shipment", "Delivered"]
    },
    "OrderPackaged": {
        done: ["Placed", "Confirmed", "Packaged"],
        todo: ["Shipment", "Delivered"]
    },
    "OrderShipment": {
        done: ["Placed", "Confirmed", "Packaged", "Shipment"],
        todo: ["Delivered"]
    },
    "OrderDelivered": {
        done: ["Placed", "Confirmed", "Packaged", "Shipment", "Delivered"],
        todo: []
    },
};


