import {downloadFile, requestValue} from "./request_helpers";
import {CLASSNAMES} from "./Constants";
import {NAV_ID, TOP_ID} from "../Menubar/Menubar";
import {HEADER_ID} from "../HeaderSection/Headers";
import {MAINPAGE_ID} from "../MainPage/MainPage";

{/** These class names are for showing one line originally and then expand on click*/
}
export const originalClass = "text-one-line";
export const newClass = "text-show-all";

export function isMobileView() {
    return window.innerWidth < 991
}

export function startTawkTo() {
    var Tawk_API = Tawk_API || {}, Tawk_LoadStart = new Date();
    (function () {
        var s1 = document.createElement("script"), s0 = document.getElementsByTagName("script")[0];
        s1.async = true;
        s1.src = 'https://embed.tawk.to/5c9076a5c37db86fcfce9f66/default';
        s1.charset = 'UTF-8';
        s1.setAttribute('crossorigin', '*');
        s0.parentNode.insertBefore(s1, s0);
    })();
}

export function openHamburgerMenu() {
    const classname1 = "overflow-y-hidden";
    const classname2 = "category-nav__wrapper--isopen";
    const element1 = document.getElementById(MAINPAGE_ID);
    const element2 = document.getElementById(NAV_ID);

    if (!element1.className.includes(classname1) && !element2.className.includes(classname2)) {
        element1.className += classname1;
        element2.className += classname2;
    }
}

export function hideHamburgerMenu() {
    const classname1 = "overflow-y-hidden";
    const classname2 = "category-nav__wrapper--isopen";
    const element1 = document.getElementById(MAINPAGE_ID);
    const element2 = document.getElementById(NAV_ID);

    if (element1.className.includes(classname1) && element2.className.includes(classname2)) {
        element1.className = element1.className.replace(classname1, "");
        element2.className = element2.className.replace(classname2, "");
    }
}


export function expandOverflow(id) {
    const element = document.getElementById(id);
    element.className = element.className.replace(originalClass, newClass);
}

export function _handleKeyDown(event) {
    //enter key code is 13
    if (event.keyCode === 13) {
        event.preventDefault();
    } else if (event.keyCode === 27) {
        event.preventDefault();
        removeShoppingList();

    }
}

export function redirectToPath(path) {
    const element = document.createElement("a");
    element.href = path;
    // element.setAttribute("target", "_blank");
    document.body.appendChild(element);
    element.click();
    // document.body.removeChild(element);
}

export function setTopMargin() {
    this.setState({
        topMargin: document.getElementById(HEADER_ID).clientHeight
    })
}


export function setProductCardWidth() {
    const elements = document.getElementsByClassName(CLASSNAMES.PRODUCT_CARD);
    if (elements.length) {
        this.setState({
            productCardWidth: elements[0].clientWidth
        })
    }

}


export function removeShoppingList() {
    const item = document.getElementById('shopping-list');
    item.className =
        item.className.replace(' homepage-sidebar--open', '');
}


export function getProductMainImage(identifier) {
    const {config, query} = this.state;
    const {getMainImageProduct} = query;
    const {data} = getMainImageProduct;
    return new Promise((resolve, reject) => {

        requestValue(Object.assign({}, getMainImageProduct, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(async mainImage => {
                const {contentUrl} = mainImage;
                {/** Download*/
                }
                if (contentUrl) {
                    await downloadFile(config, contentUrl)
                        .then(url => {
                            resolve({
                                ...mainImage,
                                contentUrl: url
                            })
                        })
                        .catch(error => console.log(" Image Download => " + error))
                } else {
                    resolve("Done")
                }
            })
            .catch(error => {
                console.log(" Image Get => " + error);
                reject(error);
            })
    })
}


