export function getProductDetail() {
    const {config, query, product} = this.state;
    const {getProductDetail} = query;
    const {identifier} = product;
    const {data} = getProductDetail;

    return new Promise((resolve, reject) => {
        requestValue(Object.assign({}, getProductDetail, {
            data: {
                ...data,
                identifier
            }
        }), config)
            .then(data => {
                this.setState({
                    product: {
                        ...product,
                        ...data
                    }
                }, () => {
                    const {product} = this.state;
                    const {description, material, productDetails, directionOfUse, productName} = product;
                    window.document.title = productName;
                    if (description) {
                        document.getElementById("description").innerHTML = description;
                    }
                    if (material) {
                        document.getElementById("material").innerHTML = material;
                    }
                    if (productDetails) {
                        document.getElementById("productDetails").innerHTML = productDetails;
                    }
                    if (directionOfUse) {
                        document.getElementById("directionOfUse").innerHTML = directionOfUse;
                    }
                    resolve("Detail success");
                })
            })
            .catch(error => reject(error));
    })
}

export function getProductOffer() {
    const {config, query, product} = this.state;
    const {getProductOffer} = query;
    const {data} = getProductOffer;
    const {productID, productName} = product;
    return new Promise((resolve, reject) => {
        requestValue(Object.assign({}, getProductOffer, {
            data: {
                ...data,
                productID
            }
        }), config)
            .then(offer => {
                this.setState({
                    product: {
                        ...product,
                        offer
                    }
                }, () => {
                    resolve("success");
                })
            })
            .catch(error => {
                console.log(productName + " Offer " + error);
                reject(error);
            })
    })


}

export function getProductManufacturer() {
    const {product, config, query} = this.state;
    const {productID} = product;
    const {getProductManufacturer} = query || {};
    const {data} = getProductManufacturer;

    return new Promise((resolve, reject) => {
        const {product} = this.state;

        requestValue(Object.assign({}, getProductManufacturer, {
            data: {
                ...data,
                productID
            }
        }), config)
            .then(data => {
                this.setState({
                    product: {
                        ...product,
                        manufacturedBy: data
                    }
                }, () => {
                    resolve("Manufacturer success");
                })
            })
            .catch(error => {
                console.log("Error in Manufacturer ");
                reject(error);
            })
    })
}

