import React from "react";
import {getProductMainImage} from "../helpers/Utilities";
import {DefaultImage, } from "../StaticContent/staticContents";
import {LOCAL_CONSTANTS} from "../helpers/Constants";

export class ProductAddAlert extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {}
        };
        this.getProductMainImage = getProductMainImage.bind(this);
    }

    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        window.addEventListener("mouseup", (event) => {
            const temp1 = document.getElementById("product-add-alert");
            if (temp1 && event.target !== temp1 && event.target.parentNode !== temp1) {
                temp1.className =
                    temp1.className.replace(" show", "");
            }

        })
    }

    componentWillReceiveProps(nextProps) {
        const {productToAdd} = nextProps;
        this.setState({
            productToAdd
        }, () => {
            const {productToAdd} = this.state;
            const {identifier, mainImage} = productToAdd || {};
            if (identifier && mainImage) {
                this.getProductMainImage(identifier)
                    .then(mainImage => {
                        this.setState({
                            productToAdd: {
                                ...productToAdd,
                                mainImage
                            }
                        })
                    })
                    .catch(error => console.log(error));
            }
        })
    }

    // handleUndo = (undo) => {
    //     this.props.handleUndo(undo);
    // };

    render() {
        const {handleUndo, clearProductAdded} = this.props;
        const {productToAdd} = this.state;

        function hideAlert() {
            document.getElementById("product-add-alert").className = document.getElementById("product-add-alert").className.replace(" show", "");
        }

        if (productToAdd && Object.keys(productToAdd).length) {
            const {productName, mainImage, unitValue, quantity} = productToAdd;
            const {contentUrl} = mainImage || {};
            return (
                <div id="product-add-alert"
                     className="alert alert-md alert-fixed shadow  alert-bottom-left alert-light alert-dismissible fade"
                     role="alert">
                    <div className="media align-items-center">
                        <div className="product-card__image-holder product-card__image-holder--alert mr-3">
                            <div className="product-card__image">
                                {/*<img src=""/>*/}
                                <img src={contentUrl ? contentUrl : DefaultImage}/>
                            </div>
                        </div>
                        <div className="media-body">
                            <small>
                                <strong>{`${productName} - ${quantity}  ${unitValue["value"]}${unitValue["unitText"]} `}</strong>
                                is added to the shopping list.
                            </small>
                        </div>
                        {/** Need to work for undo because the Delete function removes the product irrespective of the quantity*/}
                        {/** But, this undo button needs to delete the quantity just added*/}
                        <button type="button" onClick={() => {
                            handleUndo(true);
                            hideAlert();
                            setTimeout(() => clearProductAdded(), 500);
                        }} className="btn btn-sm btn-outline-primary ml-3">Undo</button>
                    </div>
                    <button type="button" className="close" data-dismiss="alert" onClick={() => hideAlert()}
                            aria-label="Close">
                        <span aria-hidden="true"></span>
                    </button>
                </div>
            );
        } else return null
    }
}


