import React, {Component} from "react";
import logo from "../style/images/kirana_logo_color.svg";
import google_play from "../style/images/download_google_play.png";
import apple_store from "../style/images/download_apple_store.png";
import facebook from "../style/images/ic_facebook.svg";
import youtube from "../style/images/ic_youtube.svg";
import messenger from "../style/images/ic_messenger.svg";
import instagram from "../style/images/ic_instagram.svg";
import {Link} from "react-router-dom";
import {requestList} from "../helpers/request_helpers";
import {LOCAL_CONSTANTS} from "../helpers/Constants";

export class Footer extends Component {

    constructor(props) {
        super(props);
        this.state = {
            config: {},
            query: {},
            message: {},
            categoryList: []
        };
        this.setDefaultCategories = this.setDefaultCategories.bind(this);
    }


    componentWillMount() {
        const config = localStorage.getItem(LOCAL_CONSTANTS.CONFIG);
        const query = localStorage.getItem(LOCAL_CONSTANTS.QUERY);
        const message = localStorage.getItem(LOCAL_CONSTANTS.MESSAGE);
        if (config && query && message) {
            this.setState({
                config: JSON.parse(config),
                query: JSON.parse(query),
                message: JSON.parse(message)
            })
        }
    }

    componentDidMount() {
        this.setDefaultCategories();
    }

    setDefaultCategories() {
        const {query, config} = this.state;
        const {listDefaultCategories} = query;
        requestList(listDefaultCategories, config)
            .then(data => {
                this.setState({
                    categoryList: data
                })
            })
            .catch(error => {
                console.log(error)
            })
    }

    render() {
        const {categoryList} = this.state;
        const {showShoppingList} = this.props;
        return <div className="footer">
            <div className="footer__content">
                <div className="container">
                    <div className="row">
                        <div className="col-md-2 col-6">
                            <Link to="/"><img src={logo} className="footer__logo"/></Link>
                            <nav className="footer__nav">
                                <ul>
                                    <li className="footer__nav-item" key={0}><Link to="/">Home</Link></li>
                                    <li className="footer__nav-item" key={1}><Link to="/">About Us</Link></li>
                                    <li className="footer__nav-item" key={2}><Link to="/">How to Order</Link></li>
                                    <li className="footer__nav-item" key={3}><Link to="/">Support</Link></li>
                                </ul>
                            </nav>
                        </div>
                        <div className="col-md-2 col-6">
                            <nav className="footer__nav">
                                <h4 className="footer__nav-title">
                                    Categories
                                </h4>
                                <ul>
                                    {
                                        categoryList.length
                                            ? categoryList.map((category, index) => {
                                                const {categoryName, identifier} = category;
                                                return <li className="footer__nav-item" key={index}>
                                                    <Link to={`search/KiranaCategory/${categoryName}/${identifier}`}>{categoryName}</Link>
                                                </li>

                                            })
                                            : null
                                    }
                                </ul>
                            </nav>
                        </div>
                        <div className="col-md-2 col-6">
                            <nav className="footer__nav">
                                <h4 className="footer__nav-title">
                                    Lists
                                </h4>
                                <ul>
                                    <li className="footer__nav-item" key={0} onClick={() => showShoppingList()}><Link
                                        to="#">Create
                                        My
                                        List</Link></li>
                                    <li className="footer__nav-item" key={1}><Link to="/">How to use lists?</Link></li>
                                    <li className="footer__nav-item" key={2}><Link to="/">Popular</Link></li>
                                    <li className="footer__nav-item" key={3}><Link to="/">Recommended</Link></li>
                                </ul>
                            </nav>
                        </div>
                        <div className="col-md-2 col-6">
                            <nav className="footer__nav footer__nav--download-app">
                                <h4 className="footer__nav-title">
                                    Download App
                                </h4>
                                <ul>
                                    <li className="footer__nav-item" key={0}><Link to="/"><img
                                        src={google_play}/></Link>
                                    </li>
                                    <li className="footer__nav-item" key={1}><Link to="/"><img src={apple_store}
                                                                                               alt=""/></Link>
                                    </li>
                                </ul>
                            </nav>
                        </div>
                        <div className="col-md-4 col-6 text-md-right">
                            <nav className="footer__nav footer__nav--social-media">
                                <h4 className="footer__nav-title">Stay in Touch</h4>
                                <ul className="list-inline">
                                    <li className="list-inline-item footer__nav-item" key={0}><Link target="_blank"
                                                                                                    to="//www.facebook.com/merokirana/"><img
                                        src={facebook}
                                        alt=""/></Link>
                                    </li>
                                    <li className="list-inline-item footer__nav-item" key={1}><Link to="/"><img
                                        src={youtube}
                                        alt=""/></Link>
                                    </li>
                                    <li className="list-inline-item footer__nav-item" key={2}><Link target="_blank"
                                                                                                    to="//www.messenger.com/t/194178437733781"><img
                                        src={messenger}
                                        alt=""/></Link>
                                    </li>
                                    <li className="list-inline-item footer__nav-item" key={3}><Link target="_blank"
                                                                                                    to="//www.instagram.com/mero_kirana/?hl=en"><img
                                        src={instagram}
                                        alt=""/></Link>
                                    </li>
                                </ul>
                            </nav>
                        </div>
                    </div>
                    <div className="row">
                        <div className="col-12">
                            <div className="footer__text">
                                Copyright  2018 merokirana.com
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    }
};



